export class PostDynamicPricingDetailsRequest
{
  public DynamicPriceForm: DynamicPricingTemplate[];
  public DayOfWeekColAndRow: DayOfWeekColAndRow[];
  public DynamicPricingDetails: TimeGroupDetails[];
  public DynamicPriceUpdatedValues: TimeGroupDetails[];
  public CourseTemplateUID: string;
  public IsPublished: boolean;
  public IsWeb: boolean;
  public IsLocal: boolean;
  public EnablePrepaid: boolean;
  public RequirePrepaid: boolean;
  public Prepay18GolfDiscountPercent: number;
  public Prepay18CartDiscountPercent: number;
  public Public: boolean;
  public IsPublicPriceLevel: boolean;
  public IsAllPriceLevels: boolean;
  public PriceLevels: PriceLevelOption[];
}

export class PostDynamicPricingDetailsRequestForm extends UntypedFormGroup
{
  // public readonly DynamicPriceForm: DynamicPriceFieldFormArray;
  public readonly DayOfWeekColAndRow: DayOfWeekColAndRowFormArray;

  constructor(value: GetDynamicPricingDetailsResponse)
  {
    super({
      // DynamicPriceForm: new DynamicPriceFieldFormArray(value?.DynamicPricingTemplates),
      DynamicPricingWizard: new UntypedFormControl(''),
      DynamicPriceForm: new UntypedFormControl(''),
      DayOfWeekColAndRow: new DayOfWeekColAndRowFormArray(value?.DayOfWeekColAndRow),
      IsPublished: new UntypedFormControl(value != undefined ? value.IsPublished : false)
    });

    //this.DynamicPriceForm = this.get('DynamicPriceForm') as DynamicPriceFieldFormArray;

    this.DayOfWeekColAndRow = this.get('DayOfWeekColAndRow') as DayOfWeekColAndRowFormArray;
  }

  public value: PostDynamicPricingDetailsRequest;

  public setValue(value: GetDynamicPricingDetailsResponse)
  {
    const request = new PostDynamicPricingDetailsRequest();
    request.DynamicPriceForm = value?.DynamicPricingTemplates;
    request.DayOfWeekColAndRow = value?.DayOfWeekColAndRow;
    request.IsPublished = value != undefined ? value.IsPublished : false;
    super.setValue(request);
  }
}

export class DynamicPricingDetailsFormArray extends UntypedFormArray
{
  constructor(value: TimeGroupDetails[])
  {
    super([]);
    this.setValue(value);
  }

  public setValue(value: TimeGroupDetails[],
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    if (value != undefined)
    {
      value.forEach(dynamicPricingDetails =>
      {
        let dynamicPricingForm = new TimeGroupDetailsForm(dynamicPricingDetails);
        this.push(dynamicPricingForm);
      });
    }
  }
}



