export function minmaxValidator(control: UntypedFormGroup): { [key: string]: string; } | null
{
  if (Number(control?.parent?.controls["MinPrice"].value) > Number(control?.parent?.controls["MaxPrice"].value))
  {
    return { "minmaxValidator": "" };
  }
  else
  {
    control?.parent?.controls["MinPrice"].setErrors(null);
    control?.parent?.controls["MaxPrice"].setErrors(null);
  }
}
