import { DragDropModule } from '@angular/cdk/drag-drop';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MccColorPickerModule } from 'material-community-components/color-picker';
import { NgxSliderModule } from '@angular-slider/ngx-slider';
import { DynamicPricingUtilizationRangesService } from '../../@services/dynamic-pricing-utilization-ranges.service';
import { DynamicPricingUtilizationRangesComponent } from './dynamic-pricing-utilization-ranges.component';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';

const CORE_MODULES = [TeiTextTranslatePipeModule];

const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MatFormFieldModule, MatInputModule, MatIconModule, MatButtonModule, NgxSliderModule, MccColorPickerModule, DragDropModule, MatDialogModule];
const TEI_MATERIAL_MODULES = [];

const COMPONENTS = [DynamicPricingUtilizationRangesComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'IDynamicPricingUtilizationRangesService', useClass: DynamicPricingUtilizationRangesService }],
})
export class DynamicPricingUtilizationRangesModule { }
