import { UntypedFormArray, UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { UtilizationRange } from '../../dynamic-pricing-details';
import { GetDynamicPricingUtilizationRangesRequest, GetDynamicPricingUtilizationRangesResponse } from './get-dynamic-pricing-utilization-ranges.model';

export class PostDynamicPricingUtilizationRangesRequest
{
  public UtilizationRanges: UtilizationRange[];
  public CourseTemplateUID: string;
}

export class PostDynamicPricingUtilizationRangesRequestForm extends UntypedFormGroup
{
  public readonly UtilizationRanges: UtilizationRangesFormArray;

  constructor(value: UtilizationRange[])
  {
    super({
      UtilizationRanges: new UtilizationRangesFormArray(value),
    });
    this.UtilizationRanges = this.get('UtilizationRanges') as UtilizationRangesFormArray;
  }


  public value: PostDynamicPricingUtilizationRangesRequest;

  public setValue(
    value: GetDynamicPricingUtilizationRangesResponse,
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    const request = new PostDynamicPricingUtilizationRangesRequest();
    request.UtilizationRanges = value.UtilizationRanges;
    super.setValue(request, options);
  }
}



export class UtilizationRangesFormArray extends UntypedFormArray
{

  constructor(value: UtilizationRange[])
  {
    super([]);
  }
  public setValue(value: UtilizationRange[],
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    if (value != undefined)
    {
      value.forEach(range =>
      {
        let utilRange = new UtilizationRangesForm(range);
        this.push(utilRange);
      });
    }
  }

}


export class UtilizationRangesForm extends UntypedFormGroup
{
  public readonly UtilizationRanges: UtilizationRangesFormArray;

  constructor(value: UtilizationRange)
  {
    super({
      UtilizationRangeUID: new UntypedFormControl(value.UtilizationRangeUID),
      UtilName: new UntypedFormControl(value.UtilName, [nameValidator, Validators.required]),
      ColorCode: new UntypedFormControl(value.ColorCode, Validators.required),
      SortOrder: new UntypedFormControl(value.SortOrder),
      Deleted: new UntypedFormControl(value.Deleted == null ? false : value.Deleted),
      UtilStartRange: new UntypedFormControl(value.UtilStartRange),
      UtilEndRange: new UntypedFormControl(value.UtilEndRange),
      IsControlUpdated: new UntypedFormControl(value.IsControlUpdated),
      UtilRange: new UntypedFormControl([value.UtilStartRange, value.UtilEndRange], [rangeValidator])
    });
    this.UtilizationRanges = this.get('UtilizationRanges') as UtilizationRangesFormArray;
  }

  public value: PostDynamicPricingUtilizationRangesRequest;

  public setValue(value: GetDynamicPricingUtilizationRangesRequest)
  {
    const request = new PostDynamicPricingUtilizationRangesRequest();
    super.setValue(request);
  }

}

let isValueChanged: boolean = false;

export function nameValidator(control: UntypedFormGroup): { [key: string]: string; } | null
{
  let validName = true;
  if (!isValueChanged)
  {
    if (control?.parent?.controls["UtilName"]._parent._parent.value.length > 1)
    {
      control?.parent?.controls["UtilName"]._parent._parent.value.forEach((element, i) =>
      {
        if (element.Deleted == false)
        {
          if (control.value == element.UtilName && control.value != "")
          {
            validName = false;
            return;
          }
          else
          {
            if (control["_parent"]?._parent?.status === "INVALID")
            {
              control.parent.parent.controls[i].controls["UtilName"].setErrors(null);
            }
          }
        }
      });

      if (validName)
      {

        return null;
      }
      else
      {
        return { "nameValidator": "Name already exist." };
      }
    }
  }
}


export function rangeValidator(control: UntypedFormGroup): { [key: string]: string; } | null
{
  let validName = true;
  let defaultValue = false;
  let currentIndex = control?.parent?.value.SortOrder;

  if (!isValueChanged)
  {
    if (control?.parent?.controls["UtilRange"]._parent._parent.value.length > 1)
    {
      control?.parent?.controls["UtilRange"]._parent._parent.value.forEach((element, i) =>
      {
        if (element.Deleted == false)
        {
          if (currentIndex != element.SortOrder)
          {
            if (control.value[0] == 0 && control.value[1] == 0)
            {
              defaultValue = true;
              return;
            }

            if ((control.value[0] == element.UtilRange[0] && control.value[1] == element.UtilRange[1])
              || (control.value[0] >= element.UtilRange[0] && control.value[0] <= element.UtilRange[1])
              || (control.value[1] >= element.UtilRange[0] && control.value[1] <= element.UtilRange[1])
              && currentIndex != element.SortOrder
              && control.value != ""
              && !defaultValue
            )
            {
              validName = false;
              return;
            }
            else
            {
              if (control["_parent"]?._parent?.status === "INVALID")
              {
                control.parent.parent.controls[i].controls["UtilRange"].setErrors(null);
              }
            }
          }
          else
          {
            if (currentIndex == element.SortOrder)
            {
              let min = control.value[0];
              let max = control.value[1];
              control?.parent?.controls["UtilRange"]._parent._parent.value.forEach((element, i) =>
              {
                if (element.Deleted == false && element.SortOrder != currentIndex)
                {
                  if ((element.UtilRange[0] >= min && element.UtilRange[1] <= min)
                    || (element.UtilRange[1] >= min && element.UtilRange[1] <= max)
                    && control.value != ""
                    && !defaultValue
                  )
                  {
                    validName = false;
                    return;
                  }
                }
              });
            }
          }
        }
      });

      if (defaultValue)
      {
        return { "rangeValidator": "range is not valid." };
      }

      if (validName)
      {
        control?.parent?.controls["UtilRange"].setErrors(null);
        return null;
      }
      else
      {
        return { "rangeValidator": "range already exist." };
      }
    }
  }
}



export function valueChanged(setValue: boolean)
{
  isValueChanged = setValue;
}

export class PostDynamicPricingUtilizationRangesResponse
{

}
