import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IDynamicPricingUtilizationRangesService, GetDynamicPricingUtilizationRangesRequest, GetDynamicPricingUtilizationRangesResponse, PostDynamicPricingUtilizationRangesRequest, PostDynamicPricingUtilizationRangesResponse } from '../@intermediate/dynamic-pricing-utilization-ranges';

@Injectable({
  providedIn: 'root'
})

export class DynamicPricingUtilizationRangesService implements IDynamicPricingUtilizationRangesService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Booking/TeeTimes/DynamicPricingUtilizationRanges';
  }

  public Get(request: GetDynamicPricingUtilizationRangesRequest): Observable<GetDynamicPricingUtilizationRangesResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetDynamicPricingUtilizationRangesResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostDynamicPricingUtilizationRangesRequest): Observable<PostDynamicPricingUtilizationRangesResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostDynamicPricingUtilizationRangesResponse>(apiUrl, request);
  }
}
