import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatNativeDateModule } from '@angular/material/core';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { SnackBarModule } from '@tei/material';
import { DynamicPricingTemplateService } from '../../@services/dynamic-pricing-template.service';
import { DynamicPricingTemplateComponent } from './dynamic-pricing-template.component';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';

const CORE_MODULES = [TeiTextTranslatePipeModule];

const BASE_MODULES = [FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatButtonModule, MatFormFieldModule, MatInputModule, MatSelectModule, CommonModule, MatDatepickerModule, MatNativeDateModule];
const TEI_MATERIAL_MODULES = [SnackBarModule];

const COMPONENTS = [DynamicPricingTemplateComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'IDynamicPricingTemplateService', useClass: DynamicPricingTemplateService }],
})
export class DynamicPricingTemplateModule { }
