import { UntypedFormArray, UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import moment from 'moment'
import { TimeGroup } from '../../dynamic-pricing-details';
import { GetDynamicTimeGroupsRequest, GetDynamicTimeGroupsResponse } from './get-dynamic-time-groups.model';
export class PostDynamicTimeGroupsRequest
{
  public TimeGroupFormArray: TimeGroup[];
  public CourseTemplateUID: string;
}

export class PostDynamicTimeGroupsRequestForm extends UntypedFormGroup
{
  public readonly TimeGroupFormArray: TimeGroupRangeFormArray;
  constructor(value: TimeGroup[])
  {
    super({
      TimeGroupFormArray: new TimeGroupRangeFormArray(value)
    });
    this.TimeGroupFormArray = this.get('TimeGroupFormArray') as TimeGroupRangeFormArray;
  }

  public value: PostDynamicTimeGroupsRequest;

  public setValue(
    value: GetDynamicTimeGroupsResponse,
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    const request = new PostDynamicTimeGroupsRequest();

    request.TimeGroupFormArray = value.TimeGroups;
    super.setValue(request);
  }

}


export class TimeGroupRangeFormArray extends UntypedFormArray
{

  constructor(value: TimeGroup[])
  {
    super([]);
  }
  public setValue(value: TimeGroup[],
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    if (value != undefined)
    {
      value.forEach(time =>
      {
        const timeGroupForm = new TimeGroupForm(time);
        this.push(timeGroupForm);
      });
    }
  }

}


export class TimeGroupForm extends UntypedFormGroup
{
  public readonly TimeGroupFormArray: TimeGroupRangeFormArray;
  constructor(value: TimeGroup)
  {
    super({
      TimeGroupID: new UntypedFormControl(value.TimeGroupID),
      GroupName: new UntypedFormControl(value.GroupName, [nameValidator, Validators.required]),
      StartTime: new UntypedFormControl(value.StartTime, [timeValidator, Validators.required]),
      EndTime: new UntypedFormControl(value.EndTime, [timeValidator, Validators.required]),
      IsDeleted: new UntypedFormControl(value.IsDeleted),
      SortOrder: new UntypedFormControl(value.SortOrder),
      IsControlUpdated: new UntypedFormControl(value.IsControlUpdated)
    });
    this.TimeGroupFormArray = this.get('TimeGroupFormArray') as TimeGroupRangeFormArray;
  }

  public value: PostDynamicTimeGroupsRequest;

  public setValue(value: GetDynamicTimeGroupsRequest)
  {
    const request = new PostDynamicTimeGroupsRequest();
    super.setValue(request);
  }

}

let isValueChanged: boolean = false;
//let istimeValueChanged: boolean = false;

export function nameValidator(control: UntypedFormGroup): { [key: string]: string; } | null
{
  let validName = true;
  if (!isValueChanged)
  {
    if (control?.parent?.controls["GroupName"]._parent._parent.value.length > 1)
    {
      control?.parent?.controls["GroupName"]._parent._parent.value.forEach(element =>
      {
        if (element.IsDeleted == false)
        {
          if (control.value == element.GroupName && control.value != "")
          {
            validName = false;
            return;
          }
        }
      });


      if (validName)
      {
        return null;
      }
      else
      {
        return { "nameValidator": "Group Name already exist." };
      }
    }
  }
}

export function timeValidator(control: UntypedFormGroup): { [key: string]: string; } | null
{
  let validName = true;
  let defaultValue = false;
  let currentIndex = control?.parent?.value.SortOrder;
  let timeValueChange: boolean = false;

  if (!isValueChanged || !timeValueChange)
  {
    if ((control?.parent?.controls["StartTime"].value == "12:00 AM") && (control?.parent?.controls["EndTime"].value == "12:00 AM"))
    {
      return { "timeValidator": "time is not valid." };
    }
    if (control?.parent?.controls["StartTime"]._parent._parent.value.length > 1)
    {
      let start = control?.parent?.controls["StartTime"].value;
      let end = control?.parent?.controls["EndTime"].value;
      if ((start != "" && start != null) || (end != "" && end != null))
      {
        end = (end == "") ? start : end;
      }
      control?.parent?.controls["StartTime"]._parent._parent.value.forEach((element, i) =>
      {
        if (element.IsDeleted == false)
        {
          if (currentIndex != element.SortOrder)
          {
            if (control?.parent?.controls["StartTime"].value == '' && control?.parent?.controls["EndTime"].value == '')
            {
              defaultValue = true;
              return;
            }
            if ((moment(start, 'hh:mm A').format('HH:mm') == moment(element.StartTime, 'hh:mm A').format('HH:mm') &&
              moment(end, 'hh:mm A').format('HH:mm') == moment(element.EndTime, 'hh:mm A').format('HH:mm')) ||
              (moment(start, 'hh:mm A').format('HH:mm') >= moment(element.StartTime, 'hh:mm A').format('HH:mm') &&
                moment(start, 'hh:mm A').format('HH:mm') <= moment(element.EndTime, 'hh:mm A').format('HH:mm')) ||
              (moment(end, 'hh:mm A').format('HH:mm') >= moment(element.StartTime, 'hh:mm A').format('HH:mm') &&
                moment(end, 'hh:mm A').format('HH:mm') <= moment(element.EndTime, 'hh:mm A').format('HH:mm')) ||
              ((moment(start, 'hh:mm A').format('HH:mm') <= moment(element.StartTime, 'hh:mm A').format('HH:mm')) &&
                (moment(end, 'hh:mm A').format('HH:mm') >= moment(element.EndTime, 'hh:mm A').format('HH:mm')))
              && currentIndex != element.SortOrder
              && control?.parent?.controls["StartTime"].value != ""
              && control?.parent?.controls["EndTime"].value != ""
              && !defaultValue
            )
            {
              validName = false;
              return;
            }
            else
            {
              //if (control["_parent"]?._parent?.status === "INVALID")
              if (control["_parent"]?._parent.controls[i].status === "VALID")
              {
                control.parent.parent.controls[i].controls["StartTime"].setErrors(null);
                control.parent.parent.controls[i].controls["EndTime"].setErrors(null);
              }
              // else
              // {
              //   let TimeGroupvalidation: TimeGroup[] = [];
              //   TimeGroupvalidation = control?.parent?.controls["StartTime"]._parent._parent.value;
              //   TimeGroupvalidation.forEach((x, i) =>
              //   {
              //     if (x.TimeGroupID == control?.parent?.controls["TimeGroupID"].value || x.GroupName == control?.parent?.controls["GroupName"].value)
              //     {
              //       x.StartTime = control?.parent?.controls["StartTime"].value;
              //       x.EndTime = control?.parent?.controls["EndTime"].value;
              //     }
              //   });
              //   timeValidator1(control["_parent"]?._parent.controls[i].value.StartTime, control["_parent"]?._parent.controls[i].value.EndTime, i, control, TimeGroupvalidation);
              // }
            }
          }
          else
          {
            if (currentIndex == element.SortOrder)
            {
              control?.parent?.controls["StartTime"]._parent._parent.value.forEach((element, i) =>
              {
                if (element.IsDeleted == false && currentIndex != element.SortOrder)
                {
                  if ((moment(start, 'hh:mm A').format('HH:mm') >= moment(element.StartTime, 'hh:mm A').format('HH:mm') &&
                    moment(start, 'hh:mm A').format('HH:mm') <= moment(element.EndTime, 'hh:mm A').format('HH:mm'))
                    || (moment(end, 'hh:mm A').format('HH:mm') >= moment(element.StartTime, 'hh:mm A').format('HH:mm') &&
                      moment(end, 'hh:mm A').format('HH:mm') <= moment(element.EndTime, 'hh:mm A').format('HH:mm'))
                    || ((moment(start, 'hh:mm A').format('HH:mm') <= moment(element.StartTime, 'hh:mm A').format('HH:mm')) &&
                      (moment(end, 'hh:mm A').format('HH:mm') >= moment(element.EndTime, 'hh:mm A').format('HH:mm')))
                    && control?.parent?.controls["StartTime"].value != ""
                    && control?.parent?.controls["EndTime"].value != ""
                    && !defaultValue)
                  {
                    validName = false;
                    return;
                  }
                }
              });
            }
          }
        }
      });

      if (defaultValue)
      {
        return { "timeValidator": "time is not valid." };
      }

      if (validName)
      {
        if (control?.parent?.controls["StartTime"].value != '' && control?.parent?.controls["EndTime"].value != '')
        {
          control.parent.controls["EndTime"].setErrors(null);
          control.parent.controls["StartTime"].setErrors(null);
        }
        return null;
      }
      else
      {
        return { "timeValidator": "Time range already exist." };
      }
    }
  }
}

function timeValidator1(start: string, end: string, index: number, control: UntypedFormGroup, TimeGroupvalidation)
{
  let valueexist = false;
  TimeGroupvalidation.forEach((element, i) =>
  {
    if (element.IsDeleted == false && index != i && !valueexist)
    {
      if ((moment(start, 'hh:mm A').format('HH:mm') == moment(element.StartTime, 'hh:mm A').format('HH:mm') &&
        moment(end, 'hh:mm A').format('HH:mm') == moment(element.EndTime, 'hh:mm A').format('HH:mm')) ||
        (moment(start, 'hh:mm A').format('HH:mm') >= moment(element.StartTime, 'hh:mm A').format('HH:mm') &&
          moment(start, 'hh:mm A').format('HH:mm') <= moment(element.EndTime, 'hh:mm A').format('HH:mm')) ||
        (moment(end, 'hh:mm A').format('HH:mm') >= moment(element.StartTime, 'hh:mm A').format('HH:mm') &&
          moment(end, 'hh:mm A').format('HH:mm') <= moment(element.EndTime, 'hh:mm A').format('HH:mm')) ||
        ((moment(start, 'hh:mm A').format('HH:mm') <= moment(element.StartTime, 'hh:mm A').format('HH:mm')) &&
          (moment(end, 'hh:mm A').format('HH:mm') >= moment(element.EndTime, 'hh:mm A').format('HH:mm')))
        && control?.parent?.controls["StartTime"].value != ""
        && control?.parent?.controls["EndTime"].value != ""
      )
      {
        valueexist = true;
        return;
      }
      else
      {
        return;
      }
    }
  });
  if (!valueexist)
  {
    control.parent.parent.controls[index].controls["StartTime"].setErrors(null);
    control.parent.parent.controls[index].controls["EndTime"].setErrors(null);
  }
}

export function valueChanged(setValue: boolean)
{
  isValueChanged = setValue;
}

