import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteData } from '@tei/core';
import { ConfirmationComponent } from './pages/confirmation/confirmation.component';
import { DynamicPricingDetailsComponent } from './pages/dynamic-pricing-details/dynamic-pricing-details.component';
import { DynamicPricingComponent } from './pages/dynamic-pricing/dynamic-pricing.component';
import { PagesComponent } from './pages/pages.component';
import { SettingsComponent } from './pages/settings/settings.component';
import { TeeTimeDetailsComponent } from './pages/tee-time-details/tee-time-details.component';
import { TeetimesComponent } from './pages/teetimes/teetimes.component';

const routes: Routes = [
  {
    path: '',
    component: PagesComponent,
    children: [
      {
        path: '',
        component: TeetimesComponent,
        data: { RouteData: new RouteData({ Title: 'Teetimes' }) }
      },
      {
        path: 'teetimes/:teeTimeID/:moveReservation/:selectionHeaderUID',
        component: TeetimesComponent,
        data: { RouteData: new RouteData({ Title: 'Teetimes' }) }
      },
      {
        path: 'teetime-details',
        component: TeeTimeDetailsComponent,
        data: { RouteData: new RouteData({ Title: 'Tee Time Details' }) }
      },
      {
        path: 'teetime-details/:teeTimeID/:edit',
        component: TeeTimeDetailsComponent,
        data: { RouteData: new RouteData({ Title: 'Tee Time Details' }) }
      },
      {
        path: 'confirmation/:teetimeId/:moveReservation',
        component: ConfirmationComponent,
        data: { RouteData: new RouteData({ Title: 'Tee Time Confirmation' }) }
      },
      // Maybe unused, but keeping for reference
      {
        path: 'settings/:CourseID/:CourseName',
        component: SettingsComponent,
        data: { RouteData: new RouteData({ Title: 'Settings' }) }
      },
      {
        path: 'tee-sheet',
        loadChildren: () => import('./pages/teesheet/teesheet.module').then(m => m.TeesheetViewModule)
      },
      {
        path: 'dynamic-pricing',
        component: DynamicPricingComponent,
        data: { RouteData: new RouteData({ Title: 'Dynamic Pricing' }) }
      },
      {
        path: 'dynamic-pricing-details/:templateID',
        component: DynamicPricingDetailsComponent,
        data: { RouteData: new RouteData({ Title: 'Dynamic Pricing', CanGoBack: true, BackRoute: '/dynamic-pricing' }) },
      }
    ]
  },
  { path: '**', redirectTo: '' },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class TeeTimesRoutingModule
{
}
