import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IDynamicTimeGroupsService, GetDynamicTimeGroupsRequest, GetDynamicTimeGroupsResponse, PostDynamicTimeGroupsRequest, PostDynamicTimeGroupsResponse } from '../@intermediate/dynamic-time-groups';

@Injectable({
  providedIn: 'root'
})

export class DynamicTimeGroupsService implements IDynamicTimeGroupsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Booking/TeeTimes/DynamicTimeGroups';
  }

  public Get(request: GetDynamicTimeGroupsRequest): Observable<GetDynamicTimeGroupsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetDynamicTimeGroupsResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostDynamicTimeGroupsRequest): Observable<PostDynamicTimeGroupsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostDynamicTimeGroupsResponse>(apiUrl, request);
  }
}
