import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { ConfirmDialogModule, LoadingModule, SnackBarModule } from '@tei/material';
import { ManageDialogService } from '../../@services/manage-dialog.service';
import { ManageDialogComponent } from './manage-dialog.component';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { TranslateModule } from '@ngx-translate/core';

const CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TeiDateTranslateModule, TranslateModule];
const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatSelectModule, MatFormFieldModule, MatDividerModule, MatButtonModule, MatIconModule];
const TEI_MATERIAL_MODULES = [SnackBarModule, ConfirmDialogModule, LoadingModule];

const COMPONENTS = [ManageDialogComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'IManageDialogService', useClass: ManageDialogService }],
})
export class ManageDialogModule { }
