import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { GetTeetimesResponse } from './get-teetimes.model';

export class PostTeetimesRequest
{
  public SingleDate: any;
  public AllCourses: string;
  public Players: string;
  public TeetimeChips: any;
  public AvailabilityTime: string;
  public SearchCourses: string;
  public TimeOptions: string;
  public ClubID?: string;
}

export class PostTeetimesRequestForm extends UntypedFormGroup
{
  constructor(datePlayerDisable: boolean)
  {
    super({
      SingleDate: new UntypedFormControl({ value: '', disabled: datePlayerDisable }),
      AllCourses: new UntypedFormControl(''),
      Players: new UntypedFormControl({ value: '', disabled: datePlayerDisable }),
      AvailabilityTime: new UntypedFormControl(''),
      TeetimeChips: new UntypedFormControl(''),
      SearchCourses: new UntypedFormControl(''),
      TimeOptions: new UntypedFormControl(''),
      ClubID: new UntypedFormControl(''),
    });
  }

  public value: PostTeetimesRequest;

  public setValue(value: GetTeetimesResponse)
  {
    const request = new PostTeetimesRequest();
    request.SingleDate = new Date(value.Filters.SelectedDate);
    request.AllCourses = value.Filters.SelectedCourse;
    request.Players = value.Filters.SelectedPlayer;
    request.AvailabilityTime = value.Filters.AvailabilitySelectedTime;
    request.TeetimeChips = '';
    request.SearchCourses = '';
    request.TimeOptions = value.Filters.TimeOptions;
    super.patchValue(request);
  }
}

export class PostTeetimesResponse
{

}
