import { UntypedFormGroup, UntypedFormControl, Validators, AbstractControl } from '@angular/forms';
import { GetDynamicPricingWizardResponse } from './get-dynamic-pricing-wizard.model';

export class PostDynamicPricingWizardRequest
{
  public Dayofweek: string[];
}

export class PostDynamicPricingWizardRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      Dayofweek: new UntypedFormControl(''),
      DaysOut: new UntypedFormControl(''),
      TimeGroup: new UntypedFormControl(''),
      UtilizationRange: new UntypedFormControl(''),
      MinPricing: new UntypedFormControl('5.00', minmaxPricingValidator),
      MaxPricing: new UntypedFormControl('150.00', minmaxPricingValidator),
      price: new UntypedFormControl('Reset'),
      SetPrice: new UntypedFormControl({ value: '', disabled: true }, Validators.required),
      IncreasePrice: new UntypedFormControl({ value: '', disabled: true }, Validators.required),
      DecreasePrice: new UntypedFormControl({ value: '', disabled: true }, Validators.required)
    });
  }

  public value: PostDynamicPricingWizardRequest;

  public setValue(
    value: GetDynamicPricingWizardResponse,
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    const request = new PostDynamicPricingWizardRequest();



    super.setValue(request, options);
  }
}

export class PostDynamicPricingWizardResponse
{

}

export function minmaxPricingValidator(control: UntypedFormGroup): { [key: string]: string; } | null
{
  if (Number(control?.parent?.controls["MinPricing"].value) > Number(control?.parent?.controls["MaxPricing"].value))
  {
    return { "minmaxValidator": "" };
  }
  else
  {
    control?.parent?.controls["MinPricing"].setErrors(null);
    control?.parent?.controls["MaxPricing"].setErrors(null);
  }
}

