import { UntypedFormGroup, UntypedFormControl } from '@angular/forms';
import { GetDynamicPricingTemplateResponse } from './get-dynamic-pricing-template.model';

export class PostDynamicPricingTemplateRequest
{
    public Course: string;
    public SelectedStartDate: Date | string;
    public SelectedEndDate: Date | string;
}

export class PostDynamicPricingTemplateRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      Course: new UntypedFormControl(''),
      SelectedStartDate: new UntypedFormControl(''),
      SelectedEndDate: new UntypedFormControl('')
    });
  }

  public value: PostDynamicPricingTemplateRequest;

  public setValue(
    value: GetDynamicPricingTemplateResponse,
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    const request = new PostDynamicPricingTemplateRequest();

    request.Course = value.TemplateCourses.find(x=>x.CourseID == '-1').CourseID;
    request.SelectedStartDate = value.Templatedates.StartDate;
    request.SelectedEndDate = value.Templatedates.EndDate;

    super.setValue(request);
  }
}

export class PostDynamicPricingTemplateResponse
{

}
