import { ScrollingModule } from '@angular/cdk/scrolling';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatNativeDateModule } from '@angular/material/core';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatSidenavModule } from '@angular/material/sidenav';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { RouterModule } from '@angular/router';
import { SnackBarModule } from '@tei/material';
import { DynamicPricingDetailsService } from '../../@services/dynamic-pricing-details.service';
import { DynamicPricingDetailTemplateModule } from '../../components/dynamic-pricing-detail-template/dynamic-pricing-detail-template.module';
import { DynamicPricingUtilizationRangesComponent } from '../../components/dynamic-pricing-utilization-ranges/dynamic-pricing-utilization-ranges.component';
import { DynamicPricingUtilizationRangesModule } from '../../components/dynamic-pricing-utilization-ranges/dynamic-pricing-utilization-ranges.module';
import { DynamicPricingWizardModule } from '../../components/dynamic-pricing-wizard/dynamic-pricing-wizard.module';
import { DynamicTimeGroupsComponent } from '../../components/dynamic-time-groups/dynamic-time-groups.component';
import { DynamicTimeGroupsModule } from '../../components/dynamic-time-groups/dynamic-time-groups.module';
import { CanDeactivateGuardService } from './can-deactivate-guard.service';
import { DynamicPricingDetailsComponent } from './dynamic-pricing-details.component';
import { TwoDigitDecimalNumberDirective } from './two-digit-decimal-number.directive';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';

const CORE_MODULES = [TeiTextTranslatePipeModule];


const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MatSlideToggleModule, ScrollingModule, MatInputModule, MatFormFieldModule, MatDatepickerModule, MatNativeDateModule, MatCardModule, MatButtonToggleModule, MatButtonModule, MatIconModule, MatDividerModule, MatDialogModule, MatSidenavModule];
const TEI_MATERIAL_MODULES = [SnackBarModule];
const MODULES = [DynamicTimeGroupsModule, DynamicPricingUtilizationRangesModule, DynamicPricingWizardModule, DynamicPricingDetailTemplateModule];

@NgModule({
    declarations: [DynamicPricingDetailsComponent, TwoDigitDecimalNumberDirective],
    imports: [
        RouterModule.forChild([{ path: '', component: DynamicPricingDetailsComponent, canDeactivate: [CanDeactivateGuardService] }]),
        ...BASE_MODULES,
        ...MATERIAL_MODULES,
        ...TEI_MATERIAL_MODULES,
        ...MODULES,
        ...CORE_MODULES
    ],
    exports: [DynamicPricingDetailsComponent],
    providers: [{ provide: 'IDynamicPricingDetailsService', useClass: DynamicPricingDetailsService },
    ]
})
export class DynamicPricingDetailsModule { }
