import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { Observable } from "rxjs";
import { GetClubsListRequest, GetClubsResponse } from "../@intermediate/tee-time-club/models/tee-time-club.model";
import { ITeeTimeClubService } from "../@intermediate/tee-time-club/tee-time-club.interface";

@Injectable({
  providedIn: 'root'
})
export class TeeTimeClubService implements ITeeTimeClubService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Booking/TeeTimes';
  }

  GetList(request?: GetClubsListRequest): Observable<GetClubsResponse>
  {
    const apiUrl = this.serviceUrl + "/Club";
    return this.http.get<GetClubsResponse>(apiUrl, { params: request as any });
  }
}