import { Component, ElementRef, EventEmitter, forwardRef, HostListener, Inject, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { ControlValueAccessor, FormControl, NG_VALUE_ACCESSOR } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { debounceTime, finalize, map, Subscription } from 'rxjs';
import { Club, GetClubsListRequest } from '../../@intermediate/tee-time-club/models/tee-time-club.model';
import { ITeeTimeClubService } from '../../@intermediate/tee-time-club/tee-time-club.interface';

@Component({
  selector: 'tei-tee-times-club-selector',
  templateUrl: './tee-time-club.component.html',
  styleUrls: ['./tee-time-club.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TeeTimeClubComponent),
      multi: true
    }
  ]
})
export class TeeTimeClubComponent implements ControlValueAccessor, OnInit, OnDestroy
{
  @ViewChild('clubCardContainer', { read: ElementRef }) clubCardContainer: ElementRef<HTMLElement>;
  @Output() hasNoPermission = new EventEmitter<void>();
  @Output() onCoursesList = new EventEmitter<string[]>();
  defaultClub: Club;

  selectedClub: Club;
  selectedClubID: string;
  clubs: Club[] = [];
  page = 1;
  pageSize = 20;
  isLoading = false;
  showLoadMore = false;
  isMenuOpen = false;
  searchCtrl = new FormControl('');
  subscriptions = new Subscription();
  private onChange: (value?: string) => void;
  private onTouched: () => void;

  constructor(@Inject("ITeeTimeClubService") private teeTimeClubService: ITeeTimeClubService, private translate: TranslateService)
  {
    this.defaultClub = {
      ClubName: this.translate.instant("TEE_TIME.TEE_TIME_CLUB.ALL_CLUBS"),
      AllClubs: true
    } as Partial<Club> as Club;
    this.selectedClub = this.defaultClub;
  }

  ngOnInit(): void
  {
    this.getClubsList();
    this.subscriptions.add(this.searchCtrl.valueChanges.pipe(debounceTime(700), map(m => m.trim() === '' ? null : m.trim())).subscribe(searchTerm =>
    {
      this.page = 1;
      this.getClubsList({
        ...(searchTerm && { ClubName: searchTerm }),
      }, true);
    }));
  }

  public writeValue(value: string): void
  {
    if (value?.trim() !== '')
    {
      this.selectedClubID = value;
      const selectedClub = this.clubs.find(p => p.ClubCode === this.selectedClubID);
      if (selectedClub)
      {
        this.selectedClub = selectedClub;
        this.onCoursesList.emit(selectedClub.Courses?.map(m => m.CourseID));
      } else
      {
        this.selectedClub = this.defaultClub;
        this.hasNoPermission.emit();
        this.onCoursesList.emit(undefined);
      }
    } else
    {
      this.selectedClubID = undefined;
      this.selectedClub = this.defaultClub;
      this.onCoursesList.emit(undefined);
    }
  }

  public registerOnChange(fn: (value?: string) => void): void
  {
    this.onChange = fn;
  }

  public registerOnTouched(fn: () => void): void
  {
    this.onTouched = fn;
  }

  public setDisabledState?(isDisabled: boolean): void
  {
    // Not Implemented
  }

  fixMenuPanelWidth(): void
  {
    const menuElement = document.querySelector('.mat-menu-panel') as HTMLElement;
    if (menuElement)
    {
      menuElement.style.width = `${ this.clubCardContainer.nativeElement.clientWidth }px`;
      menuElement.style.maxWidth = 'unset';
    }
  }

  getClubsList(data?: GetClubsListRequest, isSearching: boolean = false): void
  {
    this.isLoading = true; //Developer Note: Buggy behavior of TEI loading module causes the menu panel be on top of the loading screen.
    this.teeTimeClubService.GetList(data).pipe(finalize(() => { this.isLoading = false; })).subscribe(result =>
    {
      if (this.page === 1)
      {
        this.clubs = [this.defaultClub, ...result.Clubs];
      } else
      {
        this.clubs.push(...result.Clubs);
      }
      // this.showLoadMore = result.Total > this.clubs.length && result.Clubs.length > 0; // Disabled the pagination for now. Might need it later
      if (!isSearching)
      {
        if (this.selectedClubID)
        {
          const selectedClub = this.clubs.find(p => p.ClubCode === this.selectedClubID);
          if (selectedClub)
          {
            this.selectedClub = selectedClub;
            this.onCoursesList.emit(selectedClub.Courses?.map(m => m.CourseID));
          } else
          {
            this.selectedClub = this.defaultClub;
            this.hasNoPermission.emit();
            this.onCoursesList.emit(undefined);
          }
        } else
        {
          this.selectedClub = this.defaultClub;
          this.onCoursesList.emit(undefined);
        }
      }
    });
  }

  handleClubClick(club: Club): void
  {
    this.selectedClub = club;
    this.selectedClubID = club.ClubCode;
    this.onCoursesList.emit(club.Courses?.map(m => m.CourseID));
    this.onChange(club.ClubCode);
  }

  @HostListener('scroll', ['$event'])
  onScroll(event: Event)
  {
    const el = event.target as HTMLElement;
    if (el.scrollHeight - el.scrollTop <= el.clientHeight + 1)
    {
      this.loadNextPage();
    }
  }

  loadNextPage(): void
  {
    if (this.isLoading || !this.showLoadMore)
    {
      return;
    }
    this.page++;
    const searchText = this.searchCtrl.value.trim() === '' ? null : this.searchCtrl.value.trim();
    this.getClubsList({
      ...(searchText && { ClubName: searchText }),
    }, !!searchText);
  }

  ngOnDestroy(): void
  {
    this.subscriptions.unsubscribe();
  }
}
