import { Component, EventEmitter, Inject, Input, OnInit, Output, SkipSelf } from '@angular/core';
import { Validators } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { ActivatedRoute } from '@angular/router';
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
import { Subject } from "rxjs";
import { DayOfWeekColAndRow, DayOfWeekList, TimeGroup, UtilizationRange } from '../../@intermediate/dynamic-pricing-details';
import { GetDynamicPricingWizardRequest, GetDynamicPricingWizardResponse, IDynamicPricingWizardService, PostDynamicPricingWizardRequestForm } from '../../@intermediate/dynamic-pricing-wizard';

@Component({
  selector: 'tei-tee-times-dialog-dynamic-pricing-wizard',
  templateUrl: './dynamic-pricing-wizard.component.html',
  styleUrls: ['./dynamic-pricing-wizard.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(DynamicPricingWizardComponent)
  ]
})
export class DynamicPricingWizardComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{
  model: GetDynamicPricingWizardResponse;
  form: PostDynamicPricingWizardRequestForm;
  templateID: string;
  @Input() resetFormSubject: Subject<boolean> = new Subject<boolean>();
  dayOfWeekID: string[] = [];
  dayOut: string[] = [];
  timeSelection: string[] = [];
  utilSelection: string[] = [];
  @Output() pricingEmitter = new EventEmitter<any>();

  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('IDynamicPricingWizardService') private DynamicPricingWizardService: IDynamicPricingWizardService,
    private activatedRoute: ActivatedRoute,
    private snackbarService: SnackBarService,
    private confirmdialogService: ConfirmDialogService)
  {
    super(_errorStateMatcher);
    this.templateID = this.activatedRoute.snapshot.params['templateID'];
  }

  ngOnInit()
  {
    this.form = new PostDynamicPricingWizardRequestForm();
    const request = new GetDynamicPricingWizardRequest();
    this.form.get('price').valueChanges.subscribe((data) =>
    {
      if (data == "Set")
      {
        this.form.get('SetPrice').enable();
        this.form.get('IncreasePrice').reset();
        this.form.get('DecreasePrice').reset();
        this.form.get('IncreasePrice').disable();
        this.form.get('DecreasePrice').disable();
      }
      else if (data == "Increase")
      {
        this.form.get('IncreasePrice').enable();
        this.form.get('SetPrice').reset();
        this.form.get('DecreasePrice').reset();
        this.form.get('SetPrice').disable();
        this.form.get('DecreasePrice').disable();
      }
      else if (data == "Decrease")
      {
        this.form.get('DecreasePrice').enable();
        this.form.get('SetPrice').reset();
        this.form.get('IncreasePrice').reset();
        this.form.get('SetPrice').disable();
        this.form.get('IncreasePrice').disable();
      }
      else if (data == "Reset")
      {
        this.form.get('SetPrice').reset();
        this.form.get('IncreasePrice').reset();
        this.form.get('DecreasePrice').reset();
        this.form.get('SetPrice').disable();
        this.form.get('IncreasePrice').disable();
        this.form.get('DecreasePrice').disable();
      }
    });

    this.resetFormSubject.subscribe(response =>
    {
      if (response)
      {
        this.load(request);
        this.form.get('SetPrice').reset();
        this.form.get('IncreasePrice').reset();
        this.form.get('DecreasePrice').reset();
        this.form.get('SetPrice').disable();
        this.form.get('IncreasePrice').disable();
        this.form.get('DecreasePrice').disable();
        this.form.get('Dayofweek').reset();
        this.form.get('DaysOut').reset();
        this.form.get('TimeGroup').reset();
        this.form.get('UtilizationRange').reset();
        this.form.get('price').patchValue('Reset');
        this.form.get('MinPricing').reset();
        this.form.get('MaxPricing').reset();
        this.form.get('MinPricing').patchValue('5.00');
        this.form.get('MaxPricing').patchValue('150.00');
        this.dayOfWeekID = [];
        this.dayOut = [];
        this.timeSelection = [];
        this.utilSelection = [];
      }
    });

    this.form.get('Dayofweek').setValidators(Validators.required);
    this.form.get('DaysOut').setValidators(Validators.required);
    this.form.get('TimeGroup').setValidators(Validators.required);
    this.form.get('UtilizationRange').setValidators(Validators.required);

    request.TemplateID = this.templateID;
    this.load(request);
  }

  chkDayOfWeekChange($event, day?: DayOfWeekList)
  {
    const checked = $event.checked;
    if (checked)
    {
      this.dayOfWeekID.push(this.model.DayOfWeekList.find(x => x.DayOfWeekID == day.DayOfWeekID).DayOfWeekID);
      this.form.get("Dayofweek").patchValue(this.dayOfWeekID);
    }
    else
    {
      let selectedDayofWeekId = this.dayOfWeekID.filter(x => x != day.DayOfWeekID);
      this.dayOfWeekID = [];
      selectedDayofWeekId.forEach(x =>
      {
        this.dayOfWeekID.push(x);
      });
      this.form.get("Dayofweek").patchValue(this.dayOfWeekID);
    }
  }

  chkDayOutChange($event, daysout?: DayOfWeekColAndRow)
  {
    const checked = $event.checked;
    if (checked)
    {
      this.dayOut.push(this.model.DayOfWeekColAndRow.find(x => x.DayOfWeekColID == daysout.DayOfWeekColID).DayOfWeekColID);
      this.form.get("DaysOut").patchValue(this.dayOut);
    }
    else
    {
      let selectedDayOut = this.dayOut.filter(x => x != daysout.DayOfWeekColID);
      this.dayOut = [];
      selectedDayOut.forEach(x =>
      {
        this.dayOut.push(x);
      });
      this.form.get("DaysOut").patchValue(this.dayOut);
    }
  }

  chkTimeChange($event, time?: TimeGroup)
  {
    const checked = $event.checked;
    if (checked)
    {
      this.timeSelection.push(this.model.TimeGroup.find(x => x.TimeGroupID == time.TimeGroupID).TimeGroupID);
      this.form.get("TimeGroup").patchValue(this.timeSelection);
    }
    else
    {
      let selectedTime = this.timeSelection.filter(x => x != time.TimeGroupID);
      this.timeSelection = [];
      selectedTime.forEach(x =>
      {
        this.timeSelection.push(x);
      });
      this.form.get("TimeGroup").patchValue(this.timeSelection);
    }
  }

  chkUtilChange($event, util?: UtilizationRange)
  {
    const checked = $event.checked;
    if (checked)
    {
      this.utilSelection.push(this.model.UtilizationRange.find(x => x.UtilizationRangeUID == util.UtilizationRangeUID).UtilizationRangeUID);
      this.form.get("UtilizationRange").patchValue(this.utilSelection);
    }
    else
    {
      let selectedUtil = this.utilSelection.filter(x => x != util.UtilizationRangeUID);
      this.utilSelection = [];
      selectedUtil.forEach(x =>
      {
        this.utilSelection.push(x);
      });
      this.form.get("UtilizationRange").patchValue(this.utilSelection);
    }
  }

  minPriceConversion(): void
  {
    let minprice = Number(this.form.get('MinPricing').value);
    this.form.get('MinPricing').patchValue(minprice.toFixed(2));
  }

  maxPriceConversion(): void
  {
    let maxprice = Number(this.form.get('MaxPricing').value);
    this.form.get('MaxPricing').patchValue(maxprice.toFixed(2));
  }

  logPriceValidation(): void
  {
    let minprice = Number(this.form.get('MinPricing').value);
    let maxprice = Number(this.form.get('MaxPricing').value);
    let currentValue = Number(this.form.get('SetPrice').value);

    if (currentValue < minprice)
    {
      const dialogData: DialogData = {
        title: '<div class="a-text-h3">Warning</div>',
        message: `<div class="a-text-default">The entered price is lower than the minimum price, are you sure you wish to apply this change?</div>`,
        textOkayButton: 'Continue',
        textCancelButton: 'Cancel'
      };

      this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
      {
        if (result)
        {
          this.form.get('SetPrice').patchValue(currentValue.toFixed(2));
          this.pricingEmitter.emit(this.form.value);
        }
      });
    }
    else if (currentValue > maxprice)
    {
      const dialogData: DialogData = {
        title: '<div class="a-text-h3">Warning</div>',
        message: `<div class="a-text-default">The entered price is greater than the maximum price, are you sure you wish to apply this change?</div>`,
        textOkayButton: 'Continue',
        textCancelButton: 'Cancel'
      };

      this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
      {
        if (result)
        {
          this.form.get('SetPrice').patchValue(currentValue.toFixed(2));
          this.pricingEmitter.emit(this.form.value);
        }
      });
    }
    else
    {
      this.form.get('SetPrice').patchValue(currentValue.toFixed(2));
      this.pricingEmitter.emit(this.form.value);
    }
  }


  private load(request: GetDynamicPricingWizardRequest)
  {
    if (!this.model)
    {
      this.model = new GetDynamicPricingWizardResponse();
    }

    this.DynamicPricingWizardService.Get(request).subscribe(response =>
    {
      this.model = response;
    });
  }

  save(form: PostDynamicPricingWizardRequestForm)
  {
    if (Number(this.form.get('SetPrice').value) && this.form.get('price').value == "Set")
    {
      this.logPriceValidation();
    }
    if (this.form.get('price').value == "Reset")
    {
      this.pricingEmitter.emit(this.form.value);
    }
    if (this.form.get('price').value == "Increase")
    {
      this.pricingEmitter.emit(this.form.value);
    }
    if (this.form.get('price').value == "Decrease")
    {
      this.pricingEmitter.emit(this.form.value);
    }
  }
}
