import { Component, Inject, Input, OnInit, SkipSelf, ViewChild } from '@angular/core';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatLegacyMenuTrigger as MatMenuTrigger } from '@angular/material/legacy-menu';
import { TranslateService } from '@ngx-translate/core';
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { FieldConfig } from '@tei/material-generic-filter/lib/field.interface';
import { GetTeeTimeFilterRequest, GetTeeTimeFilterResponse, ITeeTimeFilterService, PostTeeTimeFilterRequestForm } from '../../@intermediate/tee-time-filter';
import { Filtersetting } from '../../@intermediate/teetimes';

@Component({
  selector: 'tei-tee-times-dialog-tee-time-filter',
  templateUrl: './tee-time-filter.component.html',
  styleUrls: ['./tee-time-filter.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(TeeTimeFilterComponent)
  ]
})
export class TeeTimeFilterComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{
  model: GetTeeTimeFilterResponse;
  form = new PostTeeTimeFilterRequestForm(this.textTranslation);
  @Input() InputFilterData: FieldConfig[];
  @Input() InputFilterSettings: Filtersetting;
  @ViewChild(MatMenuTrigger) menuTrigger: MatMenuTrigger;
  holes: FieldConfig;
  minPrice: number;
  maxPrice: number;
  priceLabel: string;
  step: number;
  specials: FieldConfig;
  cart: FieldConfig;
  cartLabel: string;
  cartValue: string;
  cartIcon: string;
  prepaidLabel: string;
  prepaidValue: string;
  prepaidIcon: string;
  cartToggleValue: string;
  prepaidToggleValue: string;
  sliderPriceValue: string;
  @ViewChild('slider') slider;

  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('ITeeTimeFilterService') private TeeTimeFilterService: ITeeTimeFilterService,
    private textTranslation: TranslateService
  )
  {
    super(_errorStateMatcher);
  }

  ngOnInit()
  {
    this.form = new PostTeeTimeFilterRequestForm(this.textTranslation);
    const request = new GetTeeTimeFilterRequest();
    //this.load(request);
    this.load(this.InputFilterData, this.InputFilterSettings);
  }



  ngAfterViewInit(): void
  {
    this.form.get('Holes').valueChanges.subscribe(filtervalue =>
    {
      if (filtervalue)
      {
        this.holes.value = filtervalue;
      }
    });

    this.form.get('Specials').valueChanges.subscribe(filtervalue =>
    {
      if (filtervalue)
      {
        this.specials.value = filtervalue;
      }
    });
  }

  public change($event)
  {
    this.sliderPriceValue = $event.value;
  }

  close()
  {
    this.menuTrigger.closeMenu();
  }

  removeHole(hole: string)
  {
    if (hole == '18' || '9')
    {
      this.form.get('Holes').patchValue('Any');
    }
  }

  removeSpecial(special: string)
  {
    if (special == this.textTranslation.instant('TEE_TIME.TEE_TIME_FILTER.SPECIALS'))
    {
      this.form.get('Specials').patchValue('All');
    }
  }

  removePrice(price: string)
  {
    if (price != this.textTranslation.instant('TEE_TIME.TEE_TIME_FILTER.PRICE'))
    {
      this.priceLabel = this.textTranslation.instant('TEE_TIME.TEE_TIME_FILTER.PRICE');
      this.slider.value = 0;
      this.sliderPriceValue = undefined;
      this.form.get('Price').patchValue(null);
    }
  }


  applyPrice()
  {
    this.form.get('Price').patchValue((this.sliderPriceValue == '0' || this.sliderPriceValue == undefined) ? null : this.sliderPriceValue);
    this.priceLabel = (this.sliderPriceValue != '0' && this.sliderPriceValue != undefined) ? this.textTranslation.instant('TEE_TIME.TEE_TIME_FILTER.UP_TO_DOLLAR') + this.sliderPriceValue : this.textTranslation.instant('TEE_TIME.TEE_TIME_FILTER.UP_TO_DOLLAR') + this.maxPrice;
  }

  // private load(request: GetTeeTimeFilterRequest)
  // {
  //   if (!this.model)
  //   {
  //     this.model = new GetTeeTimeFilterResponse();
  //   }

  //   this.TeeTimeFilterService.Get(request).subscribe(response =>
  //   {
  //     this.model = response;
  //     this.writeValue(this.model);
  //   });
  // }

  private load(inputdata: FieldConfig[], inputfilter: Filtersetting)
  {
    this.model = new GetTeeTimeFilterResponse();

    this.model.Filterdata = inputdata;
    this.model.FilterSettings = inputfilter;

    this.holes = inputdata.find(x => x.identifier === 'Holes');
    this.priceLabel = inputdata.find(x => x.identifier === 'Price').label;
    this.minPrice = Number(inputdata.find(x => x.identifier === 'Price').options.find(x => x.identifier === 'floor').value);
    this.maxPrice = Number(inputdata.find(x => x.identifier === 'Price').options.find(x => x.identifier === 'ceil').value);
    this.step = Number(inputdata.find(x => x.identifier === 'Price').options.find(x => x.identifier === 'step').value);
    this.specials = inputdata.find(x => x.identifier === 'Specials');
    this.cart = inputdata.find(x => x.identifier === 'Cart Included');
    this.cartIcon = inputdata.find(x => x.identifier === 'Cart Included').name;
    this.cartLabel = inputdata.find(x => x.identifier === 'Cart Included').label;
    this.cartValue = inputdata.find(x => x.identifier === 'Cart Included').value;
    this.cartToggleValue = 'CartIncluded';
    this.prepaidToggleValue = 'PrePaid';
    this.prepaidIcon = inputdata.find(x => x.identifier === 'Prepaid Only').name;
    this.prepaidLabel = inputdata.find(x => x.identifier === 'Prepaid Only').label;
    this.prepaidValue = inputdata.find(x => x.identifier === 'Prepaid Only').value;

    this.writeValue(this.model);
  }


  togglecartTag(item: string)
  {
    (this.form.get('CartIncluded').value) ? this.form.get('CartIncluded').reset() : this.form.get('CartIncluded').patchValue(item);
  }

  toggleprepaidTag(item: string)
  {
    (this.form.get('PrepaidOnly').value) ? this.form.get('PrepaidOnly').reset() : this.form.get('PrepaidOnly').patchValue(item);
  }
}
