import { UntypedFormArray, UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { DynamicPricingTemplate } from '../../dynamic-pricing-details';
import { GetDynamicPricingDetailTemplateResponse, PriceLevelOption } from './get-dynamic-pricing-detail-template.model';

export class PostDynamicPricingDetailTemplateRequest
{
  public DynamicPriceForm: DynamicPricingTemplate[];
}

export class PostDynamicPricingDetailTemplateRequestForm extends UntypedFormGroup
{
  public readonly DynamicPriceForm: DynamicPriceFieldFormArray;

  constructor(value: DynamicPricingTemplate[])
  {
    super({
      DynamicPriceForm: new DynamicPriceFieldFormArray(value),
    });

    this.DynamicPriceForm = this.get('DynamicPriceForm') as DynamicPriceFieldFormArray;
  }

  public value: PostDynamicPricingDetailTemplateRequest;

  public setValue(
    value: GetDynamicPricingDetailTemplateResponse,
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    const request = new PostDynamicPricingDetailTemplateRequest();
    request.DynamicPriceForm = value?.DynamicPricingTemplates;
    super.setValue(request, options);
  }
}

export class DynamicPriceFieldFormArray extends UntypedFormArray
{
  constructor(value: DynamicPricingTemplate[])
  {
    super([]);
    this.setValue(value);
  }
  public setValue(value: DynamicPricingTemplate[],
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    if (value != undefined)
    {
      value.forEach(dynamicPricingDetails =>
      {
        if (dynamicPricingDetails.AllPriceLevels)
        {
          dynamicPricingDetails.PriceLevels.forEach(f => { f.Disabled = true; f.Selected = false; });
        }
        let dynamicPricingForm = new DynamicPriceFieldForm(dynamicPricingDetails);
        this.push(dynamicPricingForm);
      });
    }
  }
}

class DynamicPriceFieldForm extends UntypedFormGroup
{
  public readonly PriceLevels: PriceLevelOptionFormArray;


  constructor(value: DynamicPricingTemplate)
  {
    super({
      StartDate: new UntypedFormControl(value.StartDate),
      EndDate: new UntypedFormControl(value.EndDate),
      AlloctedPrice: new UntypedFormControl(value.AlloctedPrice.toFixed(2)),
      IsPowerCartIncluded: new UntypedFormControl(value.IsPowerCartIncluded),
      IsWeb: new UntypedFormControl(value != undefined ? value.IsWeb : false),
      IsLocal: new UntypedFormControl(value != undefined ? value.IsLocal : false),
      EnablePrepay: new UntypedFormControl(value != undefined ? value.EnablePrepay : false),
      RequirePrepay: new UntypedFormControl(value != undefined ? value.RequirePrepay : false),
      Prepay18GolfDiscountPercent: new UntypedFormControl(Number(value.Prepay18GolfDiscountPercent) * 100),
      Prepay18CartDiscountPercent: new UntypedFormControl(Number(value.Prepay18CartDiscountPercent) * 100),
      PriceLevels: new PriceLevelOptionFormArray(value.PriceLevels),
      PublicPriceLevel: new UntypedFormControl(value.PublicPriceLevel),
      AllPriceLevels: new UntypedFormControl(value.AllPriceLevels)
    });

    this.PriceLevels = this.get('PriceLevels') as PriceLevelOptionFormArray;

  }
}


class PriceLevelOptionFormArray extends UntypedFormArray
{
  constructor(value: PriceLevelOption[])
  {
    super([]);
    this.setValue(value);
  }
  public setValue(value: PriceLevelOption[],
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    if (value != undefined)
    {
      value.forEach(priceLevel =>
      {
        let priceLevelOption = new PriceLevelForm(priceLevel);
        this.push(priceLevelOption);
      });
    }
  }
}

class PriceLevelForm extends UntypedFormGroup
{
  constructor(value: PriceLevelOption)
  {
    super({
      Display: new UntypedFormControl(value.Display),
      Selected: new UntypedFormControl(value.Selected),
      Value: new UntypedFormControl(value.Value),
      Disabled: new UntypedFormControl(value.Disabled),
    });
  }
}




export class PostDynamicPricingDetailTemplateResponse
{

}
