import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IDynamicPricingWizardService, GetDynamicPricingWizardRequest, GetDynamicPricingWizardResponse, PostDynamicPricingWizardRequest, PostDynamicPricingWizardResponse } from '../@intermediate/dynamic-pricing-wizard';

@Injectable({
  providedIn: 'root'
})

export class DynamicPricingWizardService implements IDynamicPricingWizardService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Booking/TeeTimes/DynamicPricingWizard';
  }

  public Get(request: GetDynamicPricingWizardRequest): Observable<GetDynamicPricingWizardResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetDynamicPricingWizardResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostDynamicPricingWizardRequest): Observable<PostDynamicPricingWizardResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostDynamicPricingWizardResponse>(apiUrl, request);
  }
}
