import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { PostContactSelectionWrapperRequest } from '@tei/common-contact-lookup-shared';
import { GetPriceTeeTimeDetailsRequest } from './get-price-tee-time-details.model';
import { GetTeeTimeDetailsResponse } from './get-tee-time-details.model';


export class PostTeeTimeDetailsRequest
{
  public TermsAndConditions: boolean;
  public IsReleaseTeeTime: boolean;
  public TeeTimeID: string;
  public ContactLookupWrapper: any;
  public IsAddToShoppingCart: boolean;
  public IsCommitTeeTime: boolean;
  public ContactSelections: PostContactSelectionWrapperRequest;
  public TeeTimePriceDetails: GetPriceTeeTimeDetailsRequest;
  public SelectionHeaderUID: string;
  public PrePayTeeTime: boolean;
  public CreditCardRequired: boolean;
  public MoveReservation: boolean;
  public MoveTeeTimeID: string;
}



export class PostTeeTimeDetailsRequestForm extends UntypedFormGroup
{
  constructor(edit: boolean)
  {
    super({
      TermsAndConditions: new UntypedFormControl(false, !edit ? Validators.requiredTrue : Validators.nullValidator),
      ContactLookupWrapper: new UntypedFormControl('')
    });
  }

  public value: PostTeeTimeDetailsRequest;

  public setValue(value: GetTeeTimeDetailsResponse)
  {
    const request = new PostTeeTimeDetailsRequest();

    request.TermsAndConditions = value.TermsAndConditions;
    request.ContactLookupWrapper = null;
    super.setValue(request);
  }
}

export class PostTeeTimeDetailsResponse
{
  public PaymentMethodUrl: string;
  public SettingValue: string;
  public ErrorMessage: string;
  public Count: number;
  public TeeTimeId: string;
  public CCRequired: boolean;
}
