import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { GetManageDialogResponse } from './get-manage-dialog.model';

export class PostManageDialogRequest
{
  // public Title: string;
  public Player: string;
  public Holes: string;
  public Cart: string;
  public CourseID: string;
  public Time: string;
  public Date: string;
  public RequiredGolfCart: number;
  public StartTee: number;
  public CourseName: string;
  public SubTitle: string;
  public TeeTimeDisplay: string;
  public PaymentType: number;
  public TeeTimeID: string;
  public IsMoveReservation: boolean;
  public MoveTeeTimeID: string;
}

export class PostManageDialogRequestForm extends UntypedFormGroup
{
  constructor(cartIncluded: boolean, holeDisabled: boolean, groupsFilter: boolean)
  {
    super({
      Player: new UntypedFormControl({ value: '', disabled: groupsFilter }, Validators.required),
      Holes: new UntypedFormControl({ value: '', disabled: holeDisabled }, Validators.required),
      Cart: new UntypedFormControl({ value: '', disabled: cartIncluded }, Validators.required),
      CourseID: new UntypedFormControl(''),
      Time: new UntypedFormControl(''),
      Date: new UntypedFormControl(''),
      RequiredGolfCart: new UntypedFormControl(''),
      StartTee: new UntypedFormControl(''),
      CourseName: new UntypedFormControl(''),
      SubTitle: new UntypedFormControl(''),
      TeeTimeDisplay: new UntypedFormControl('')
    });
  }

  public value: PostManageDialogRequest;

  public setValue(
    value: GetManageDialogResponse,
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    const request = new PostManageDialogRequest();

    request.Player = value.TeeTimeDialog.DialogPlayers.filter(x => x.isSelected === true)[0].value;
    request.Holes = value.TeeTimeDialog.DialogHoles.filter(x => x.IsSelected === true)[0].Value;
    const cart = value.TeeTimeDialog.Cart.filter(x => x.IsSelected === true || x.IsDefalut === true)[0];
    request.Cart = cart ? cart.Value : null;
    request.CourseID = value.TeeTimeDialog.CourseID;
    request.Date = value.TeeTimeDialog.Date;
    request.Time = value.TeeTimeDialog.Time;
    request.RequiredGolfCart = value.TeeTimeDialog.RequiredGolfCart;
    request.StartTee = value.TeeTimeDialog.StartTee;
    request.CourseName = value.TeeTimeDialog.SubTitle;
    request.SubTitle = value.TeeTimeDialog.SubTitle;
    request.TeeTimeDisplay = value.TeeTimeDialog.TeeTimeDisplay;
    super.setValue(request, options);
  }
}

export class PostManageDialogResponse
{
  IsUserLoggedIn: boolean;
  AnonymousContactID: string;

}
