import { CartOption } from "../../teetimes";

export class GetSettingsRequest
{
  public CourseID: String;
  public CourseName: String;
  public IsNineHole: String;
}

export class GetSettingsResponse
{
  public ImageUrl: String;
  public ImageName: String;
  public CourseName: String;
  public CourseID: String;
  public IsNineHole: String;
  public RoleBasedSettings: RoleBasedSetting[];
  public CartOptions: CartOption[];
}

export class RoleBasedSetting
{
  public RoleSettingName: string;
  public RoleBasedID: number;
  public RoleValueSetting: RoleValueSetting[];
}

export class RoleValueSetting
{
  public RoleName: string;
  public Value: string;
  public Type: string;
  public RoleID: number;
}
