import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { GetTeeTimeFilterResponse } from './get-tee-time-filter.model';

export class PostTeeTimeFilterRequest
{
  public Title: string;
}

export class PostTeeTimeFilterRequestForm extends UntypedFormGroup
{
  constructor(
    private textTranslation: TranslateService
  )
  {
    super({
      Holes: new UntypedFormControl(''),
      Price: new UntypedFormControl(''),
      Specials: new UntypedFormControl(''),
      CartIncluded: new UntypedFormControl(''),
      PrepaidOnly: new UntypedFormControl('')
    });
  }

  public value: PostTeeTimeFilterRequest;

  public setValue(
    value: GetTeeTimeFilterResponse,
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    const request = new PostTeeTimeFilterRequest();

    this.get('Holes').patchValue(value.Filterdata.find(x => x.identifier === 'Holes').value);
    this.get('Specials').patchValue(value.Filterdata.find(x => x.identifier === 'Specials').value);
    this.get('CartIncluded').patchValue(value.Filterdata.find(x => x.identifier === 'Cart Included').value);
    this.get('PrepaidOnly').patchValue(value.Filterdata.find(x => x.identifier === 'Prepaid Only').value);

    //super.setValue(request, options);
  }
}

export class PostTeeTimeFilterResponse
{

}
