import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IDynamicPricingTemplateService, GetDynamicPricingTemplateRequest, GetDynamicPricingTemplateResponse, PostDynamicPricingTemplateRequest, PostDynamicPricingTemplateResponse } from '../@intermediate/dynamic-pricing-template';

@Injectable({
  providedIn: 'root'
})

export class DynamicPricingTemplateService implements IDynamicPricingTemplateService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Booking/TeeTimes/DynamicPricingTemplate';
  }

  public Get(request: GetDynamicPricingTemplateRequest): Observable<GetDynamicPricingTemplateResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetDynamicPricingTemplateResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostDynamicPricingTemplateRequest): Observable<PostDynamicPricingTemplateResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostDynamicPricingTemplateResponse>(apiUrl, request);
  }
}
