import { ChangeDetectionStrategy, ChangeDetectorRef, Component, HostListener, Inject, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { AbstractControl, UntypedFormArray, UntypedFormGroup } from '@angular/forms';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatSidenav } from '@angular/material/sidenav';
import { ActivatedRoute, Router } from '@angular/router';
import { ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
import { Subject } from "rxjs";
import { PriceLevelOption } from '../../@intermediate/dynamic-pricing-detail-template';
import { DayOfWeekColAndRow, DayOfWeekList, GetDynamicPricingDetailsRequest, GetDynamicPricingDetailsResponse, IDynamicPricingDetailsService, PostDynamicPricingDetailsRequest, PostDynamicPricingDetailsRequestForm, TimeGroupDetails } from '../../@intermediate/dynamic-pricing-details';
import { DynamicPricingUtilizationRangesComponent } from '../../components/dynamic-pricing-utilization-ranges/dynamic-pricing-utilization-ranges.component';
import { DynamicTimeGroupsComponent } from '../../components/dynamic-time-groups/dynamic-time-groups.component';



@Component({
  selector: 'tei-tee-times-pages-dynamic-pricing-details',
  templateUrl: './dynamic-pricing-details.component.html',
  styleUrls: ['./dynamic-pricing-details.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class DynamicPricingDetailsComponent implements OnInit, OnDestroy
{
  model: GetDynamicPricingDetailsResponse;
  form: PostDynamicPricingDetailsRequestForm;
  templateID: string;
  dayOfWeekCols: DayOfWeekColAndRow[];
  dayOfWeekList: DayOfWeekList[];
  control: AbstractControl[];
  hideDynamicPricingTable: boolean = false;
  powercartPrice: boolean;
  subTimeout: any;
  rangeDetailIndex: number;
  updatedValues: TimeGroupDetails[] = [];
  opened: boolean = false;
  initialStartDate: Date | string;
  initialEndDate: Date | string;
  initialPowerCartIncluded: boolean;
  initialAllocatedPrice: number;
  intialIsLocal: boolean;
  intialIsWeb: boolean;
  intialEnablePrepay: boolean;
  intialRequirePrepay: boolean;
  intialPrepay18GolfDiscountPercent: number;
  intialPrepay18CartDiscountPercent: number;
  @ViewChild('sidenav') sidenav: MatSidenav;
  resetFormSubject: Subject<boolean> = new Subject<boolean>();
  emittedData: any;
  onPop: boolean = true;
  highlightData: any;
  setForm: boolean = false;
  dynamicPricingAccess: boolean = true;
  dynamicPricingErrorMessage: string;
  detectInterval: any;
  backNavigate: boolean = false;




  constructor(@Inject('IDynamicPricingDetailsService') private DynamicPricingDetailsService: IDynamicPricingDetailsService, private activatedRoute: ActivatedRoute,
    private snackbarService: SnackBarService, public dialog: MatDialog, private ref: ChangeDetectorRef,
    private confirmdialogService: ConfirmDialogService, private router: Router)
  {
    this.templateID = this.activatedRoute.snapshot.params['templateID'];
  }

  ngOnInit()
  {
    this.form = new PostDynamicPricingDetailsRequestForm(undefined);
    const request = new GetDynamicPricingDetailsRequest();
    this.form.get('DynamicPricingWizard').valueChanges.subscribe((data) =>
    {

      this.highlightcell(data);
      this.form.get('DynamicPricingWizard').clearValidators();
    });

    request.TemplateID = this.templateID;
    this.load(request);
    this.ref.detach();
    // interval for doing the change detection every 5 seconds
    this.detectInterval = setInterval(() =>
    {
      this.ref.detectChanges();
    }, 1000);
  }

  ngOnDestroy()
  {
    clearInterval(this.detectInterval);
  }

  //on screen refresh this method is called, when you have unsaved changes
  @HostListener('window:beforeunload', ['$event'])
  unloadNotification($event: any)
  {
    if (this.hasUnsavedData())
    {
      $event.returnValue = true;
    }
  }

  //on backtab button this method is called , when you have unsaved changes
  canExit(): boolean
  {
    if (this.hasUnsavedData() && !this.backNavigate)
    {
      if (confirm("Changes you made may not be saved."))
      {
        return true;
      } else
      {
        return false;
      }
    }

    return true;
  }

  hasUnsavedData()
  {
    if (this.tempalateValueUpdated() == true || this.form.DayOfWeekColAndRow.touched == true || this.form.DayOfWeekColAndRow.dirty == true)
    {
      return true;
    }
    else
    {
      return false;
    }
  }

  back()
  {
    if (this.hasUnsavedData())
    {
      this.backNavigate = true;
      const dialogData: DialogData = {
        title: 'Warning',
        message: `You have unsaved changes! If you leave, your changes will be lost.`,
        textOkayButton: 'Continue',
        textCancelButton: 'Cancel'
      };

      this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
      {
        if (result)
        {
          this.router.navigate(['/tee-times/dynamic-pricing']);
        }
        else
        {
          this.backNavigate = false;
        }
      });
    }
    else
    {
      this.router.navigate(['/tee-times/dynamic-pricing']);
    }
  }


  private load(request: GetDynamicPricingDetailsRequest)
  {
    if (!this.model)
    {
      this.model = new GetDynamicPricingDetailsResponse();
    }


    this.DynamicPricingDetailsService.Get(request).subscribe(response =>
    {
      this.model = response;
      if (this.model.DynamicPricingTemplates != null)
      {
        this.powercartPrice = this.model.DynamicPricingTemplates[0]?.IsPowerCartIncluded;
        this.initialStartDate = this.model.DynamicPricingTemplates[0]?.StartDate;
        this.initialEndDate = this.model.DynamicPricingTemplates[0]?.EndDate;
        this.initialPowerCartIncluded = this.model.DynamicPricingTemplates[0]?.IsPowerCartIncluded;
        this.initialAllocatedPrice = this.model.DynamicPricingTemplates[0]?.AlloctedPrice;
        this.intialIsLocal = this.model.DynamicPricingTemplates[0]?.IsLocal;
        this.intialIsWeb = this.model.DynamicPricingTemplates[0]?.IsWeb;
        this.intialEnablePrepay = this.model.DynamicPricingTemplates[0]?.EnablePrepay;
        this.intialRequirePrepay = this.model.DynamicPricingTemplates[0]?.RequirePrepay;
        this.intialPrepay18GolfDiscountPercent = Number(this.model.DynamicPricingTemplates[0]?.Prepay18GolfDiscountPercent) * 100;
        this.intialPrepay18CartDiscountPercent = Number(this.model.DynamicPricingTemplates[0]?.Prepay18CartDiscountPercent) * 100;
      }
      if (this.model.DayOfWeekColAndRow != null)
      {
        this.dayOfWeekCols = this.model?.DayOfWeekColAndRow;
      }
      if (this.model.TimeGroup.filter(x => x.TimeGroupID != null).length <= 0 || this.model.UtilizationRange.filter(x => x.UtilizationRangeUID != null).length <= 0)
      {
        this.hideDynamicPricingTable = true;
      }
      else
      {
        this.hideDynamicPricingTable = false;
      }

      this.dayOfWeekList = this.model.DayOfWeekList;
      this.form.DayOfWeekColAndRow.setValue(this.model.DayOfWeekColAndRow);
      this.form.controls["IsPublished"].setValue(this.model.IsPublished);
    },
      error =>
      {
        if (error.error.Message == 'Authorization has been denied for this request.')
        {
          this.dynamicPricingAccess = false;
          this.dynamicPricingErrorMessage = error.error.Message;
        }
      });

  }

  minPriceConversion(): void
  {
    let minprice = Number(this.form.controls["DynamicPriceForm"].value.DynamicPriceForm[0].MinPrice);
    this.form.get('DynamicPriceForm').get('0').get("MinPrice").patchValue(minprice.toFixed(2));
  }

  maxPriceConversion(): void
  {
    let maxprice = Number(this.form.controls["DynamicPriceForm"].value.DynamicPriceForm[0].MaxPrice);
    this.form.get('DynamicPriceForm').get('0').get("MaxPrice").patchValue(maxprice.toFixed(2));
  }

  alloctedPriceConversion(): void
  {
    let allocatedprice = Number(this.form.controls["DynamicPriceForm"].value.DynamicPriceForm[0].AlloctedPrice);
    this.form.get('DynamicPriceForm').get('0').get("AlloctedPrice").patchValue(allocatedprice.toFixed(2));
  }

  close()
  {
    this.sidenav.close();
    this.resetFormSubject.next(true);
  }

  ClearAll()
  {
    this.resetFormSubject.next(true);
  }

  open()
  {
    this.sidenav.open();
  }

  onApply(event: any)
  {
    this.emittedData = event;

    this.emittedData["Dayofweek"].forEach(w =>
    {
      this.emittedData["DaysOut"].forEach(o =>
      {
        this.emittedData["TimeGroup"].forEach(t =>
        {
          this.emittedData["UtilizationRange"].forEach(u =>
          {
            let dayOfWeekColArray = this.form.get('DayOfWeekColAndRow') as UntypedFormArray;
            dayOfWeekColArray.controls.forEach(x =>
            {
              let timeGroupRange = x.get('TimeGroupRangeDetails') as UntypedFormArray;
              if (timeGroupRange.controls.length != 0)
              {
                timeGroupRange.controls.forEach(tr =>
                {
                  if (tr.value.ColID == o && tr.value.RowID == w)
                  {
                    let utilRange = tr.get('UtilRangeGroupList') as UntypedFormArray;
                    utilRange.controls.forEach(ut =>
                    {
                      const timeGroupDetail = ut.get('TimeGroupDetails') as UntypedFormArray;
                      timeGroupDetail.controls.forEach(tg =>
                      {
                        if (tg.get('DayOfWeekRowID').value == w && tg.get('DayOfWeekColID').value == o && tg.get('TimeGroupID').value == t && tg.get('UtilizationRangeID').value == u)
                        {
                          if (this.emittedData.price == "Reset")
                          {
                            tg.get('Price').patchValue(Number(tg.get('DefaultPrice').value).toFixed(2));
                          }
                          else if (this.emittedData.SetPrice)
                          {
                            tg.get('Price').patchValue(Number(this.emittedData.SetPrice).toFixed(2));
                          }
                          else if (this.emittedData.IncreasePrice)
                          {
                            let currentValue = Number(tg.get('Price').value);
                            let minprice = Number(this.emittedData["MinPricing"]);
                            let maxprice = Number(this.emittedData["MaxPricing"]);
                            let increasedprice = currentValue + Number(this.emittedData.IncreasePrice);
                            if (increasedprice < minprice)
                            {
                              tg.get('Price').patchValue(minprice.toFixed(2));
                            }
                            else if (increasedprice > maxprice)
                            {
                              tg.get('Price').patchValue(maxprice.toFixed(2));
                            }
                            else
                            {
                              tg.get('Price').patchValue(increasedprice.toFixed(2));
                            }
                          }
                          else if (this.emittedData.DecreasePrice)
                          {
                            let currentValue = Number(tg.get('Price').value);
                            let minprice = Number(this.emittedData["MinPricing"]);
                            let maxprice = Number(this.emittedData["MaxPricing"]);
                            let decreasedprice = currentValue - Number(this.emittedData.DecreasePrice);
                            if (decreasedprice < minprice)
                            {
                              tg.get('Price').patchValue(minprice.toFixed(2));
                            }
                            else if (decreasedprice > maxprice)
                            {
                              tg.get('Price').patchValue(maxprice.toFixed(2));
                            }
                            else
                            {
                              tg.get('Price').patchValue(decreasedprice.toFixed(2));
                            }
                          }
                          tg.markAsDirty();
                          // this.resetFormSubject.next(true);
                        }
                      });
                    });
                  }
                });
              }
            });
          });
        });
      });
    });

    //  this.form.get('DynamicPricingWizard').clearValidators();
    //this.form.get('DynamicPricingWizard').updateValueAndValidity();

    //this.sidenav.close();
  }


  trackPricingID(index, price)
  {
    return price.value.TimeGroupDetailsID;
  }

  dayOfWeekColAndRowID(index, dayOfWeek)
  {
    return dayOfWeek.value.DayOfWeekColID;
  }

  timeGroupRangeDetailsID(index, range)
  {
    return range.value.RowID;
  }

  trackRangeID(index, utilRange)
  {
    return utilRange.value.RangeID;
  }

  trackGroupID(index, timeGroup)
  {
    return timeGroup.value.TimeGroupID;
  }

  openTimeGroupDialog(): void
  {
    if (this.tempalateValueUpdated() == true || this.form.DayOfWeekColAndRow.touched == true || this.form.DayOfWeekColAndRow.dirty == true)
    {
      const dialogData = this.dataDialog();
      this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
      {
        if (result)
        {
          this.save(this.form);
          setTimeout(() =>
          {
            this.timeGroupDialog();
          }, 1500);
        }
        else
        {
          this.confirmdialogService.dialog.closeAll();
        }
      });
    }
    else
    {
      this.timeGroupDialog();
    }
  }


  dataDialog(): DialogData
  {
    const dataDialog: DialogData = {
      title: 'Warning',
      message: this.getWarningMessage(),
      textOkayButton: 'Save & Continue',
      textCancelButton: 'Cancel'
    };
    return dataDialog;
  }

  timeGroupDialog()
  {
    let dialogref = this.dialog.open(DynamicTimeGroupsComponent, {
      panelClass: '',
      maxWidth: '200vh',
      data: { CourseTemplateUID: this.templateID }
    });

    dialogref.afterClosed().subscribe(result =>
    {
      if (result !== 'canceled')
      {
        const request = new GetDynamicPricingDetailsRequest();
        request.TemplateID = this.templateID;
        this.form.DayOfWeekColAndRow.clear();
        this.load(request);
      }

    });
  }


  openUtilizationRangesDialog(): void
  {
    if (this.tempalateValueUpdated() == true || this.form.DayOfWeekColAndRow.touched == true || this.form.DayOfWeekColAndRow.dirty == true)
    {
      const dialogData = this.dataDialog();
      this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
      {
        if (result)
        {
          this.save(this.form);
          setTimeout(() =>
          {
            this.utilRangeDialog();
          }, 1500);
        }
        else
        {
          this.confirmdialogService.dialog.closeAll();
        }
      });
    }
    else
    {
      this.utilRangeDialog();
    }
  }


  utilRangeDialog()
  {
    let dialogref = this.dialog.open(DynamicPricingUtilizationRangesComponent, {
      panelClass: '',
      maxWidth: '200vh',
      data: { templateUID: this.templateID }
    });

    dialogref.afterClosed().subscribe(result =>
    {
      if (result !== 'canceled')
      {
        const request = new GetDynamicPricingDetailsRequest();
        request.TemplateID = this.templateID;
        this.form.DayOfWeekColAndRow.clear();
        this.load(request);
      }
    });
  }


  tempalateValueUpdated()
  {
    let updated: boolean = false;
    let isPowerCartIncluded = this.form.controls["DynamicPriceForm"].value.DynamicPriceForm[0].IsPowerCartIncluded;
    let allocatedprice = Number(this.form.controls["DynamicPriceForm"].value.DynamicPriceForm[0].AlloctedPrice);
    let startDate = this.form.controls["DynamicPriceForm"].value.DynamicPriceForm[0].StartDate;
    let endDate = this.form.controls["DynamicPriceForm"].value.DynamicPriceForm[0].EndDate;
    let isLocal = this.form.controls["DynamicPriceForm"].value.DynamicPriceForm[0].IsLocal;
    let isWeb = this.form.controls["DynamicPriceForm"].value.DynamicPriceForm[0].IsWeb;
    let enablePrepay = this.form.controls["DynamicPriceForm"].value.DynamicPriceForm[0].EnablePrepay;
    let requirePrepay = (this.form.controls["DynamicPriceForm"].value.DynamicPriceForm[0].RequirePrepay == undefined) ? false : this.form.controls["DynamicPriceForm"].value.DynamicPriceForm[0].RequirePrepay;
    let prepay18GolfDiscountPercent = (this.form.controls["DynamicPriceForm"].value.DynamicPriceForm[0].Prepay18GolfDiscountPercent == undefined) ? 0 : this.form.controls["DynamicPriceForm"].value.DynamicPriceForm[0].Prepay18GolfDiscountPercent;
    let prepay18CartDiscountPercent = (this.form.controls["DynamicPriceForm"].value.DynamicPriceForm[0].Prepay18CartDiscountPercent == undefined) ? 0 : this.form.controls["DynamicPriceForm"].value.DynamicPriceForm[0].Prepay18CartDiscountPercent;

    if (new Date(this.initialStartDate).toString() != startDate || new Date(this.initialEndDate).toString() != endDate || this.initialPowerCartIncluded.toString() != isPowerCartIncluded.toString()
      || this.initialAllocatedPrice != allocatedprice || this.intialIsWeb != isWeb || this.intialIsLocal != isLocal || this.intialEnablePrepay != enablePrepay
      || this.intialRequirePrepay != requirePrepay || this.intialPrepay18GolfDiscountPercent != prepay18GolfDiscountPercent
      || this.intialPrepay18CartDiscountPercent != prepay18CartDiscountPercent)
    {
      updated = true;
    }
    return updated;
  }

  getWarningMessage()
  {
    let warningMessage = "";
    if ((this.form.DayOfWeekColAndRow.touched == true || this.form.DayOfWeekColAndRow.dirty == true) && this.tempalateValueUpdated() == false)
    {
      warningMessage = `You have unsaved changes in your price.`;
    }
    if (this.tempalateValueUpdated() == true && this.form.DayOfWeekColAndRow.touched == false)
    {
      warningMessage = `You have unsaved changes in your template.`;
    }
    if (this.tempalateValueUpdated() == true && (this.form.DayOfWeekColAndRow.touched == true || this.form.DayOfWeekColAndRow.dirty == true))
    {
      warningMessage = `You have unsaved changes in your template and price.`;
    }
    return warningMessage;
  }

  logPriceConversion(timeGroupdetails: UntypedFormGroup): void
  {
    let currentValue = Number(timeGroupdetails.value.Price);
    timeGroupdetails["controls"]["Price"].patchValue(currentValue.toFixed(2));
  }

  save(form: PostDynamicPricingDetailsRequestForm)
  {
    this.setForm = true;
    this.updatedValues = [];
    this.getDynamicPricingUpdatedValues(this.form.DayOfWeekColAndRow);
    const reqForm = this.form.controls["DynamicPriceForm"].value.DynamicPriceForm;
    let req = new PostDynamicPricingDetailsRequest();

    req.CourseTemplateUID = this.templateID;
    req.IsPublished = this.form.value["IsPublished"];
    this.form.controls["DynamicPriceForm"].value.DynamicPriceForm.forEach(x =>
    {
      req.IsWeb = x.IsWeb;
      req.IsLocal = x.IsLocal;
      req.EnablePrepaid = x.EnablePrepay;
      req.RequirePrepaid = (x.RequirePrepay == undefined) ? false : x.RequirePrepay;
      req.Prepay18GolfDiscountPercent = (x.Prepay18GolfDiscountPercent == undefined) ? 0 : Number(x.Prepay18GolfDiscountPercent) / 100;
      req.Prepay18CartDiscountPercent = (x.Prepay18CartDiscountPercent == undefined) ? 0 : Number(x.Prepay18CartDiscountPercent) / 100;
      req.IsPublicPriceLevel = x.PublicPriceLevel;
      req.IsAllPriceLevels = x.AllPriceLevels;
      req.PriceLevels = x.PriceLevels as PriceLevelOption[];

    });
    req.DynamicPriceForm = reqForm;
    req.DynamicPriceUpdatedValues = this.updatedValues;

    this.DynamicPricingDetailsService.Post(req).subscribe(response =>
    {
      this.snackbarService.showSuccess('Template saved.');
      this.form.DayOfWeekColAndRow.clear();
      const request = new GetDynamicPricingDetailsRequest();
      request.TemplateID = this.templateID;
      this.load(request);
      this.form.DayOfWeekColAndRow.markAsUntouched();
      this.form.DayOfWeekColAndRow.reset();
      this.setForm = false;
    }, error =>
    {
      this.setForm = false;
      let errorMessage = error.error.Message;
      if (errorMessage.indexOf("Start") != -1)
      {
        this.snackbarService.showError(errorMessage.substring(errorMessage.indexOf("Start"), errorMessage.indexOf("\r")));
      }
      else if (errorMessage.indexOf("Dates") != -1)
      {
        this.snackbarService.showError(errorMessage.substring(errorMessage.indexOf("Dates"), errorMessage.indexOf("\r")));
      }
      else if (errorMessage.indexOf("Price") != -1)
      {
        this.snackbarService.showError(errorMessage.substring(errorMessage.indexOf("Price"), errorMessage.indexOf(",")));
      }
      else
      {
        this.snackbarService.showError(errorMessage);
      }
    });
  }


  getDynamicPricingUpdatedValues(form: any)
  {
    Object.keys(form.controls).forEach(key =>
    {
      let currentControl = form.controls[key];
      if (currentControl.dirty)
      {
        if (currentControl.controls)
        {
          if (currentControl.controls.TimeGroupRangeDetails)
          {
            if (currentControl.controls.TimeGroupRangeDetails.dirty)
            {
              this.getDynamicPricingUpdatedValues(currentControl.controls.TimeGroupRangeDetails);
            }
          }
          else if (currentControl.controls.UtilRangeGroupList)
          {
            if (currentControl.controls.UtilRangeGroupList.dirty)
            {
              this.getDynamicPricingUpdatedValues(currentControl.controls.UtilRangeGroupList);
            }
          }
          else if (currentControl.controls.TimeGroupDetails)
          {
            if (currentControl.controls.TimeGroupDetails.dirty)
            {
              this.getDynamicPricingUpdatedValues(currentControl.controls.TimeGroupDetails);
            }
          }
          else
          {
            this.updatedValues.push(currentControl.getRawValue());
          }
        }
        else
        {
          this.updatedValues.push(currentControl.getRawValue());
        }
      }
    });
  }



  highlightcell(data: any)
  {
    let uncheckdayOfWeekColArray = this.form.get('DayOfWeekColAndRow') as UntypedFormArray;
    uncheckdayOfWeekColArray.controls.forEach(x =>
    {
      let uchecktimeGroupRange = x.get('TimeGroupRangeDetails') as UntypedFormArray;
      if (uchecktimeGroupRange.controls.length != 0)
      {
        uchecktimeGroupRange.controls.forEach(tr =>
        {
          let uncheckutilRange = tr.get('UtilRangeGroupList') as UntypedFormArray;
          uncheckutilRange.controls.forEach(ut =>
          {
            const unchecktimeGroupDetail = ut.get('TimeGroupDetails') as UntypedFormArray;
            unchecktimeGroupDetail.controls.forEach(tg =>
            {
              //if (data["Dayofweek"] != null && data["DaysOut"] != null)
              //{
              if (tg.get('PriceSelected').value == 1)
              {
                tg.get('PriceSelected').patchValue(0);
              }
              //}
            });
          });
        });
      }
    });


    this.highlightData = data;
    if (this.highlightData["Dayofweek"] && this.highlightData["DaysOut"] && this.highlightData["TimeGroup"] && this.highlightData["UtilizationRange"])
    {
      this.highlightData["Dayofweek"].forEach(w =>
      {
        this.highlightData["DaysOut"].forEach(o =>
        {
          this.highlightData["TimeGroup"].forEach(t =>
          {
            this.highlightData["UtilizationRange"].forEach(u =>
            {
              let dayOfWeekColArray = this.form.get('DayOfWeekColAndRow') as UntypedFormArray;
              dayOfWeekColArray.controls.forEach(x =>
              {
                let timeGroupRange = x.get('TimeGroupRangeDetails') as UntypedFormArray;
                if (timeGroupRange.controls.length != 0)
                {
                  timeGroupRange.controls.forEach(tr =>
                  {
                    if (tr.value.ColID == o && tr.value.RowID == w)
                    {
                      let utilRange = tr.get('UtilRangeGroupList') as UntypedFormArray;
                      utilRange.controls.forEach(ut =>
                      {
                        const timeGroupDetail = ut.get('TimeGroupDetails') as UntypedFormArray;
                        timeGroupDetail.controls.forEach(tg =>
                        {
                          if (tg.get('DayOfWeekRowID').value == w && tg.get('DayOfWeekColID').value == o && tg.get('TimeGroupID').value == t && tg.get('UtilizationRangeID').value == u)
                          {
                            tg.get('PriceSelected').patchValue(1);
                          }
                        });
                      });
                    }
                  });
                }
              });
            });
          });
        });
      });
    }
  }
}


