import { NgModule } from '@angular/core';
import { TranslateModule } from '@ngx-translate/core';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { DefaultLayoutModule } from '@tei/theme';
import { ConfirmationModule } from './confirmation/confirmation.module';
import { DynamicPricingDetailsModule } from './dynamic-pricing-details/dynamic-pricing-details.module';
import { DynamicPricingModule } from './dynamic-pricing/dynamic-pricing.module';
import { PagesRoutingModule } from './pages-routing.module';
import { PagesComponent } from './pages.component';
import { SettingsModule } from './settings/settings.module';
import { TeeTimeDetailsModule } from './tee-time-details/tee-time-details.module';
import { TeetimesModule } from './teetimes/teetimes.module';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';

const CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TeiDateTranslateModule, TranslateModule];
const MATERIAL_MODULES = [MatCardModule];

@NgModule({
  declarations: [PagesComponent],
  imports: [
    PagesRoutingModule,
    DefaultLayoutModule,
    TeetimesModule,
    TeeTimeDetailsModule,
    ConfirmationModule,
    DynamicPricingModule,
    DynamicPricingDetailsModule,
    SettingsModule,
    ...CORE_MODULES,
    ...MATERIAL_MODULES
    //ServicesModule
  ],
  providers: []
})
export class PagesModule { }
