import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IDynamicPricingService, GetDynamicPricingRequest, GetDynamicPricingResponse, PostDynamicPricingRequest, PostDynamicPricingResponse } from '../@intermediate/dynamic-pricing';
import { DeleteDynamicPricingRequest, DeleteDynamicPricingResponse } from '../@intermediate/dynamic-pricing/models/delete-dynamic-pricing.model';
import { DuplicateDynamicPricingRequest, DuplicateDynamicPricingResponse } from '../@intermediate/dynamic-pricing/models/duplicate-dynamic-pricing.model';

@Injectable({
  providedIn: 'root'
})

export class DynamicPricingService implements IDynamicPricingService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Booking/TeeTimes/DynamicPricing';
  }

  public Get(request: GetDynamicPricingRequest): Observable<GetDynamicPricingResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetDynamicPricingResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostDynamicPricingRequest): Observable<PostDynamicPricingResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostDynamicPricingResponse>(apiUrl, request);
  }

  public DeleteDynamicPricing(request: DeleteDynamicPricingRequest): Observable<DeleteDynamicPricingResponse>
  {
    const apiurl = this.serviceUrl + '/DeleteDynamicPricing';
    return this.http.post<DeleteDynamicPricingResponse>(apiurl,request);
  }

  public DuplicateDynamicPricing(request: DuplicateDynamicPricingRequest): Observable<DuplicateDynamicPricingResponse>
  {
    const apiurl = this.serviceUrl + '/DuplicateDynamicPricing';
    return this.http.post<DuplicateDynamicPricingResponse>(apiurl, request);
  }
}
