import { DatePipe } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatRippleModule } from '@angular/material/core';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { ViewCourseDetailsModule } from '@tei/booking-tee-times-shared';
import { ContactGroupsModule } from '@tei/common-contact-lookup-shared';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { CMSSimpleAlarmBoxModule } from '@tei/marketing-cms';
import { CountDownTimerModule, LoadingModule, SnackBarModule, TimePickerModule } from '@tei/material';
import { MaterialGenericFilterModule } from '@tei/material-generic-filter';
import { NgxMatTimepickerModule } from 'ngx-mat-timepicker';
import { TeeTimeDetailsService } from '../../@services/tee-time-details.service';
import { TeetimesService } from '../../@services/teetimes.service';
import { ManageDialogModule } from '../../components/manage-dialog/manage-dialog.module';
import { TeeTimeClubModule } from '../../components/tee-time-club/tee-time-club.module';
import { TeeTimeFilterModule } from '../../components/tee-time-filter/tee-time-filter.module';
import { SearchPipe } from './search.pipe';
import { TeetimesComponent } from './teetimes.component';
const CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TranslateModule, TeiDateTranslateModule];
const BASE_MODULES = [FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatCardModule, MatFormFieldModule,
  MatInputModule, MatSelectModule, MatDividerModule,
  MatDialogModule, MatButtonModule, MatRippleModule,
  MatTooltipModule, MatDatepickerModule, MatCheckboxModule,
  MatMenuModule, MatChipsModule, MatRadioModule];
const TEI_MATERIAL_MODULES = [ViewCourseDetailsModule, SnackBarModule, TimePickerModule, LoadingModule, CMSSimpleAlarmBoxModule];
const MODULES = [MaterialGenericFilterModule, ManageDialogModule, TeeTimeFilterModule, ContactGroupsModule, CountDownTimerModule, TeeTimeClubModule];
const THIRD_PARTY_MODULES = [NgxMatTimepickerModule.setLocale('en-US')];

@NgModule({
  declarations: [TeetimesComponent, SearchPipe],
  imports: [
    RouterModule.forChild([{ path: '', component: TeetimesComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...THIRD_PARTY_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...MODULES,
    ...CORE_MODULES
  ],
  providers: [{ provide: 'ITeetimesService', useClass: TeetimesService }, { provide: 'ITeeTimeDetailsService', useClass: TeeTimeDetailsService },
    DatePipe]
})
export class TeetimesModule { }
