import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatNativeDateModule } from '@angular/material/core';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyListModule as MatListModule } from '@angular/material/legacy-list';
import { MatSidenavModule } from '@angular/material/sidenav';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { DynamicPricingDetailTemplateService } from '../../@services/dynamic-pricing-detail-template.service';
import { DynamicPricingDetailTemplateComponent } from './dynamic-pricing-detail-template.component';
import { TwodigitDirective } from './twodigit.directive';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';

const CORE_MODULES = [TeiTextTranslatePipeModule, TeiDateTranslateModule, TeiCurrencyTranslateModule];


const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MatInputModule, MatFormFieldModule, MatDatepickerModule, MatNativeDateModule, MatCardModule, MatButtonToggleModule, MatButtonModule,
  MatIconModule, MatDialogModule, MatSidenavModule, MatExpansionModule, MatCheckboxModule, MatSlideToggleModule, MatListModule];
const TEI_MATERIAL_MODULES = [];

const COMPONENTS = [DynamicPricingDetailTemplateComponent];

@NgModule({
  declarations: [...COMPONENTS, TwodigitDirective],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'IDynamicPricingDetailTemplateService', useClass: DynamicPricingDetailTemplateService }],
})
export class DynamicPricingDetailTemplateModule { }
