import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetDynamicPricingDetailTemplateRequest, GetDynamicPricingDetailTemplateResponse, IDynamicPricingDetailTemplateService, PostDynamicPricingDetailTemplateRequest, PostDynamicPricingDetailTemplateResponse } from '../@intermediate/dynamic-pricing-detail-template';

@Injectable({
  providedIn: 'root'
})

export class DynamicPricingDetailTemplateService implements IDynamicPricingDetailTemplateService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Booking/TeeTimes/DynamicPricingDetailTemplate';
  }

  public Get(request: GetDynamicPricingDetailTemplateRequest): Observable<GetDynamicPricingDetailTemplateResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetDynamicPricingDetailTemplateResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostDynamicPricingDetailTemplateRequest): Observable<PostDynamicPricingDetailTemplateResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostDynamicPricingDetailTemplateResponse>(apiUrl, request);
  }
}
