import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatSidenavModule } from '@angular/material/sidenav';
import { ConfirmDialogModule, SnackBarModule } from '@tei/material';
import { DynamicPricingWizardService } from '../../@services/dynamic-pricing-wizard.service';
import { DynamicPricingWizardComponent } from './dynamic-pricing-wizard.component';
import { TwoDigitDecimalDirective } from './two-digit-decimal.directive';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';

const CORE_MODULES = [TeiTextTranslatePipeModule];
const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatSidenavModule, MatCheckboxModule, MatDividerModule, MatRadioModule, MatInputModule, MatButtonModule, MatIconModule];
const TEI_MATERIAL_MODULES = [SnackBarModule, ConfirmDialogModule];

const COMPONENTS = [DynamicPricingWizardComponent];

@NgModule({
  declarations: [...COMPONENTS, TwoDigitDecimalDirective],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'IDynamicPricingWizardService', useClass: DynamicPricingWizardService }],
})
export class DynamicPricingWizardModule { }
