import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IDynamicPricingDetailsService, GetDynamicPricingDetailsRequest, GetDynamicPricingDetailsResponse, PostDynamicPricingDetailsRequest, PostDynamicPricingDetailsResponse } from '../@intermediate/dynamic-pricing-details';

@Injectable({
  providedIn: 'root'
})

export class DynamicPricingDetailsService implements IDynamicPricingDetailsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Booking/TeeTimes/DynamicPricingDetails';
  }

  public Get(request: GetDynamicPricingDetailsRequest): Observable<GetDynamicPricingDetailsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetDynamicPricingDetailsResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostDynamicPricingDetailsRequest): Observable<PostDynamicPricingDetailsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostDynamicPricingDetailsResponse>(apiUrl, request);
  }
}
