import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { Component, Inject, OnInit, SkipSelf } from '@angular/core';
import { UntypedFormArray } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
import { TimeGroup } from '../../../lib/@intermediate/dynamic-pricing-details';
import { GetDynamicTimeGroupsRequest, GetDynamicTimeGroupsResponse, IDynamicTimeGroupsService, PostDynamicTimeGroupsRequest, PostDynamicTimeGroupsRequestForm, timeValidator, valueChanged } from '../../@intermediate/dynamic-time-groups';


@Component({
  selector: 'tei-tee-times-dialog-dynamic-time-groups',
  templateUrl: './dynamic-time-groups.component.html',
  styleUrls: ['./dynamic-time-groups.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(DynamicTimeGroupsComponent)
  ]
})
export class DynamicTimeGroupsComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{
  model: GetDynamicTimeGroupsResponse;
  form: PostDynamicTimeGroupsRequestForm;
  addTimeDisabled: boolean = false;
  maxRange: number = 10;

  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('IDynamicTimeGroupsService') private DynamicTimeGroupsService: IDynamicTimeGroupsService,
    public dialog: MatDialogRef<DynamicTimeGroupsComponent>, @Inject(MAT_DIALOG_DATA) public data: any, private snackbarService: SnackBarService,
    private confirmdialogService: ConfirmDialogService)
  {
    super(_errorStateMatcher);
  }

  ngOnInit()
  {
    this.form = new PostDynamicTimeGroupsRequestForm(undefined);
    const request = new GetDynamicTimeGroupsRequest();
    request.CourseTemplateUID = this.data.CourseTemplateUID;
    this.form.valueChanges.subscribe(value =>
    {
      var tindex = 1;
      value.TimeGroupFormArray.forEach((x, i) =>
      {
        if (x.IsDeleted == false)
        {
          x.SortOrder = tindex++;
        }
      });
      valueChanged(false);

    });

    this.load(request);
  }

  private load(request: GetDynamicTimeGroupsRequest)
  {
    if (!this.model)
    {
      this.model = new GetDynamicTimeGroupsResponse();
    }

    this.DynamicTimeGroupsService.Get(request).subscribe(response =>
    {
      this.model = response;
      if (this.model.TimeGroups.filter(t => t.TimeGroupID != "" && t.TimeGroupID != null).length >= 10)
      {
        this.addTimeDisabled = true;
      }

      this.form.setValue(this.model);
      valueChanged(true);
    });
  }

  getUpdatedValues(form: any)
  {
    const req = new PostDynamicTimeGroupsRequest();
    let TimeGroupRanges: TimeGroup[] = [];
    this.form.TimeGroupFormArray.controls.forEach(x =>
    {
      if (x.dirty || (x.value["TimeGroupID"] != "" && x.value["IsDeleted"] === true))
      {
        x.value["IsControlUpdated"] = true;
        TimeGroupRanges.push(x.value);
      }

      // for drag and drop only
      if (x.value["TimeGroupID"] != "" && x.value["IsControlUpdated"] == false)
      {
        this.model.TimeGroups.forEach(element =>
        {
          if (x.value["SortOrder"] != element.SortOrder && x.value["TimeGroupID"] === element.TimeGroupID)
          {
            x.value["IsControlUpdated"] = true;
            TimeGroupRanges.push(x.value);
          }
        });
      }
    });

    //insertion
    req.TimeGroupFormArray = TimeGroupRanges.filter(x => (x.IsDeleted == false && x.TimeGroupID == "") || (x.TimeGroupID != ""));
    req.TimeGroupFormArray.forEach(f =>
    {
      if (f.TimeGroupID != "")
      {
        this.model.TimeGroups.forEach(element =>
        {
          if ((f.TimeGroupID === element.TimeGroupID && f.IsControlUpdated === false)
            &&
            (f.GroupName != element.GroupName
              || f.StartTime != element.StartTime
              || f.EndTime != element.EndTime
            )
          )
          {
            f.IsControlUpdated = true;
          }
        });
      }
    });
    req.TimeGroupFormArray = req.TimeGroupFormArray.filter(f => f.IsControlUpdated == true);
    return req.TimeGroupFormArray;
  }

  addTimeGroup(): void
  {
    const timeGroupArray = this.form.get('TimeGroupFormArray') as UntypedFormArray;
    const timeGroupProperty = new TimeGroup();
    const len = timeGroupArray.controls.filter(t => t.value.IsDeleted == false).length;
    if (len === this.maxRange - 1)
    {
      this.addTimeDisabled = true;
    }
    timeGroupProperty.IsDeleted = false;
    timeGroupProperty.GroupName = "";
    timeGroupProperty.StartTime = "";
    timeGroupProperty.EndTime = "";
    timeGroupProperty.IsControlUpdated = true;
    const maxIndex = timeGroupArray.value.filter(t => t.IsDeleted == false).length;
    timeGroupProperty.SortOrder = maxIndex + 1;
    timeGroupProperty.TimeGroupID = "";

    timeGroupArray.setValue([timeGroupProperty]);
  }

  onCancel(): void
  {
    this.dialog.close('canceled');
  }

  removeTime(removeIsOptionIndex: number)
  {
    const timeGroupArray = this.form.get('TimeGroupFormArray') as UntypedFormArray;
    const deleteMsg = 'Are you sure you want to delete this time group?';
    const dialogData: DialogData = {
      title: 'Delete',
      message: deleteMsg,
      textOkayButton: 'Delete',
      textCancelButton: 'Cancel'
    };
    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        timeGroupArray.controls.forEach((x, ti) =>
        {
          if (ti == removeIsOptionIndex)
          {
            x.value.IsDeleted = true;
            x.value.IsControlUpdated = true;
            this.form.TimeGroupFormArray.controls[ti]["controls"].IsDeleted.value = true;
            timeGroupArray.controls[ti]["controls"].GroupName.status = "VALID";
            timeGroupArray.controls[ti]["controls"].StartTime.status = "VALID";
            timeGroupArray.controls[ti]["controls"].EndTime.status = "VALID";
            timeGroupArray.controls[ti].setErrors(null);
            return;
          }
        });

        timeGroupArray.controls.forEach((y, i) =>
        {
          if (i >= removeIsOptionIndex && (y.value.IsDeleted == false))
          {
            y.value.SortOrder = (y.value.SortOrder) - 1;
          }
        });

        if (this.form.TimeGroupFormArray.status === "INVALID")
        {
          this.form.TimeGroupFormArray.controls.forEach(x =>
          {
            if (x.status === "INVALID")
            {
              timeValidator(x["controls"].StartTime);
            }
          });
        }

        const len = timeGroupArray.value.filter(t => t.IsDeleted == false).length;
        this.addTimeDisabled = (len == this.maxRange) ? true : false;
      }
    });
  }

  drop(event: CdkDragDrop<string[]>)
  {
    const timeGroupArray = this.form.get('TimeGroupFormArray') as UntypedFormArray;
    moveItemInArray(timeGroupArray.controls, event.previousIndex, event.currentIndex);
    var index = 1;
    timeGroupArray.controls.forEach((x, i) =>
    {
      if (x.value.IsDeleted == false)
      {
        timeGroupArray.controls[i].patchValue(["SortOrder", index++]);
      }
    });
  }

  save(form: PostDynamicTimeGroupsRequestForm)
  {
    const req = new PostDynamicTimeGroupsRequest();
    req.TimeGroupFormArray = this.getUpdatedValues(form);
    req.CourseTemplateUID = this.data.CourseTemplateUID;
    if (req.TimeGroupFormArray.length > 0)
    {
      this.DynamicTimeGroupsService.Post(req).subscribe(response =>
      {
        this.dialog.close('saved');
        this.snackbarService.showSuccess('Template saved.');
      }, error =>
      {
        let errorMessage = error.error.Message;
        let a = errorMessage.indexOf("Start");
        let b = errorMessage.indexOf("\r");
        this.snackbarService.showError(errorMessage.substring(a, b));
      });
    }
    else
    {
      this.dialog.close('canceled');
    }
  }



}
