import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatLegacySliderModule as MatSliderModule } from '@angular/material/legacy-slider';
import { SafeHtmlPipeModule, TeiCurrencyTranslateModule } from '@tei/core-translate';
import { TeeTimeFilterService } from '../../@services/tee-time-filter.service';
import { TeeTimeFilterComponent } from './tee-time-filter.component';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { TranslateModule } from '@ngx-translate/core';

const CORE_MODULES = [TeiTextTranslatePipeModule, TranslateModule, TeiCurrencyTranslateModule];

const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule, SafeHtmlPipeModule];
const MATERIAL_MODULES = [MatFormFieldModule, MatChipsModule, MatMenuModule, MatRadioModule, MatIconModule, MatButtonModule, MatSliderModule];
const TEI_MATERIAL_MODULES = [];

const COMPONENTS = [TeeTimeFilterComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'ITeeTimeFilterService', useClass: TeeTimeFilterService }],
})
export class TeeTimeFilterModule { }
