import { LocationStrategy } from '@angular/common';
import { Component, HostListener, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { GetConfirmationRequest, GetConfirmationResponse, IConfirmationService, PostConfirmationRequestForm } from '../../@intermediate/confirmation';

@Component({
  selector: 'tei-tee-times-pages-confirmation',
  templateUrl: './confirmation.component.html',
  styleUrls: ['./confirmation.component.scss']
})
export class ConfirmationComponent implements OnInit
{
  model: GetConfirmationResponse;
  form: PostConfirmationRequestForm;
  teetimeId: string;
  moveReservation: boolean = false;
  moveteetimeId: string;

  constructor(@Inject('IConfirmationService') private ConfirmationService: IConfirmationService,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private locationStrategy: LocationStrategy)
  {
    if (this.activatedRoute.snapshot.params['teetimeId'])
    {
      this.teetimeId = this.activatedRoute.snapshot.params['teetimeId'];
    }
    if (this.activatedRoute.snapshot.params['moveReservation'])
    {
      this.moveReservation = this.activatedRoute.snapshot.params['moveReservation'];
    }
    this.preventBackButton();
  }

  @HostListener('window:popstate', ['$event'])
  onBrowserBackBtnClose($event)
  {
    this.preventBackButton();
  }


  preventBackButton()
  {
    history.pushState(null, null, location.href);
    this.locationStrategy.onPopState(() =>
    {
      history.pushState(null, null, location.href);
    });
  }

  ngOnInit()
  {
    this.form = new PostConfirmationRequestForm();
    const request = new GetConfirmationRequest();
    request.TeeTimeID = this.teetimeId;
    request.IsMoveReservation = this.moveReservation;
    request.MoveTeeTimeID = this.activatedRoute.snapshot.queryParamMap.get('moid');
    this.load(request);
  }

  private load(request: GetConfirmationRequest)
  {
    if (!this.model)
    {
      this.model = new GetConfirmationResponse();
    }

    this.ConfirmationService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.form.setValue(response);
    });
  }

  save(form: PostConfirmationRequestForm)
  {
    this.ConfirmationService.Post(form.value).subscribe(response =>
    {

    });
  }

  get needToCollapse()
  {
    const ret = window.innerWidth >= 800 && window.innerHeight >= 600;
    return ret;
  }

  GoBack()
  {
    this.router.navigate(['../../..'], { relativeTo: this.activatedRoute });
  }
}
