import { Component, Inject, OnInit, SkipSelf } from '@angular/core';
import { ErrorStateMatcher } from '@angular/material/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders, ResponsiveService } from '@tei/core';
import { ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
import { CartOption, GetTeetimesRequest, HoleOption, TeeTimeData } from '../../../lib/@intermediate/teetimes';
import { CheckAttachmentsBeforeMoveDataServiceRequest, CheckAttachmentsBeforeMoveDataServiceResponse, FieldOption1, GetManageDialogRequest, GetManageDialogResponse, IManageDialogService, PostManageDialogRequest, PostManageDialogRequestForm, TeeTimeDialog } from '../../@intermediate/manage-dialog';


@Component({
  selector: 'tei-tee-times-dialog-manage-dialog',
  templateUrl: './manage-dialog.component.html',
  styleUrls: ['./manage-dialog.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(ManageDialogComponent)
  ]
})
export class ManageDialogComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{
  model: GetManageDialogResponse;
  form: PostManageDialogRequestForm;
  fields: TeeTimeData;
  teetimedialogdata: TeeTimeDialog;
  fieldOptionPlayers: FieldOption1[];
  fieldOptionHoles: HoleOption[];
  fieldOptionCart: CartOption[];
  cartIncluded: boolean = false;
  holeDisabled: boolean = false;
  holes: string;
  selectedPlayer: string;
  lastFilter: GetTeetimesRequest;
  minNumberOfPlayers: number;
  maxNumberOfPlayers: number;
  selectionHeaderUID: string;
  groupsFilter: boolean = false;
  moveReservation: boolean = false;
  moveTeeTimeID: string;
  searchGroupEnabled: boolean = false;
  teetimeDialogTeetimeId: string;
  isLoading: boolean;
  displayCartFee: boolean = true;
  displayGreenFee: boolean = true;
  displaySubtotal: boolean = true;
  continueDisable: boolean = false;
  requireJoinGroup = false;
  maxNumberOfPlayer = 0;
  isMobileMode = false;
  activatedRoute: ActivatedRoute;

  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('IManageDialogService') private ManageDialogService: IManageDialogService,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialog: MatDialogRef<ManageDialogComponent>,
    private snackbarService: SnackBarService,
    private teiTranslateService: TranslateService,
    private confirmdialogService: ConfirmDialogService,
    public responsiveService: ResponsiveService,
    private router: Router)
  {
    super(_errorStateMatcher);
    //this.lastFilter = data.lastFilter;
    this.displayCartFee = data.displayCartFee;
    this.displayGreenFee = data.displayGreenFee;
    this.displaySubtotal = data.displaySubtotal;
    this.requireJoinGroup = data.requireJoinGroup;
    this.activatedRoute = data.activatedRoute;
  }

  ngOnInit()
  {
    this.form = new PostManageDialogRequestForm(this.cartIncluded, this.holeDisabled, this.groupsFilter);
    const request = new GetManageDialogRequest();
    this.fields = (this.data) ? this.data.filterdata : this.fields;
    this.holes = this.data ? this.data.holes : "";
    this.selectedPlayer = this.data.selectedPlayer;
    this.fieldOptionCart = this.data.cartOptions;
    this.fieldOptionHoles = this.data.holeOptions;
    this.minNumberOfPlayers = this.data.minNumberOfPlayers;
    this.maxNumberOfPlayers = this.data.maxNumberOfPlayers;
    this.groupsFilter = this.data.groupsFilter;
    this.selectionHeaderUID = this.data.selectionHeaderUID;
    this.moveReservation = this.data.moveReservation;
    this.moveTeeTimeID = this.data.moveTeeTimeID;
    this.searchGroupEnabled = this.data.searchGroupEnabled;
    this.maxNumberOfPlayer = this.data?.maxNumberOfPlayer;
    this.dialogTeeTimeData(this.fields, this.holes, this.selectedPlayer, this.fieldOptionCart, this.fieldOptionHoles);
    this.isMobileMode = ['xs', 'sm'].includes(this.responsiveService.breakPoint);
  }

  onNoClick(): void
  {
    this.dialog.close();
  }

  dialogTeeTimeData(teeTimeData: TeeTimeData, holes: string, selectedPlayer: string, cartOptions: CartOption[], fieldOptionHoles: HoleOption[])
  {
    if (!this.model)
    {
      this.model = new GetManageDialogResponse();
    }

    this.teetimedialogdata = new TeeTimeDialog();
    if (this.searchGroupEnabled)
    {
      this.teetimedialogdata.DialogPlayers = this.getPlayers(Number(selectedPlayer), this.maxNumberOfPlayers, teeTimeData.NumberofPlayers, this.groupsFilter, selectedPlayer);
    }
    else
    {
      this.teetimedialogdata.DialogPlayers = this.getPlayers(this.minNumberOfPlayers, this.maxNumberOfPlayers, teeTimeData.NumberofPlayers, this.groupsFilter, selectedPlayer);
    }
    if (this.searchGroupEnabled)
    {
      this.minNumberOfPlayers = Number(selectedPlayer);
    }
    else
    {
      let selectPlayer = this.teetimedialogdata.DialogPlayers.find(x => x.value === (selectedPlayer.toString()));
      {
        if (selectPlayer)
        {
          this.minNumberOfPlayers = Number(selectPlayer.value);
        }
      }
    }

    this.teetimedialogdata.Title = teeTimeData.Title;
    this.teetimedialogdata.SubTitle = teeTimeData.SubTitle;
    this.teetimedialogdata.IsDisplayTime = teeTimeData.IsDisplayTime;
    this.teetimedialogdata.TeeTimeDisplay = teeTimeData.TeeTimeDisplay;
    this.teetimedialogdata.CourseName = teeTimeData.CourseName;
    this.teetimedialogdata.DisplayTime = teeTimeData.DisplayTime;

    this.teetimedialogdata.DialogHoles = this.getHoles(teeTimeData, holes, fieldOptionHoles);
    this.teetimedialogdata.Cart = cartOptions;
    let player = this.minNumberOfPlayers;  //this.teetimedialogdata.DialogPlayers.filter(x => x.isSelected === true)[0].value;
    const hole = this.teetimedialogdata.DialogHoles.filter(x => x.IsSelected === true)[0].Value;
    this.teetimedialogdata.GreenFee = this.getGreenFee(Number(player), hole);
    const selectedCart = this.teetimedialogdata.Cart.filter(x => x.IsSelected == true || x.IsDefalut == true)[0];
    this.teetimedialogdata.CartFee = this.getCartFee(selectedCart ? selectedCart.Value : null, Number(player), hole);
    this.teetimedialogdata.SubTotal = this.teetimedialogdata.GreenFee + this.teetimedialogdata.CartFee;
    this.teetimedialogdata.CourseID = teeTimeData.CourseID;
    this.teetimedialogdata.Time = teeTimeData.Time;
    this.teetimedialogdata.Date = teeTimeData.TTDate;
    this.teetimedialogdata.RequiredGolfCart = teeTimeData.RequireGolfCart;
    this.teetimedialogdata.StartTee = teeTimeData.StartTee;
    this.teetimedialogdata.CourseName = teeTimeData.SubTitle;
    this.teetimedialogdata.TeeTimeDisplay = teeTimeData.TTDate + " " + teeTimeData.TeeTimeDisplay;
    this.teetimedialogdata.PaymentType = teeTimeData.IsDueLater ? 0 : teeTimeData.IsPrePaidAndDueLater ? 2 : 1;
    this.model.TeeTimeDialog = this.teetimedialogdata;


    if (this.model)
    {
      this.holeDisabled = (!teeTimeData.Allow18 || !teeTimeData.Allow9) ? true : false;
      if (this.moveReservation)
      {
        this.form = new PostManageDialogRequestForm(this.moveReservation, this.holeDisabled, this.groupsFilter);
      }
      else
      {
        this.form = new PostManageDialogRequestForm(teeTimeData.IsCartIncluded, this.holeDisabled, this.groupsFilter);
      }
      if (!this.searchGroupEnabled)
      {
        var minPlayerIndex = this.model.TeeTimeDialog.DialogPlayers.findIndex(x => x.value == this.minNumberOfPlayers.toString());
        this.model.TeeTimeDialog.DialogPlayers[minPlayerIndex].isSelected = true;
      }
      else
      {
        var minPlayerIndex = this.model.TeeTimeDialog.DialogPlayers.findIndex(x => x.value == selectedPlayer.toString());
        this.model.TeeTimeDialog.DialogPlayers[minPlayerIndex].isSelected = true;
      }

      this.form.setValue(this.model);
    }
  }

  changeCart(event: any)
  {
    const numberOfPlayer = Number(this.form.get("Player").value);
    const hole = this.form.get("Holes").value;
    this.teetimedialogdata.GreenFee = this.getGreenFee(numberOfPlayer, hole);
    this.teetimedialogdata.CartFee = this.getCartFee(event.value, numberOfPlayer, hole);
    this.teetimedialogdata.SubTotal = this.teetimedialogdata.GreenFee + this.teetimedialogdata.CartFee;
  }

  changePlayer(event: any)
  {
    const cart = this.form.get("Cart").value;
    const numberOfPlayer = event.value;
    const hole = this.form.get("Holes").value;
    this.teetimedialogdata.GreenFee = this.getGreenFee(numberOfPlayer, hole);
    this.teetimedialogdata.CartFee = this.getCartFee(cart, numberOfPlayer, hole);
    this.teetimedialogdata.SubTotal = this.teetimedialogdata.GreenFee + this.teetimedialogdata.CartFee;
  }

  changeHole(event: any)
  {

    const numberOfPlayer = Number(this.form.get("Player").value);
    const cart = this.form.get("Cart").value;
    const hole = event.value;
    this.teetimedialogdata.GreenFee = this.getGreenFee(numberOfPlayer, hole);
    this.teetimedialogdata.CartFee = this.getCartFee(cart, numberOfPlayer, hole);
    this.teetimedialogdata.SubTotal = this.teetimedialogdata.GreenFee + this.teetimedialogdata.CartFee;
  }

  getGreenFee(player: number, hole: string)
  {


    this.teetimedialogdata.GreenFee = 0;

    if (hole === "18")
    {
      this.teetimedialogdata.GreenFee = this.fields.IsPrePaid ? this.fields.PrePayGolfPrice18 : this.fields.GolfPrice18;
    }
    else
    {
      this.teetimedialogdata.GreenFee = this.fields.IsPrePaid ? this.fields.PrePayGolfPrice9 : this.fields.GolfPrice9;
    }

    return (this.teetimedialogdata.GreenFee * Number(player));
  }

  getCartFee(cart: string, player: number, hole: string)
  {

    this.teetimedialogdata.CartFee = 0;

    if (hole === "18")
    {
      if (cart === "PowerCart")
      {
        this.teetimedialogdata.CartFee = (this.fields.IsPrePaid || this.fields?.IsPrePaidAndDueLater) ? this.fields.PrePayGolfPowerCartPrice18 : this.fields.GolfPowerCartPrice18;
      }
      if (cart === "PullCart")
      {
        this.teetimedialogdata.CartFee = (this.fields.IsPrePaid || this.fields?.IsPrePaidAndDueLater) ? this.fields.PrePayGolfPullCartPrice18 : this.fields.GolfPullCartPrice18;
      }
    }
    else
    {
      if (cart === "PowerCart")
      {
        this.teetimedialogdata.CartFee = (this.fields.IsPrePaid || this.fields?.IsPrePaidAndDueLater) ? this.fields.PrePayGolfPowerCartPrice9 : this.fields.GolfPowerCartPrice9;
      }
      if (cart === "PullCart")
      {
        this.teetimedialogdata.CartFee = (this.fields.IsPrePaid || this.fields?.IsPrePaidAndDueLater) ? this.fields.PrePayGolfPullCartPrice9 : this.fields.GolfPullCartPrice9;
      }
    }
    return (this.teetimedialogdata.CartFee * Number(player));
  }


  getHoles(teeTimeDate: TeeTimeData, holes: string, fieldOptionHoles): HoleOption[]
  {

    const allow18Hole: boolean = teeTimeDate.Allow18;
    const allow9Hole: boolean = teeTimeDate.Allow9;

    if (holes === "18")
    {
      this.fieldOptionHoles.forEach(x =>
      {
        if (x.Value == "18")
        {
          x.IsSelected = true;
        }
      });
    }
    if (holes === "9")
    {
      this.fieldOptionHoles.forEach(x =>
      {
        if (x.Value == "9")
        {
          x.IsSelected = true;
        }
      });
    }

    if (holes === "Any")
    {
      if (allow18Hole)
      {
        this.fieldOptionHoles.forEach(x =>
        {
          if (x.Value == "18")
          {
            x.IsSelected = true;
          }
        });
      }
      else
      {
        this.fieldOptionHoles.forEach(x =>
        {
          if (x.Value == "9")
          {
            x.IsSelected = true;
          }
        });
      }
    }

    return this.fieldOptionHoles;
  }

  getPlayers(minPlayer: number, maxPlayer: number, numberOfPlayers: number, groupsFilter: boolean, selectedPlayer: string): FieldOption1[]
  {
    minPlayer--;

    if (maxPlayer > numberOfPlayers)
    {
      maxPlayer = numberOfPlayers;
    }
    if (this.requireJoinGroup && numberOfPlayers == this.maxNumberOfPlayer && !groupsFilter && minPlayer === 0)
    {
      minPlayer++;
    }

    this.fieldOptionPlayers = [];
    const selectedPlayerNum = Number(selectedPlayer);
    const isDefaultSelected = selectedPlayerNum === -1;

    for (let index = minPlayer; index < maxPlayer; index++)
    {
      const playerCount = index + 1;
      const displayText = playerCount + this.teiTranslateService.instant(
        playerCount == 1 ? 'TEE_TIME.MANAGE__DIALOG.PLAYER' : 'TEE_TIME.MANAGE__DIALOG.PLAYERS'
      );

      this.fieldOptionPlayers.push({
        display: displayText,
        value: playerCount.toString(),
        isSelected: isDefaultSelected ? playerCount === 1 : playerCount === selectedPlayerNum,
      });


    }
    return this.fieldOptionPlayers;
  }
  private load(request: GetManageDialogRequest)
  {
    if (!this.model)
    {
      this.model = new GetManageDialogResponse();
    }

    this.ManageDialogService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.teetimedialogdata = response.TeeTimeDialog[0];
      if (this.model)
      {
        this.form.setValue(this.model);
      }
    });
  }

  save(form: PostManageDialogRequestForm)
  {
    if (!this.continueDisable)
    {
      this.continueDisable = true;
      localStorage.removeItem('previousPage');
      let req = form.getRawValue() as PostManageDialogRequest;
      req.PaymentType = this.teetimedialogdata.PaymentType;
      req.TeeTimeID = this.fields.TeeTimeID;
      req.MoveTeeTimeID = this.moveTeeTimeID;
      req.IsMoveReservation = this.moveReservation;
      localStorage.setItem("extraUrlParams", JSON.stringify(this.activatedRoute.snapshot.queryParams));
      this.isLoading = true;
      if (this.fields.TeeTimeID != null)
      {
        this.ManageDialogService.Post(req).subscribe(response =>
        {
          this.turnOffProgressBar();
          this.dialog.close({ error: "" });
          if (this.moveReservation)
          {
            this.SaveAnonymousTeeTimeInLocalStroage(this.fields.TeeTimeID, response.AnonymousContactID);
            this.checkMoveAttcachments(this.moveTeeTimeID);
          }
          else
          {
            this.router.navigate(['teetime-details', this.fields.TeeTimeID, false],
              {
                queryParams: { shuid: this.selectionHeaderUID }, replaceUrl: true, relativeTo: this.activatedRoute
              });
          }
        }, error =>
        {
          this.turnOffProgressBar();
        });
      }
      else
      {
        this.ManageDialogService.Post(req).subscribe(response =>
        {
          this.turnOffProgressBar();
          if (response["TeeTimeDialog"].Error !== "")
          {

            this.dialog.close({ error: "error" });
            this.snackbarService.showError(response["TeeTimeDialog"].Error);
          }
          else
          {
            this.dialog.close({ error: "" });
            this.SaveAnonymousTeeTimeInLocalStroage(response["TeeTimeDialog"].TeeTimeID, response.AnonymousContactID);
            if (this.moveReservation)
            {
              this.teetimeDialogTeetimeId = response["TeeTimeDialog"].TeeTimeID;
              this.checkMoveAttcachments(this.moveTeeTimeID);
            }
            else
            {
              this.router.navigate(['teetime-details', response["TeeTimeDialog"].TeeTimeID, false], { queryParams: { shuid: this.selectionHeaderUID }, replaceUrl: true, relativeTo: this.activatedRoute });
            }
          }
        }, error =>
        {
          this.turnOffProgressBar();
        });
      }
    }
  }

  private SaveAnonymousTeeTimeInLocalStroage(TeetimeID: string, contactID: string)
  {
    var anonymousTeeTime = {
      "AnonymousTeeTimeID": TeetimeID,
      "AnonymousContactID": contactID
    };
    localStorage.setItem(
      "AnonymousTeeTime", JSON.stringify(anonymousTeeTime)
    );
  }

  checkMoveAttcachments(teeTimeID: string)
  {
    const attreq = new CheckAttachmentsBeforeMoveDataServiceRequest();
    attreq.MoveToCourseID = this.teetimedialogdata.CourseID;
    attreq.MoveTeeTimeID = teeTimeID;
    this.ManageDialogService.GetAttachmentsChecked(attreq).subscribe(response =>
    {
      if (response.StatusMsg != "" && response.Warning != "")
      {
        this.attachmentsDialog(response);
      }
      else
      {
        this.navigateTeetimedaetailPage();
      }
    });
  }

  turnOffProgressBar()
  {
    this.isLoading = false;
  }


  attachmentsDialog(status: CheckAttachmentsBeforeMoveDataServiceResponse)
  {
    const dialogData: DialogData = {
      title: (status?.Warning) ? 'Warning' : '',
      message: status?.StatusMsg,
      textOkayButton: 'Yes',
      textCancelButton: 'No'
    };

    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.navigateTeetimedaetailPage();
      }
    });

  }

  navigateTeetimedaetailPage()
  {
    if (this.fields.TeeTimeID != null)
    {
      this.router.navigate(['../../../..', 'teetime-details', this.fields.TeeTimeID, false],
        {
          queryParams:
          {
            shuid: this.selectionHeaderUID, moveTeeTimeID: this.moveTeeTimeID
          }
          , replaceUrl: true
          , relativeTo: this.activatedRoute
        });
    }
    else
    {
      this.router.navigate(['../../../..', 'teetime-details', this.teetimeDialogTeetimeId, false],
        {
          queryParams: {
            shuid: this.selectionHeaderUID, moveTeeTimeID: this.moveTeeTimeID
          }, replaceUrl: true, relativeTo: this.activatedRoute
        });
    }
  }
}
