import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Inject, OnInit, SkipSelf } from '@angular/core';
import { UntypedFormArray, UntypedFormGroup } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { ActivatedRoute } from '@angular/router';
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { GetDynamicPricingDetailTemplateRequest, GetDynamicPricingDetailTemplateResponse, IDynamicPricingDetailTemplateService, PostDynamicPricingDetailTemplateRequestForm, PriceLevelOption } from '../../@intermediate/dynamic-pricing-detail-template';
import { MatLegacySelectionListChange as MatSelectionListChange } from '@angular/material/legacy-list';

@Component({
  selector: 'tei-tee-times-dialog-dynamic-pricing-detail-template',
  templateUrl: './dynamic-pricing-detail-template.component.html',
  styleUrls: ['./dynamic-pricing-detail-template.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(DynamicPricingDetailTemplateComponent)
  ],
  changeDetection: ChangeDetectionStrategy.Default
})
export class DynamicPricingDetailTemplateComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{
  model: GetDynamicPricingDetailTemplateResponse;
  form: PostDynamicPricingDetailTemplateRequestForm;
  templateID: string;
  panelOpenStateDate: boolean;
  panelOpenStateCartPrice: boolean;
  panelOpenStateAvailable: boolean;
  panelOpenPrepayAvailable: boolean;
  priceLevels: PriceLevelOption[];
  allPriceLevelSelected: boolean;
  // @ViewChild('allSelected', { static: true }) private allSelected: MatListOption;

  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('IDynamicPricingDetailTemplateService') private DynamicPricingDetailTemplateService: IDynamicPricingDetailTemplateService,
    private activatedRoute: ActivatedRoute, private ref: ChangeDetectorRef,
  )
  {
    super(_errorStateMatcher);
    this.templateID = this.activatedRoute.snapshot.params['templateID'];
    this.form = new PostDynamicPricingDetailTemplateRequestForm(null);
  }

  ngOnInit()
  {
    this.form = new PostDynamicPricingDetailTemplateRequestForm(null);
    const request = new GetDynamicPricingDetailTemplateRequest();
    request.TemplateID = this.templateID;
    this.load(request);
    this.ngOnChanges();

  }

  ngOnChanges(): void
  {
    this.form.valueChanges.subscribe(value =>
    {
      this.model.DynamicPricingTemplates.find(x => x).StartDate = value["DynamicPriceForm"].find(x => x).StartDate;
      this.model.DynamicPricingTemplates.find(x => x).EndDate = value["DynamicPriceForm"].find(x => x).EndDate;
      this.model.DynamicPricingTemplates.find(x => x).AlloctedPrice = value["DynamicPriceForm"].find(x => x).AlloctedPrice;
      this.model.DynamicPricingTemplates.find(x => x).IsPowerCartIncluded = value["DynamicPriceForm"].find(x => x).IsPowerCartIncluded;
      this.model.DynamicPricingTemplates.find(x => x).IsLocal = value["DynamicPriceForm"].find(x => x).IsLocal;
      this.model.DynamicPricingTemplates.find(x => x).IsWeb = value["DynamicPriceForm"].find(x => x).IsWeb;
    });
  }



  private load(request: GetDynamicPricingDetailTemplateRequest)
  {
    if (!this.model)
    {
      this.model = new GetDynamicPricingDetailTemplateResponse();
    }

    this.DynamicPricingDetailTemplateService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.priceLevels = this.model.DynamicPricingTemplates[0].PriceLevels;
      this.form.setValue(this.model);
      this.loadToggleChange();
    });
  }

  prepaidToggleChange()
  {
    const setDynamicPricePrepayForm = this.form.get('DynamicPriceForm') as UntypedFormArray;
    const setDynamicPricePrepaygroupForm = setDynamicPricePrepayForm.controls[0] as UntypedFormGroup;

    if (!setDynamicPricePrepaygroupForm.get('EnablePrepay').value)
    {
      setDynamicPricePrepaygroupForm.get('RequirePrepay').patchValue(false);
      setDynamicPricePrepaygroupForm.get("RequirePrepay").disable();
      setDynamicPricePrepaygroupForm.get('Prepay18GolfDiscountPercent').reset();
      setDynamicPricePrepaygroupForm.get("Prepay18GolfDiscountPercent").disable();
      setDynamicPricePrepaygroupForm.get('Prepay18CartDiscountPercent').reset();
      setDynamicPricePrepaygroupForm.get("Prepay18CartDiscountPercent").disable();
    }
    else
    {
      setDynamicPricePrepaygroupForm.get("RequirePrepay").enable();
      setDynamicPricePrepaygroupForm.get("Prepay18GolfDiscountPercent").enable();
      setDynamicPricePrepaygroupForm.get("Prepay18GolfDiscountPercent").patchValue(0);
      setDynamicPricePrepaygroupForm.get("Prepay18CartDiscountPercent").enable();
      setDynamicPricePrepaygroupForm.get("Prepay18CartDiscountPercent").patchValue(0);
    }

    // if (setDynamicPricePrepaygroupForm.get('EnablePrepay').value && !setDynamicPricePrepaygroupForm.get('RequirePrepay').value)
    // {
    //   setDynamicPricePrepaygroupForm.get("Prepay18GolfDiscountPercent").enable();
    //   setDynamicPricePrepaygroupForm.get("Prepay18CartDiscountPercent").enable();
    // }

    // if (setDynamicPricePrepaygroupForm.get('EnablePrepay').value && setDynamicPricePrepaygroupForm.get('RequirePrepay').value)
    // {
    //   setDynamicPricePrepaygroupForm.get('Prepay18GolfDiscountPercent').reset();
    //   setDynamicPricePrepaygroupForm.get("Prepay18GolfDiscountPercent").disable();
    //   setDynamicPricePrepaygroupForm.get('Prepay18CartDiscountPercent').reset();
    //   setDynamicPricePrepaygroupForm.get("Prepay18CartDiscountPercent").disable();
    // }
  }

  loadToggleChange()
  {
    const setDynamicPricePrepayForm = this.form.get('DynamicPriceForm') as UntypedFormArray;
    const setDynamicPricePrepaygroupForm = setDynamicPricePrepayForm.controls[0] as UntypedFormGroup;

    if (!setDynamicPricePrepaygroupForm.get('EnablePrepay').value)
    {
      setDynamicPricePrepaygroupForm.get('RequirePrepay').patchValue(false);
      setDynamicPricePrepaygroupForm.get("RequirePrepay").disable();
      setDynamicPricePrepaygroupForm.get('Prepay18GolfDiscountPercent').reset();
      setDynamicPricePrepaygroupForm.get("Prepay18GolfDiscountPercent").disable();
      setDynamicPricePrepaygroupForm.get('Prepay18CartDiscountPercent').reset();
      setDynamicPricePrepaygroupForm.get("Prepay18CartDiscountPercent").disable();
    }
    else
    {
      setDynamicPricePrepaygroupForm.get("RequirePrepay").enable();
      setDynamicPricePrepaygroupForm.get("Prepay18GolfDiscountPercent").enable();
      setDynamicPricePrepaygroupForm.get("Prepay18CartDiscountPercent").enable();
    }
  }

  greenFeePercent()
  {
    const setDynamicPricePrepayForm = this.form.get('DynamicPriceForm') as UntypedFormArray;
    const setDynamicPricePrepaygroupForm = setDynamicPricePrepayForm.controls[0] as UntypedFormGroup;
    let greenFeeValue = 100;
    let currentGreenFeeValue = Number(setDynamicPricePrepaygroupForm.get("Prepay18GolfDiscountPercent").value);
    if (currentGreenFeeValue > 100)
    {
      setDynamicPricePrepaygroupForm.get("Prepay18GolfDiscountPercent").patchValue(greenFeeValue);
    }
    else if (currentGreenFeeValue < 0)
    {
      setDynamicPricePrepaygroupForm.get("Prepay18GolfDiscountPercent").patchValue(0);
    }
    else
    {
      setDynamicPricePrepaygroupForm.get("Prepay18GolfDiscountPercent").patchValue(currentGreenFeeValue);
    }
  }

  cartFeePercent()
  {
    const setDynamicPricePrepayForm = this.form.get('DynamicPriceForm') as UntypedFormArray;
    const setDynamicPricePrepaygroupForm = setDynamicPricePrepayForm.controls[0] as UntypedFormGroup;
    let cartFeeValue = 100;
    let currentCartFeeValue = Number(setDynamicPricePrepaygroupForm.get("Prepay18CartDiscountPercent").value);
    if (currentCartFeeValue > 100)
    {
      setDynamicPricePrepaygroupForm.get("Prepay18CartDiscountPercent").patchValue(cartFeeValue);
    }
    else if (currentCartFeeValue < 0)
    {
      setDynamicPricePrepaygroupForm.get("Prepay18CartDiscountPercent").patchValue(0);
    }
    else
    {
      setDynamicPricePrepaygroupForm.get("Prepay18CartDiscountPercent").patchValue(currentCartFeeValue);
    }
  }



  alloctedPriceConversion(): void
  {
    let allocatedprice = Number(this.form.DynamicPriceForm.value[0].AlloctedPrice);
    this.form.get('DynamicPriceForm').get('0').get("AlloctedPrice").patchValue(allocatedprice.toFixed(2));
  }

  oncartValChange(value: any)
  {
    const dynamicPriceForm = this.form.get('DynamicPriceForm') as UntypedFormArray;
    dynamicPriceForm.controls.forEach((x, i) =>
    {
      x["controls"]["IsPowerCartIncluded"].patchValue((value === 'true') ? true : false);
    });
  }

  selectionChange(event: MatSelectionListChange)
  {
    let priceLevels = this.form.DynamicPriceForm.controls[0].get("PriceLevels") as UntypedFormArray;

    if (event.options[0].selected)
    {
      priceLevels.controls.forEach(element =>
      {
        if (event.options[0].value == element.get("Value").value)
        {
          element.get('Selected').patchValue(true);
        }
      });
    }
    else
    {
      priceLevels.controls.forEach(element =>
      {
        if (event.options[0].value == element.get("Value").value)
        {
          element.get('Selected').patchValue(false);
        }
      });
    }
  }

  allPriceLevelsChanged($event)
  {
    let priceLevels = this.form.DynamicPriceForm.controls[0].get("PriceLevels") as UntypedFormArray;
    if ($event.checked)
    {
      priceLevels.controls.forEach(element =>
      {
        element.get('Disabled').patchValue(true);
        element.get('Selected').patchValue(false);
      });
    }
    else
    {
      priceLevels.controls.forEach(element =>
      {
        element.get('Disabled').patchValue(false);
        element.get('Selected').patchValue(false);
      });
    }
  }

}
