import { DragDropModule } from '@angular/cdk/drag-drop';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { SnackBarModule } from '@tei/material';
import { NgxMatTimepickerModule } from 'ngx-mat-timepicker';
import { DynamicTimeGroupsService } from '../../@services/dynamic-time-groups.service';
import { DynamicTimeGroupsComponent } from './dynamic-time-groups.component';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';

const CORE_MODULES = [TeiTextTranslatePipeModule];

const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MatFormFieldModule, MatInputModule, MatIconModule, MatButtonModule, DragDropModule, MatDialogModule];
const TEI_MATERIAL_MODULES = [SnackBarModule];
const THIRD_PARTY_MODULES = [NgxMatTimepickerModule.setLocale('en-US')];

const COMPONENTS = [DynamicTimeGroupsComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...THIRD_PARTY_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  // exports: [
  //   ...BASE_MODULES,
  //   ...MATERIAL_MODULES,
  //   ...THIRD_PARTY_MODULES,
  //   ...TEI_MATERIAL_MODULES,
  //   ...COMPONENTS
  // ],
  providers: [{ provide: 'IDynamicTimeGroupsService', useClass: DynamicTimeGroupsService }],
})
export class DynamicTimeGroupsModule { }
