import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacyPaginator as MatPaginator } from '@angular/material/legacy-paginator';
import { MatSort } from '@angular/material/sort';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
import moment from 'moment'
import { CourseDetail, CourseDetailsMetadata, GetDynamicPricingRequest, GetDynamicPricingResponse, IDynamicPricingService, PostDynamicPricingRequestForm } from '../../@intermediate/dynamic-pricing';
import { DeleteDynamicPricingRequest } from '../../@intermediate/dynamic-pricing/models/delete-dynamic-pricing.model';
import { DuplicateDynamicPricingRequest } from '../../@intermediate/dynamic-pricing/models/duplicate-dynamic-pricing.model';
import { DynamicPricingTemplateComponent } from '../../components/dynamic-pricing-template/dynamic-pricing-template.component';

@Component({
  selector: 'tei-tee-times-pages-dynamic-pricing',
  templateUrl: './dynamic-pricing.component.html',
  styleUrls: ['./dynamic-pricing.component.scss']
})
export class DynamicPricingComponent implements OnInit
{
  model: GetDynamicPricingResponse;
  form: PostDynamicPricingRequestForm;
  metadata = new CourseDetailsMetadata();
  datasource: MatTableDataSource<CourseDetail>;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  dynamicPricingAccess: boolean = true;
  dynamicPricingErrorMessage: string;

  constructor(@Inject('IDynamicPricingService') private DynamicPricingService: IDynamicPricingService, public dialog: MatDialog,
    private confirmdialogService: ConfirmDialogService, private snackbarService: SnackBarService)
  {

  }

  ngOnInit()
  {
    //this.form = new PostDynamicPricingRequestForm();
    const request = new GetDynamicPricingRequest();

    this.load(request);
  }

  private load(request: GetDynamicPricingRequest)
  {
    if (!this.model)
    {
      this.model = new GetDynamicPricingResponse();
    }

    this.DynamicPricingService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.bindCourses(this.model.CourseDetails);
    },
      error =>
      {
        if (error.error.Message == 'Authorization has been denied for this request.')
        {
          this.dynamicPricingAccess = false;
          this.dynamicPricingErrorMessage = error.error.Message;
        }
      });
  }

  private bindCourses(CourseDetail: CourseDetail[])
  {
    CourseDetail.forEach(x =>
    {
      x.Start = (moment(x.Start).format("YYYY-MM-DD").toString());
      x.End = (moment(x.End).format("YYYY-MM-DD").toString());
    });
    this.datasource = new MatTableDataSource<CourseDetail>(CourseDetail);
    this.datasource.paginator = this.paginator;
    this.datasource.sort = this.sort;
  }

  openDialog(): void
  {
    let dialogref = this.dialog.open(DynamicPricingTemplateComponent, {
      panelClass: '',
      height: '',
      maxWidth: '80vh',
      data: {}
    });

    dialogref.afterClosed().subscribe(result =>
    {
      this.load(new GetDynamicPricingRequest());
    });
  }

  DeleteItem(element: CourseDetail)
  {
    const dialogData: DialogData = {
      title: 'Delete',
      message: `Are you sure you want to delete this template?`,
      textOkayButton: 'Delete',
      textCancelButton: 'Cancel'
    };

    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.deleteItem(element);
      }
    });

  }

  private deleteItem(element: CourseDetail)
  {
    const request = new DeleteDynamicPricingRequest();
    request.DynamicPricingUID = element.DynamicPricingUID;
    request.CourseId = element.CourseId;
    request.Start = element.Start;
    request.End = element.End;
    request.IsPublished = element.IsPublished;
    this.DynamicPricingService.DeleteDynamicPricing(request).subscribe(_ =>
    {
      this.load(new GetDynamicPricingRequest());
      this.snackbarService.showSuccess('Template deleted');
    }, error =>
    {
      let errorMessage = error.error.Message;
      this.snackbarService.showError(errorMessage);
    });
  }

  DuplicateItem(element: CourseDetail)
  {
    const dialogData: DialogData = {
      title: 'Duplicate',
      message: `Are you sure you want to copy this template?`,
      textOkayButton: 'Copy',
      textCancelButton: 'Cancel'
    };
    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.duplicateItem(element);
      }
    });
  }

  private duplicateItem(element: CourseDetail)
  {
    const request = new DuplicateDynamicPricingRequest();
    request.DynamicPricingUID = element.DynamicPricingUID;
    this.DynamicPricingService.DuplicateDynamicPricing(request).subscribe(_ =>
    {
      this.load(new GetDynamicPricingRequest());
      this.snackbarService.showSuccess('Course copied successfully');
    }, error =>
    {
      let errorMessage = error.error.Message;
      this.snackbarService.showError(errorMessage);
    });
  }

  save(form: PostDynamicPricingRequestForm)
  {
    this.DynamicPricingService.Post(form.value).subscribe(response =>
    {

    });
  }
}
