import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { AvatarModule, LoadingModule } from '@tei/material';
import { TeeTimeClubService } from '../../@services/tee-time-club.service';
import { TeeTimeClubComponent } from './tee-time-club.component';

const CORE_MODULES = [TeiTextTranslatePipeModule];

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatMenuModule, MatIconModule, MatButtonModule, MatCardModule, MatInputModule, MatDividerModule];
const TEI_MATERIAL_MODULES = [AvatarModule, LoadingModule];

const COMPONENTS = [TeeTimeClubComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...COMPONENTS
  ],
  providers: [{ provide: 'ITeeTimeClubService', useClass: TeeTimeClubService }],
})
export class TeeTimeClubModule { }
