import { Component, Inject, OnInit, SkipSelf } from '@angular/core';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { GetDynamicPricingTemplateRequest, GetDynamicPricingTemplateResponse, IDynamicPricingTemplateService, PostDynamicPricingTemplateRequestForm } from '../../@intermediate/dynamic-pricing-template';

@Component({
  selector: 'tei-tee-times-dialog-dynamic-pricing-template',
  templateUrl: './dynamic-pricing-template.component.html',
  styleUrls: ['./dynamic-pricing-template.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(DynamicPricingTemplateComponent)
  ]
})
export class DynamicPricingTemplateComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{
  model: GetDynamicPricingTemplateResponse;
  form: PostDynamicPricingTemplateRequestForm;
  courseSelected: boolean = true;
  setForm: boolean;
  // dateSelected:boolean = false;
  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('IDynamicPricingTemplateService') private DynamicPricingTemplateService: IDynamicPricingTemplateService,
    public dialog: MatDialogRef<DynamicPricingTemplateComponent>, @Inject(MAT_DIALOG_DATA) public data: any, private snackbarService: SnackBarService)
  {
    super(_errorStateMatcher);
  }

  ngOnInit()
  {
    this.form = new PostDynamicPricingTemplateRequestForm();
    const request = new GetDynamicPricingTemplateRequest();
    this.load(request);
  }

  private load(request: GetDynamicPricingTemplateRequest)
  {
    if (!this.model)
    {
      this.model = new GetDynamicPricingTemplateResponse();
    }

    this.DynamicPricingTemplateService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.writeValue(this.model);
    });
  }

  courseChangeAction()
  {
    let courseSelectedValue = this.form.get("Course").value;
    this.courseSelected = (courseSelectedValue != '-1') ? false : true;
  }

  dateChange()
  {
    //this.dateSelected = (this.form.get('SelectedStartDate').value != null && this.form.get('SelectedEndDate').value != null)? false : true;
    if (this.form.get('SelectedStartDate').value != null && this.form.get('SelectedEndDate').value != null)
    {
      this.form.get("SelectedEndDate").setErrors(null);
    }
    else
    {
      this.form.get("SelectedEndDate").setErrors({ required: true });
    }
  }


  onCancel(): void
  {
    this.dialog.close();
  }

  save(form: PostDynamicPricingTemplateRequestForm)
  {
    this.setForm = true;
    this.DynamicPricingTemplateService.Post(form.value).subscribe(response =>
    {
      this.dialog.close();
      this.setForm = false;
      this.snackbarService.showSuccess('Template saved.');
    }, error =>
    {
      let errorMessage = error.error.Message;
      let a = errorMessage.indexOf("Start");
      let b = errorMessage.indexOf("\r");
      this.snackbarService.showError(errorMessage.substring(a, b));
    });
  }
}
