import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { ITeeTimeFilterService, GetTeeTimeFilterRequest, GetTeeTimeFilterResponse, PostTeeTimeFilterRequest, PostTeeTimeFilterResponse } from '../@intermediate/tee-time-filter';

@Injectable({
  providedIn: 'root'
})

export class TeeTimeFilterService implements ITeeTimeFilterService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Booking/TeeTimes/TeeTimeFilter';
  }

  public Get(request: GetTeeTimeFilterRequest): Observable<GetTeeTimeFilterResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetTeeTimeFilterResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostTeeTimeFilterRequest): Observable<PostTeeTimeFilterResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostTeeTimeFilterResponse>(apiUrl, request);
  }
}
