@Injectable()
export class MyDateAdapter extends MomentDateAdapter
{
  constructor(
    @Inject(MAT_DATE_LOCALE) locale: string,
    @Optional() @Inject(MAT_MOMENT_DATE_ADAPTER_OPTIONS) private options?: any
  )
  {
    super(locale, options);
  }

  format(date: moment.Moment, displayFormat: string): string
  {
    if (this.locale.includes("fr"))
    {
      return date.format('ddd, MMM D, YYYY');
    } else
    {
      return date.format('ddd, MMM D, YYYY');
    }
  }

  setDateFormat(format: string)
  {
    this.setLocale(this.locale);

    this.setDateFormat(format);
  }
}



