  IsInitTeeTimeRequest: boolean;
  IsValidDelay: boolean = true;
  timePickerInitialSelection: boolean = false;
  isManualDateChange = false;
  cachedSelectedCourses: string[] = [];
  @ViewChild('teiTimepicker') teiTimepicker: TimePickerComponent;
  @ViewChild('dateInput') dateInput: ElementRef;
  private isInitialFocus = true;
  heartbeatInterval: NodeJS.Timeout;
  heartBeatCreated = false;
  shouldShowClubs = false;
  clubCode: string;
  selectedClubCourses: string[];
