  dateChange(event: MatDatepickerInputEvent<Date>)
  {
    if (this.isManualDateChange)
    {
      return;
    }

    this.cachedSelectedCourses = this.courses.filter(c => c.IsSelected).map(c => c.CourseValue);

    this.dateChangeOnGroups();
    this.dateChangeOnGenericSearch();
    this.dateChangeOnNoSearchButton();

    // this.setFilterTime();

    if (!this.model?.Filtersettings?.FilteringBySearch && !this.model?.Filters?.GenericFilteringBySearch)
    {
      this.initTeeTimeRequest(null);
    }
    else
    {
      this.updateCourseSelection(moment(event.value).format("YYYY-MM-DD").toString());
    }

    this.changeFavoriteCourses(false);
    this.sortFavorites();

    setTimeout(() =>
    {
      if (this.cachedSelectedCourses.length)
      {
        this.courses.forEach(course =>
        {
          course.IsSelected = this.cachedSelectedCourses.includes(course.CourseValue);
        });
      }

      this.checkSelectAllCourses();
      this.setSelectedCourses();
      this.setDefaultCourse();
      this.updateCourseDisplayText();
    }, 0);

    //Release all held teetimes, if any
    this.releaseHoldTeeTimes();
    // this.initTeeTimeRequest(moment(event.value).format("YYYY-MM-DD").toString());

    const favoriteCourses = this.courses.filter(c => c.Favorites);
    const allFavoritesSelected = favoriteCourses.every(c => c.IsSelected);

    this.selectedAllFavorites = allFavoritesSelected;
    this.model.FavoritesChecked = allFavoritesSelected;
  }

  changeDate(days: number, event: Event): void
  {
    event.stopPropagation();

    const currentDate = moment(this.form.get('SingleDate')?.value).clone();
    const newDate = currentDate.add(days, 'days');

    if (newDate.isBefore(moment(this.minDate), 'day') || newDate.isAfter(moment(this.maxDate), 'day'))
    {
      return;
    }

    this.cachedSelectedCourses = this.courses.filter(c => c.IsSelected).map(c => c.CourseValue);

    this.isManualDateChange = false;

    this.form.get('SingleDate')?.setValue(newDate.toDate());

    this.dateChange({ value: newDate.toDate() } as MatDatepickerInputEvent<Date>);
  }

