import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAuthToken } from '@tei/abstraction';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetTeeTimeDetailsRequest, GetTeeTimeDetailsResponse, ITeeTimeDetailsService, PostTeeTimeDetailsRequest, PostTeeTimeDetailsResponse } from '../@intermediate/tee-time-details';
import { DeletePlayerTeeTimeDetailsRequest, DeletePlayerTeeTimeDetailsResponse } from '../@intermediate/tee-time-details/models/delete-player-tee-time-details.model';
import { DeleteTeeTimeTeeTimeDetailsRequest, DeleteTeeTimeTeeTimeDetailsResponse } from '../@intermediate/tee-time-details/models/delete-tee-time-tee-time-details.model';
import { GetPriceTeeTimeDetailsRequest, GetPriceTeeTimeDetailsResponse } from '../@intermediate/tee-time-details/models/get-price-tee-time-details.model';
import { InitTeeTimeDetailsRequest, InitTeeTimeDetailsResponse } from '../@intermediate/tee-time-details/models/init-tee-time-details.model';
import { ShoppingCartTeeTimeDetailsRequest, ShoppingCartTeeTimeDetailsResponse } from '../@intermediate/tee-time-details/models/shopping-cart-tee-time-details.model';
import { UpdateMemberDataServiceTeeTimeDetailsRequest, UpdateMemberDataServiceTeeTimeDetailsResponse } from '../@intermediate/tee-time-details/models/update-member-data-service-tee-time-details.model';
import { ValidateMemberDetailsDataServiceTeeTimeDetailsRequest, ValidateMemberDetailsDataServiceTeeTimeDetailsResponse } from '../@intermediate/tee-time-details/models/validate-member-details-data-service-tee-time-details.model';

@Injectable({
  providedIn: 'root'
})

export class TeeTimeDetailsService implements ITeeTimeDetailsService
{
  private readonly serviceUrl: string;
  public static readonly RELEASED_TEE_TIME_ID = "RELEASED_TEE_TIME_ID";

  get releaseTeeTimeRout(): string { return this.serviceUrl; }

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider
  , protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Booking/TeeTimes/TeeTimeDetails';
  }

  public Get(request: GetTeeTimeDetailsRequest): Observable<GetTeeTimeDetailsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetTeeTimeDetailsResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostTeeTimeDetailsRequest): Observable<PostTeeTimeDetailsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostTeeTimeDetailsResponse>(apiUrl, request);
  }

  UpdateReleasedTeeTimeDetailsID(teeTimeID: string): void
  {
    sessionStorage.setItem(TeeTimeDetailsService.RELEASED_TEE_TIME_ID, teeTimeID);
  }

  GetReleasedTeeTimeDetailsID(): string
  {
    return sessionStorage.getItem(TeeTimeDetailsService.RELEASED_TEE_TIME_ID);
  }

  RemoveReleasedTeeTimeDetailsID(): void
  {
    sessionStorage.removeItem(TeeTimeDetailsService.RELEASED_TEE_TIME_ID);
  }

  public async PostByFetch(request: PostTeeTimeDetailsRequest, token: IAuthToken): Promise<PostTeeTimeDetailsResponse>
  {
    const response = await fetch(this.serviceUrl, {
      method: 'POST',
      body: JSON.stringify(request),
      headers: {
        'Authorization': `Bearer ${ token.getValue() }`,
        'Content-Type': 'application/json'
      },
      keepalive: true
    });

    return response.json();
  }

  public ReleaseTeeTimesByBeacon(request: PostTeeTimeDetailsRequest, token: IAuthToken)
  {
    const data = JSON.stringify({
      'x_access_token': `${ token.getValue() }`,
      ...request // Include all request properties
    });
    const blob = new Blob([data], { type: 'application/json' });
    navigator.sendBeacon(this.serviceUrl +'/ReleaseFireAndForget', blob);
  }

  public Init(request: InitTeeTimeDetailsRequest): Observable<InitTeeTimeDetailsResponse>
  {
    const apiUrl = this.serviceUrl + "/Init";
    return this.http.post<InitTeeTimeDetailsResponse>(apiUrl, request);
  }

  public GetPrice(request: GetPriceTeeTimeDetailsRequest): Observable<GetPriceTeeTimeDetailsResponse>
  {
    const apiUrl = this.serviceUrl + "/GetPrice";
    return this.http.post<GetPriceTeeTimeDetailsResponse>(apiUrl, request);
  }

  public ShoppingCart(request: ShoppingCartTeeTimeDetailsRequest): Observable<ShoppingCartTeeTimeDetailsResponse>
  {
    const apiUrl = this.serviceUrl + "/ShoppingCart";
    return this.http.post<ShoppingCartTeeTimeDetailsResponse>(apiUrl, request);
  }

  public DeleteTeeTime(request: DeleteTeeTimeTeeTimeDetailsRequest): Observable<DeleteTeeTimeTeeTimeDetailsResponse>
  {
    const apiUrl = this.serviceUrl + "/DeleteTeeTime";
    return this.http.post<DeleteTeeTimeTeeTimeDetailsResponse>(apiUrl, request);
  }

  public DeletePlayer(request: DeletePlayerTeeTimeDetailsRequest): Observable<DeletePlayerTeeTimeDetailsResponse>
  {
    const apiUrl = this.serviceUrl + "/DeletePlayer";
    return this.http.post<DeletePlayerTeeTimeDetailsResponse>(apiUrl, request);
  }

  public UpdateMemberDataService(request: UpdateMemberDataServiceTeeTimeDetailsRequest): Observable<UpdateMemberDataServiceTeeTimeDetailsResponse>
  {
    const apiUrl = this.serviceUrl + "/UpdateMemberDataService";
    return this.http.post<UpdateMemberDataServiceTeeTimeDetailsResponse>(apiUrl, request);
  }

  public ValidateMemberDetailsDataService(request: ValidateMemberDetailsDataServiceTeeTimeDetailsRequest): Observable<ValidateMemberDetailsDataServiceTeeTimeDetailsResponse>
  {
    const apiUrl = this.serviceUrl + "/ValidateMemberDetailsDataService";
    return this.http.post<ValidateMemberDetailsDataServiceTeeTimeDetailsResponse>(apiUrl, request);
  }
}
