import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanDeactivate, RouterStateSnapshot, UrlTree } from '@angular/router';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { DynamicPricingDetailsComponent } from './dynamic-pricing-details.component';


@Injectable({
  providedIn: 'root'
})
export class CanDeactivateGuardService implements CanDeactivate<DynamicPricingDetailsComponent>
{

  component: Object;
  route: ActivatedRouteSnapshot;
  constructor()
  {

  }


  canDeactivate(component: DynamicPricingDetailsComponent,
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot,
    nextState: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean
  {
    return component.canExit();
  }
}
