import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAuthToken } from '@tei/abstraction';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetTeetimesRequest, GetTeetimesResponse, ITeetimesService, PostTeetimesRequest, PostTeetimesResponse } from '../@intermediate/teetimes';
import { GetMaxBookingDaysRequest, GetMaxBookingDaysResponse } from '../@intermediate/teetimes/models/get-max-booking-days';
import { GetReadOnlyTeeSheetRequest, GetReadOnlyTeeSheetResponse } from '../@intermediate/teetimes/models/GetReadOnlyTeeSheet.model';
import { InsertFavoriteTeetimesRequest, InsertFavoriteTeetimesResponse } from '../@intermediate/teetimes/models/insert-favorite-teetimes.model';
import { PostReleaseHoldTeeTimeRequest, PostReleaseHoldTeeTimeResponse } from '../@intermediate/teetimes/models/post-release-teetime';

@Injectable({
  providedIn: 'root'
})
export class TeetimesService implements ITeetimesService
{
  private readonly serviceUrl: string;

  get releaseTeeTimeRout(): string { return this.serviceUrl + '/ReleaseTeeTimes'; }

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Booking/Teetimes';
  }

  public Get(request: GetTeetimesRequest): Observable<GetTeetimesResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetTeetimesResponse>(apiUrl, { params: request as any, headers: new HttpHeaders({ CaptchaRequired: 'Booking_Teetimes' }) });
  }

  public GetInitialData(request: GetTeetimesRequest): Observable<GetTeetimesResponse>
  {
    const apiUrl = this.serviceUrl + '/InitialData';
    return this.http.get<GetTeetimesResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostTeetimesRequest): Observable<PostTeetimesResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostTeetimesResponse>(apiUrl, request);
  }

  public InsertFavorite(request: InsertFavoriteTeetimesRequest): Observable<InsertFavoriteTeetimesResponse>
  {
    const apiUrl = this.serviceUrl + "/InsertFavorite";
    return this.http.post<InsertFavoriteTeetimesResponse>(apiUrl, request);
  }

  public GetMaxBookingDays(request: GetMaxBookingDaysRequest): Observable<GetMaxBookingDaysResponse>
  {
    const apiUrl = this.serviceUrl + '/GetMaxBookingDays';
    return this.http.get<GetMaxBookingDaysResponse>(apiUrl, { params: request as any });
  }
  public GetReadOnlyTeeSheet(request: GetReadOnlyTeeSheetRequest): Observable<GetReadOnlyTeeSheetResponse>
  {
    const apiUrl = this.serviceUrl + '/GetReadOnlyTeeSheet';
    return this.http.get<GetReadOnlyTeeSheetResponse>(apiUrl, { params: request as any });
  }

  public ReleaseTeeTimes(request: PostReleaseHoldTeeTimeRequest): Observable<PostReleaseHoldTeeTimeResponse>
  {
    const apiUrl = this.releaseTeeTimeRout;
    return this.http.post<PostReleaseHoldTeeTimeResponse>(apiUrl, request);
  }

  public async ReleaseTeeTimesByFetch(token: IAuthToken)
  {
    await fetch(this.releaseTeeTimeRout, {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${ token.getValue() }`,
        // 'ExtendedAuthorization': `Bearer ${ token.getPayload()?.extended_access_token }`,
        'Content-Type': 'application/json'
      },
      keepalive: true
    });


  }

  public ReleaseTeeTimesByBeacon(token: IAuthToken)
  {
    const data = JSON.stringify({
      'x_access_token': `${ token.getValue() }`,
    });
    const blob = new Blob([data], { type: 'application/json' });
    navigator.sendBeacon(this.releaseTeeTimeRout, blob);

  }
}
