import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatSortModule } from '@angular/material/sort';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { RouterModule } from '@angular/router';
import { ConfirmDialogModule, SnackBarModule } from '@tei/material';
import { DynamicPricingService } from '../../@services/dynamic-pricing.service';
import { DynamicPricingTemplateComponent } from '../../components/dynamic-pricing-template/dynamic-pricing-template.component';
import { DynamicPricingTemplateModule } from '../../components/dynamic-pricing-template/dynamic-pricing-template.module';
import { DynamicPricingComponent } from './dynamic-pricing.component';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';

const CORE_MODULES = [TeiTextTranslatePipeModule];
const BASE_MODULES = [FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatTableModule, MatPaginatorModule, MatSortModule, MatIconModule, MatButtonModule, MatDialogModule];
const TEI_MATERIAL_MODULES = [ConfirmDialogModule, SnackBarModule];
const MODULES = [DynamicPricingTemplateModule];

@NgModule({
    declarations: [DynamicPricingComponent],
    imports: [
        RouterModule.forChild([{ path: '', component: DynamicPricingComponent }]),
        ...BASE_MODULES,
        ...MATERIAL_MODULES,
        ...TEI_MATERIAL_MODULES,
        ...MODULES,
        ...CORE_MODULES
    ],
    providers: [{ provide: 'IDynamicPricingService', useClass: DynamicPricingService }]
})
export class DynamicPricingModule { }
