import { NgModule } from "@angular/core";
import { RouterModule } from "@angular/router";
import { TeeSheetMainModule, TeeSheetModule } from "@tei/pos-tee-sheet";
import { TeesheetViewComponent } from "./teesheet.component";
// import {TeeSheetModule} from '@tei/pos-tee-sheet';

@NgModule({
  declarations: [TeesheetViewComponent],
  imports: [
    RouterModule.forChild([
      {
        path: '',
        component: TeesheetViewComponent,
        children: [
          {
            path: 'read-only',
            loadChildren: () => import('@tei/pos-tee-sheet').then(m => m.TeeSheetModule)
          }]
        },
        //  {
          //     path: 'read-only',
          //     loadChildren: () => import('@tei/pos-tee-sheet').then(m => m.TeeSheetModule)
          //   },
        ]),
        TeeSheetModule,
        TeeSheetMainModule
    
    // TeeSheetModule
  ],
  exports: [
    TeesheetViewComponent
  ]
 
})
export class TeesheetViewModule { }
