import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyTabsModule as MatTabsModule } from '@angular/material/legacy-tabs';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { ContactAvatarModule, ContactLookupSubmitEventService, ContactLookupValidationFailedEventService, ContactSelectionWrapperModule } from '@tei/common-contact-lookup-shared';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ConfirmDialogModule, CountDownTimerModule, LoadingModule, SnackBarModule } from '@tei/material';
import { TeeTimeDetailsService } from '../../@services/tee-time-details.service';
import { CancelReservationDialogComponent } from '../../components/cancelreservationdialog/cancelreservationdialog.component';
import { TeeTimeDetailsComponent } from './tee-time-details.component';

const CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TeiDateTranslateModule, TranslateModule];

const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MatCardModule, MatDividerModule, MatCheckboxModule, MatFormFieldModule, MatButtonModule, MatIconModule,
  MatTabsModule, MatDialogModule, MatButtonToggleModule];
const TEI_MATERIAL_MODULES = [ContactAvatarModule, ContactSelectionWrapperModule, ConfirmDialogModule,
  SnackBarModule, CountDownTimerModule, LoadingModule];
const PROVIDERS = [ContactLookupValidationFailedEventService, ContactLookupSubmitEventService];

@NgModule({
    declarations: [TeeTimeDetailsComponent, CancelReservationDialogComponent],
    imports: [
        RouterModule.forChild([{ path: '', component: TeeTimeDetailsComponent }]),
        ...BASE_MODULES,
        ...MATERIAL_MODULES,
        ...TEI_MATERIAL_MODULES,
        ...CORE_MODULES
    ],
    providers: [{ provide: 'ITeeTimeDetailsService', useClass: TeeTimeDetailsService }, ...PROVIDERS]
})
export class TeeTimeDetailsModule { }
