import { UntypedFormArray, UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { GetSettingsResponse, RoleBasedSetting, RoleValueSetting } from './get-settings.model';

export class PostSettingsRequest
{
  public Image: String;
  public ImageName: String;
  public CourseName: String;
  public CourseID: String;
  public IsNineHole: String;
  public RoleBasedSettings: RoleBasedSetting[];
}

export class PostSettingsRequestForm extends UntypedFormGroup
{
  public readonly RoleBasedSettings: UntypedFormArray;

  constructor()
  {
    super({
      CourseID: new UntypedFormControl(''),
      CourseName: new UntypedFormControl(''),
      Image: new UntypedFormControl(''),
      ImageName: new UntypedFormControl(''),
      IsNineHole: new UntypedFormControl(''),
      RoleBasedSettings: new UntypedFormArray([])
    });

    this.RoleBasedSettings = this.get('RoleBasedSettings') as UntypedFormArray;
  }

  public value: PostSettingsRequest;

  public setValue(value: GetSettingsResponse)
  {
    const request = new PostSettingsRequest();

    request.CourseID = value.CourseID;
    request.CourseName = value.CourseName;
    request.Image = value.ImageUrl;
    request.IsNineHole = value.IsNineHole;
    request.ImageName = value.ImageName;
    request.RoleBasedSettings = value.RoleBasedSettings;

    this.RoleBasedSettings.clear();

    request.RoleBasedSettings.forEach((role) =>
    {
      this.addRoleBasedField(role);
    });

    super.setValue(request);
  }

  public addRoleBasedField(role: RoleBasedSetting)
  {
    const roleForm = new RoleBasedSettingForm(role);

    this.RoleBasedSettings.push(roleForm);
  }
}

export class RoleBasedSettingFormArray extends UntypedFormArray
{
  constructor(value: RoleBasedSetting[])
  {
    super([]);
    this.setValue(value);
  }

  public setValue(value: RoleBasedSetting[],
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    if (value != undefined)
    {
      value.forEach(roleBasedSetting =>
      {
        let roleBasedSettingForm = new RoleBasedSettingForm(roleBasedSetting);
        this.push(roleBasedSettingForm);
      });
    }
  }
}

export class RoleBasedSettingForm extends UntypedFormGroup
{
  public RoleValueSettings: RoleValueSettingFormArray;

  constructor(value: RoleBasedSetting)
  {
    super({
      RoleSettingName: new UntypedFormControl(value.RoleSettingName),
      RoleBasedID: new UntypedFormControl(value.RoleBasedID),
      RoleValueSettings: new RoleValueSettingFormArray(value.RoleValueSetting),
    });

    this.RoleValueSettings = this.get('RoleValueSettings') as UntypedFormArray;
  }
}

export class RoleValueSettingFormArray extends UntypedFormArray
{
  constructor(value: RoleValueSetting[])
  {
    super([]);
    this.setValue(value);
  }

  public setValue(value: RoleValueSetting[],
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    if (value != undefined)
    {
      value.forEach(roleBasedSetting =>
      {
        let roleBasedSettingForm = new RoleValueSettingForm(roleBasedSetting);
        this.push(roleBasedSettingForm);
      });
    }
  }
}

export class RoleValueSettingForm extends UntypedFormGroup
{
  constructor(value: RoleValueSetting)
  {
    super({
      RoleName: new UntypedFormControl(value.RoleName),
      RoleID: new UntypedFormControl(value.RoleID),
      Value: new UntypedFormControl(value.Value),
      Type: new UntypedFormControl(value.Type),
    });
  }
}

export class PostSettingsResponse
{

}
