import { Overlay } from '@angular/cdk/overlay';
import { AfterViewInit, Component, HostListener, Inject, OnDestroy, OnInit, SkipSelf, ViewChild } from '@angular/core';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { ActivatedRoute, Event as NavigationEvent, NavigationStart, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { IAuthService, IAuthToken } from '@tei/abstraction';
import
{
  ContactSelectionWrapperComponent,
  ContactSelectionWrapperGroup,
  ContactSelectionWrapperParameters,
  CustomValidationInfo,
  CustomValidationRule,
  Operations,
  PostContactSelectionWrapperRequest,
  ValidationItems,
  WrapperContactChangedArgs,
  WrapperContactRemovedArgs,
  WrapperIndivisualMasterFieldValueChangedArgs
} from '@tei/common-contact-lookup-shared';
import { FilterRef } from '@tei/common-field-management-shared';
import { AbstractFormGroupErrorStateMatcher, ServiceWorkerSyncService } from '@tei/core';
import { ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
import { ShoppingCartService } from '@tei/retail-ecommerce';
import { firstValueFrom } from 'rxjs';
import { GetTeeTimeDetailsRequest, GetTeeTimeDetailsResponse, ITeeTimeDetailsService, PostTeeTimeDetailsRequest, PostTeeTimeDetailsRequestForm, PostTeeTimeDetailsResponse } from '../../@intermediate/tee-time-details';
import { DeleteTeeTimeTeeTimeDetailsRequest } from '../../@intermediate/tee-time-details/models/delete-tee-time-tee-time-details.model';
import { Attachment, AttachmentDetails, AttachmentPerGroup, GetPriceTeeTimeDetailsRequest, GetPriceTeeTimeDetailsResponse, PayAtCoursePrice, PrePayPrice, PriceDetail } from '../../@intermediate/tee-time-details/models/get-price-tee-time-details.model';
import { InitTeeTimeDetailsRequest } from '../../@intermediate/tee-time-details/models/init-tee-time-details.model';
import { ShoppingCartTeeTimeDetailsRequest } from '../../@intermediate/tee-time-details/models/shopping-cart-tee-time-details.model';
import { PriceDetails, UpdateMemberDataServiceTeeTimeDetailsRequest, UpdateMembers } from '../../@intermediate/tee-time-details/models/update-member-data-service-tee-time-details.model';
import { ValidateMemberDetailsDataServiceTeeTimeDetailsRequest } from '../../@intermediate/tee-time-details/models/validate-member-details-data-service-tee-time-details.model';
import { GetTeetimesRequest } from '../../@intermediate/teetimes';
import { CancelReservationDialogComponent } from '../../components/cancelreservationdialog/cancelreservationdialog.component';

@Component({
  selector: 'tei-tee-times-pages-tee-time-details',
  templateUrl: './tee-time-details.component.html',
  styleUrls: ['./tee-time-details.component.scss']
})
export class TeeTimeDetailsComponent extends AbstractFormGroupErrorStateMatcher implements OnInit, AfterViewInit, OnDestroy
{
  token: IAuthToken;
  model: GetTeeTimeDetailsResponse;
  form: PostTeeTimeDetailsRequestForm;
  teeTimeID: string;
  termsAndConditions: boolean = false;
  displayTermsAndConditions: boolean = false;
  parameters: ContactSelectionWrapperParameters;
  @ViewChild('wrapper') wrapper: ContactSelectionWrapperComponent;
  contactLookupDetails: WrapperContactChangedArgs[] = [];
  filter: GetTeetimesRequest;
  teeTimeCart: PostTeeTimeDetailsResponse;
  selectionHeaderUID: string = "";
  selectedContactsSelectionHeaderUID: string;
  validateMembers: boolean;
  edit: boolean;
  updatelist: UpdateMembers[] = [];
  defaultGroup: ContactSelectionWrapperGroup;
  deletedPlayers: number[] = [];
  attachmentDetails: AttachmentDetails[] = [];
  attachmentPerGroup: AttachmentPerGroup[] = [];
  validatedMember: any;
  timerDisplay: any;
  refresh: boolean;
  //TODO what is the purpose of this flag, it should be refactored
  isLoad: boolean = false;
  payOption: number = 1;  // 0 - pay now / 1- pay at course
  updateMembers: boolean = false;
  timeLeft: number;
  moveReservationSelectionHeaderUID: string;
  moveTeeTimeID: string;
  error: boolean = false;
  isLoading: boolean;
  isContactLookUpLoaded: boolean = false;
  saveChangesDisabled: boolean = false;
  isContactRemoved: boolean = false;
  navigateToTeeTimes: boolean = false;
  storage: Storage;
  sub: any;
  releaseCalled = false;
  heartbeatInterval: NodeJS.Timeout;
  heartBeatCreated = false;

  @HostListener('window:beforeunload', ['$event'])
  beforeUnloadHandler($event)
  {
    this.releaseCalled = true;
    this.updateAfterReload();
  }

  @HostListener('window:pagehide', ['$event'])
  onPageHide(event: PageTransitionEvent)
  {
    if (!event.persisted && !this.releaseCalled)
    {
      this.updateAfterReload();
    }
  }

  private updateAfterReload()
  {
    this.ngOnDestroy();
    if (!this.edit)
    {
      this.TeeTimeDetailsService.UpdateReleasedTeeTimeDetailsID(this.teeTimeID);
      const req = new PostTeeTimeDetailsRequest();
      req.IsReleaseTeeTime = true;
      req.TeeTimeID = this.teeTimeID;
      this.TeeTimeDetailsService.ReleaseTeeTimesByBeacon(req, this.token);

      if (navigator.serviceWorker.controller)
      {
        try
        {
          this.syncService.postMessage({ type: 'HEARTBEAT_CLOSE' });
        } catch (e)
        {
          console.error(e);
        }
      }
    }
    else
    {
      history.pushState(null, null, location.origin);
      window.addEventListener('popstate', function ($event)
      {
        history.pushState(null, null, location.origin);
      });
      this.goItinerary();
    }
  }

  sendInitialHeartbeat(): void
  {
    if (!this.heartbeatInterval && !this.heartBeatCreated)
    {
      const request = new PostTeeTimeDetailsRequest();
      request.IsReleaseTeeTime = true;
      request.TeeTimeID = this.teeTimeID;
      this.syncService.postMessage({
        type: 'HEARTBEAT_INIT',
        token: this.token.getValue(),
        url: this.TeeTimeDetailsService.releaseTeeTimeRout,
        request: request
      });
    }
  }

  startHeartbeatPing(): void
  {
    if (!this.heartbeatInterval && !this.heartBeatCreated)
    {
      clearInterval(this.heartbeatInterval);
      this.heartbeatInterval = setInterval(() =>
      {
        this.syncService.postMessage({ type: 'HEARTBEAT_PING' });
      }, 5000); // every 5 seconds
      this.heartBeatCreated = true;
    }
  }

  constructor(
    @Inject('ITeeTimeDetailsService') private TeeTimeDetailsService: ITeeTimeDetailsService,
    private shoppingCartService: ShoppingCartService,
    private activatedRoute: ActivatedRoute,
    public dialog: MatDialog,
    private confirmdialogService: ConfirmDialogService,
    private overlay: Overlay,
    private teiTextTranslateService: TranslateService,
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    private router: Router,
    private activeRoute: ActivatedRoute,
    private _snackBar: SnackBarService,
    private translate: TranslateService
    , @Inject('IAuthService') private authService: IAuthService
    , private syncService: ServiceWorkerSyncService
  )
  {
    super(_errorStateMatcher);
    this.edit = false;
    if (this.activatedRoute.snapshot.params['edit'])
    {
      this.edit = Boolean(JSON.parse(this.activatedRoute.snapshot.params['edit']));
    }
    this.timeLeft = Number(localStorage.getItem('leftTime'));

    this.teeTimeID = this.activatedRoute.snapshot.params['teeTimeID'];
    //this.parameters = context.Parameters;
    this.storage = sessionStorage;
    this.handleNavigation();
  }

  private handleNavigation()
  {
    this.sub = this.router.events
      .subscribe(
        (event: NavigationEvent) =>
        {
          if (event instanceof NavigationStart)
          {
            if (!event.url.includes('/teetime-details') &&
              !event.url.includes('/payment-engine/checkout') &&
              !event.url.includes('payment-engine/billing') &&
              !event.url.includes('payment-engine/confirmation') &&
              !this.edit &&
              !decodeURIComponent(event.url).includes("/tee-times/teetime-details") &&
              !decodeURIComponent(event.url).includes("/tee-times/confirmation"))
            {
              this.releaseTeeTime(event.url);
            }
          }
        });
  }

  async ngOnInit()
  {
    this.token = await firstValueFrom(this.authService.getToken());

    if (this.TeeTimeDetailsService.GetReleasedTeeTimeDetailsID())
    {
      if (!this.edit)
      {
        this.router.navigateByUrl(this.getTeeTimesUrl());
      }
      this.TeeTimeDetailsService.RemoveReleasedTeeTimeDetailsID();
    }


    this.clearSessionStorage();
    this.selectedContactsSelectionHeaderUID = this.activeRoute.snapshot.queryParamMap.get('shuid');
    this.moveTeeTimeID = this.activeRoute.snapshot.queryParamMap.get('moveTeeTimeID');
    this.form = new PostTeeTimeDetailsRequestForm(this.edit);
    const request = new GetTeeTimeDetailsRequest();

    request.TeeTimeID = this.teeTimeID;
    request.IsMoveReservation = (this.moveTeeTimeID) ? true : false;
    request.IsEdit = this.edit;

    const storedRequest = localStorage.getItem('teeTimeRequestdetail');

    if (!(this.edit) && JSON.parse(localStorage.getItem('previousPage')) && !storedRequest)
    {
      this.goBack();
      return false;
    }

    if (this.teeTimeID == undefined)
    {
      let request_init = new InitTeeTimeDetailsRequest();

      if (storedRequest)
      {
        request_init.TeeTimeRequestdetail = JSON.parse(storedRequest);
        request_init.TeeTimeRequestdetail.MoveTeeTimeID = this.moveTeeTimeID;
        this.load_init(request_init, request);
        localStorage.removeItem('teeTimeRequestdetail');
      }
    }
    else
    {
      this.load(request);
    }
  }

  clearSessionStorage()
  {
    if (this.storage.getItem('redirection_reference') != null)
    {
      this.storage.removeItem('redirection_reference');
    }
  }

  ngAfterViewInit(): void
  {
    //history.pushState(null, null);
  }


  private load_init(request_init: InitTeeTimeDetailsRequest, request: GetTeeTimeDetailsRequest)
  {
    this.TeeTimeDetailsService.Init(request_init).subscribe(response =>
    {


      request.TeeTimeID = response.TeeTimeID;

      this.load(request);
    });
  }

  private load(request: GetTeeTimeDetailsRequest)
  {
    if (!this.model)
    {
      this.model = new GetTeeTimeDetailsResponse();
    }
    this.isLoading = true;
    this.TeeTimeDetailsService.Get(request).subscribe(response =>
    {


      this.model = response;
      //this.model.Timer = (this.timeLeft > 0) ? this.timeLeft : this.model.Timer;
      this.parameters = new ContactSelectionWrapperParameters();
      this.defaultGroup = new ContactSelectionWrapperGroup();
      this.parameters.FieldGroupFilter = new FilterRef();
      this.parameters.MasterFieldOptionFilter = new FilterRef();
      this.parameters.Groups = [];
      // this.parameters.OverrideRules = {};
      this.defaultGroup.OverrideRules = {};
      this.defaultGroup.RefID = this.model.TeeTimeDetails.CourseID;
      this.defaultGroup.ContactLookUpTemplateUID = response?.ContactLookUpTemplateUID;
      this.defaultGroup.NotificationRefID = response.AutomatedNotificationID?.toString() ?? "";
      this.defaultGroup.FieldGroupRefID = response.FieldGroupID?.toString() ?? "";

      this.defaultGroup.SelectionHeaderUID = this.selectedContactsSelectionHeaderUID;
      this.defaultGroup.Name = this.model.TeeTimeDetails.CourseName;
      this.payOption = (this.model.TeeTimeDetails.PaymentType == 1 || this.model.TeeTimeDetails.PaymentType == 2) ? 0 : 1;
      this.saveChangesDisabled = !this.model.EditEnabled;


      if (this.edit)
      {

        //this.isContactLookUpLoaded = true;
        this.turnOffProgressBar();
        this.isLoad = true;

        if (!this.model.EditEnabled)
        {
          this.model.TeeTimeDetails.AllowAdd = false;
        }

        if (this.model.TeeTimeDetails.AllowAdd)
        {
          this.parameters.AppendEmptySlotOnRemove = true;
          this.defaultGroup.NumberOfPersons = this.model.NumberOfPlayers;
        }
        else
        {
          this.parameters.AppendEmptySlotOnRemove = false;
          this.defaultGroup.NumberOfPersons = this.model.TeeTimeDetails.NumberofPlayers;
        }
        this.defaultGroup.BookingRefID = this.model.FirstExtensionUID;
        this.defaultGroup.AditionalParameters = {
          TeeTimeUID: this.teeTimeID,
          IsManager: response.IsManager
        };

        let autoPopulateSetting = { Enabled: false };
        this.defaultGroup.OverrideRules["AutoPopulateSetting"] = JSON.stringify(autoPopulateSetting).toString();

      }
      else
      {
        if (this.moveTeeTimeID)
        {
          this.isLoad = true;
        }
        this.defaultGroup.NumberOfPersons = this.model.TeeTimeDetails.NumberofPlayers;
        // localStorage.setItem('previousPage', 'true');
      }
      this.parameters.ModuleID = 1;
      this.parameters.AllowEdit = !this.edit ? true : ((this.edit && this.model.EditEnabled)) ? true : false;
      this.parameters.HideStepperForSingleStep = true;
      this.parameters.FieldGroupFilter.Ref1 = this.model.TeeTimeDetails.CourseID;
      this.parameters.MasterFieldOptionFilter.Ref1 = this.model.TeeTimeDetails.Holes.toString() == "18" ? "1" : "2";
      let holdSettings = { Enabled: response.TBDEnabled };
      let searchGroupEnabled = { Enabled: !this.edit };
      let hideRemoveReplace = { Enabled: (this.defaultGroup.SelectionHeaderUID) ? false : true };
      this.defaultGroup.OverrideRules["HoldSettings"] = JSON.stringify(holdSettings).toString();
      this.defaultGroup.OverrideRules["SearchGroupEnabled"] = JSON.stringify(searchGroupEnabled).toString();


      if (this.defaultGroup.SelectionHeaderUID)
      {
        this.defaultGroup.OverrideRules["HideRemoveReplace"] = JSON.stringify(hideRemoveReplace).toString();
      }

      this.defaultGroup.CustomValidation = {
        CustomValidationRules: [{
          ValidationItem: ValidationItems.AcceptableEntryWithSameEmailCount,
          InvalidMessage: this.teiTextTranslateService.instant('TEE_TIME.TEE_TIME_DETAIL.DUPLICATE_EMAILS'),
          Operation: Operations.LessThan,
          Operand1: 2
        } as CustomValidationRule]
      } as CustomValidationInfo;

      this.parameters.Groups.push(this.defaultGroup);

      if (this.model.DisplayTermsAndConditions)
      {
        this.model.TermsAndConditions = this.termsAndConditions;
      } else
      {
        this.model.TermsAndConditions = true;
      }

      this.form.setValue(this.model);

      if (this.updateMembers && this.edit)
      {
        setTimeout(() =>
        {
          this.updatePrice(false, true);
        }, 200);
      }
    }, error =>
    {
      this.turnOffProgressBar();
      if (error?.error?.includes("No Permission Error"))
      {
        this.router.navigateByUrl(this.getTeeTimesUrl());
      }
    });
  }

  turnOffProgressBar()
  {
    this.isLoading = false;
  }

  onTimesUp(event: boolean)
  {
    this.goBack();
  }

  timerDetailPerSecond(event: number)
  {
    localStorage.setItem('leftDetailTime', event.toString());
    //localStorage.setItem('leftDetailTime', this.model.Timer?.toString());
  }

  getNewImage()
  {
    return this.model.TeeTimeDetails.CourseImage + '?m=' + Date.now();
  }

  save(form: PostTeeTimeDetailsRequestForm)
  {
    localStorage.setItem('PaymentEngineReturnURL', this.getTeeTimesUrl());

    // const c = this.form.controls.ContactLookupWrapper.value;
    this.isLoading = true;
    setTimeout(() =>
    {
      this.validateMembers = this.wrapper.submit();
      localStorage.removeItem('previousPage');

    }, 200);

  }

  CartDialog()
  {
    const dialogData: DialogData = {
      title: `<div class="a-text-h3">${ this.teiTextTranslateService.instant('TEE_TIME.TEE_TIME_DETAIL.WARNING') }</div>`,
      message: `<div class="a-text-default">${ this.teiTextTranslateService.instant('TEE_TIME.TEE_TIME_DETAIL.YOU_ALREADY_HAVE_A_TEETIME_IN-YOUR_CART') }</div>`,
      textOkayButton: this.teiTextTranslateService.instant('TEE_TIME.TEE_TIME_DETAIL.REPLACE'),
      textCancelButton: this.teiTextTranslateService.instant('TEE_TIME.TEE_TIME_DETAIL.KEEP_CART_TEETIME')
    };

    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      // if replace, remove from the cart and add new the new tee time to the cart
      // else, relase the hold tee time and navigate to the payment engine.
      if (result)
      {
        // RemoveTeeTimeFromCart();
        let request_remove = new ShoppingCartTeeTimeDetailsRequest();
        request_remove.ReleaseTeeTimeDetails = new PostTeeTimeDetailsRequest();
        request_remove.ReleaseTeeTimeDetails.TeeTimeID = localStorage.getItem("cartTeeTimeId");
        this.TeeTimeDetailsService.ShoppingCart(request_remove).subscribe(response =>
        {
          localStorage.removeItem("cartTeeTimeId");
          this.save(this.form);
        });
      }
      else
      {
        this.releaseTeeTime('payment-engine/checkout', true);
      }
    });
  }

  goBack()
  {
    if (this.moveTeeTimeID)
    {
      this.router.navigate(['../../..', 'teetimes', this.moveTeeTimeID, true, this.selectedContactsSelectionHeaderUID], { relativeTo: this.activeRoute });
      return;
    }

    localStorage.removeItem('previousPage');
    this.releaseTeeTime(this.getTeeTimesUrl());
  }


  async releaseTeeTime(path: string = '', isPaymentMethodURL: boolean = false)
  {
    let extraParams = localStorage.getItem("extraUrlParams");
    if (extraParams)
    {
      extraParams = JSON.parse(extraParams);
      localStorage.removeItem("extraUrlParams");
    }
    this.TeeTimeDetailsService.UpdateReleasedTeeTimeDetailsID(this.teeTimeID);
    const req = new PostTeeTimeDetailsRequest();
    req.IsReleaseTeeTime = true;
    req.TeeTimeID = this.teeTimeID;
    const response = await (this.TeeTimeDetailsService.PostByFetch(req, this.token));
    {
      localStorage.removeItem('leftTime');
      localStorage.setItem('disableBackButton', 'true');

      if (!this.edit)
      {
        localStorage.setItem('previousPage', 'true');
      }

      if (isPaymentMethodURL && response.PaymentMethodUrl)
      {
        this.router.navigateByUrl(response.PaymentMethodUrl);
      }
      else
      {
        window.location.href = `${ path }?${ extraParams ? new URLSearchParams(extraParams).toString() : '' }`;
      }
    }
  }

  goItinerary()
  {
    localStorage.removeItem('previousPage');
    let ItineraryUrl = localStorage.getItem("ItineraryURL");
    // window.location.href = ItineraryUrl;
    const path = new URL(ItineraryUrl)?.pathname;
    this.router.navigate(
      [path],
      { replaceUrl: true }
    );
  }

  moveReservation()
  {
    this.router.navigate(["../../..", 'teetimes', this.teeTimeID, true, this.moveReservationSelectionHeaderUID], { relativeTo: this.activeRoute });
  }

  _onIndivisualMasterFieldValueChanged($event: WrapperIndivisualMasterFieldValueChangedArgs)
  {
    if ($event.TypeCode !== 'WEB_UDF' && $event.TypeCode == "TEE_TIME")
    {
      this.updateAttachmentDetails($event.PositionNumber, $event.NewValue, $event.MasterFieldID);

      // if (this.attachmentDetails.length > 0)
      // commented out since the length would be 0 if user unselects the options,
      if (!this.edit && !this.moveTeeTimeID)
      {
        this.updatePrice(false, this.edit);
      }
      else
      {
        setTimeout(() =>
        {
          this.updatePrice(false, this.edit);
        }, 200);
      }
      // }
    }
  }

  updateAttachmentDetails(positionNumber: number, newValue: any, masterFieldID: number)
  {
    if (!newValue)
    {
      // The user has selected the 'None' option
      this.deleteFromAttachmentDetails(positionNumber, masterFieldID);
      return;
    }

    const existing = this.attachmentDetails.find(x => (x.PositionNumber) == Number(positionNumber) && x.MasterFieldID == masterFieldID);

    if (existing)
    {
      this.editAttachmentDetails(positionNumber, newValue.Value, masterFieldID);
      return;
    }

    this.insertToAttachmentDetails(positionNumber, newValue.Value, masterFieldID);
  }

  insertToAttachmentDetails(positionNumber: number, newValue: any, masterFieldID: number)
  {
    let attachments = new AttachmentDetails();

    attachments.PositionNumber = positionNumber;
    attachments.AttachmentAnsUID = newValue;
    attachments.MasterFieldID = masterFieldID;

    this.attachmentDetails.push(attachments);
  }

  editAttachmentDetails(positionNumber: number, value: any, masterFieldID: number)
  {
    const itemIndex = this.getAttachmentDetailIndex(Number(positionNumber), masterFieldID);

    if (itemIndex > -1)
    {
      this.attachmentDetails[itemIndex].PositionNumber = positionNumber;
      this.attachmentDetails[itemIndex].AttachmentAnsUID = value;
    }
  }

  deleteFromAttachmentDetails(positionNumber: number, masterFieldID: number)
  {
    const itemIndex = this.getAttachmentDetailIndex(Number(positionNumber), masterFieldID);

    if (itemIndex > -1)
    {
      this.attachmentDetails.splice(itemIndex, 1);
    }
  }

  getAttachmentDetailIndex(positionNumber: number, masterFieldID: number): number
  {
    return this.attachmentDetails.findIndex(x => (x.PositionNumber) == positionNumber && x.MasterFieldID == masterFieldID);
  }

  getAttachmentDetailForContactIndex(positionNumber: number): number
  {
    return this.attachmentDetails.findIndex(x => (x.PositionNumber) == positionNumber);
  }

  _onIndividualFieldGroupLookupLoaded($event: WrapperIndivisualMasterFieldValueChangedArgs)
  {
    if ((this.edit || this.moveTeeTimeID) && (this.isLoad || this.attachmentDetails.length === 0))
    {
      $event["FieldGroupLookup"].forEach(element =>
      {
        element["MasterFieldAnswers"].forEach(e =>
        {
          if (e.Value != 0 && e.Value != null && e.MasterFieldTypeCode !== 'WEB_UDF' && e.MasterFieldTypeCode === 'TEE_TIME')
          {
            let attachments = new AttachmentDetails();
            attachments.AttachmentAnsUID = e["MasterFieldAnswerOptions"].find(x => x.MasterFieldOptionID == e.Value).Value;
            attachments.PositionNumber = $event.PositionNumber;
            attachments.MasterFieldID = e.MasterFieldID;
            this.attachmentDetails.push(attachments);
          }
        });
      });
    }
  }



  _onGroupFieldGroupLookupLoaded($event: WrapperIndivisualMasterFieldValueChangedArgs)
  {
    if ((this.edit || this.moveTeeTimeID) && (this.isLoad || this.attachmentDetails.length === 0))
    {
      if (this.attachmentPerGroup.length == 0)
      {
        $event["FieldGroupLookup"].forEach(element =>
        {
          element["MasterFieldAnswers"].forEach(e =>
          {
            if (e.Value != 0 && e.Value != null && e.MasterFieldTypeCode !== 'WEB_UDF' && e.MasterFieldTypeCode === 'TEE_TIME')
            {
              let attachmentPerGroup = new AttachmentPerGroup();
              attachmentPerGroup.AttachmentAnsUID = e["MasterFieldAnswerOptions"].find(x => x.MasterFieldOptionID == e.Value).Value;
              attachmentPerGroup.MasterFieldID = e.MasterFieldID;
              attachmentPerGroup.MasterFieldOptionID = e.Value;
              this.attachmentPerGroup.push(attachmentPerGroup);
            }
          });
        });
      }
    }
  }

  _onGroupMasterFieldValueChanged($event: WrapperIndivisualMasterFieldValueChangedArgs)
  {
    if ($event.TypeCode !== 'WEB_UDF' && $event.TypeCode == "TEE_TIME")
    {

      this.updateGroupAttachmentDetails($event);

      if (this.edit || this.moveTeeTimeID)
      {
        setTimeout(() =>
        {
          this.updatePrice(false, this.edit);
        }, 200);
      }
      else
      {
        this.updatePrice(false, this.edit);
      }

    }
  }

  updateGroupAttachmentDetails($event: WrapperIndivisualMasterFieldValueChangedArgs)
  {
    if (!$event.NewValue)
    {
      // The user has selected the 'None' option
      this.deleteFromGroupAttachmentDetails($event.MasterFieldID);
      return;
    }
    let group = this.attachmentPerGroup.find(f => f.MasterFieldID == $event.MasterFieldID);
    if (group)
    {
      this.editGroupAttachmentDetails($event.MasterFieldID, $event.NewValue["MasterFieldOptionID"], $event.NewValue["Value"]);
      return;
    }

    this.insertToGroupAttachmentDetails($event.MasterFieldID, $event.NewValue["MasterFieldOptionID"], $event.NewValue["Value"]);
  }

  insertToGroupAttachmentDetails(masterFieldID: number, MasterFieldOptionID: number, AttachmentAnsUID: any)
  {
    let attachmentPerGroup = new AttachmentPerGroup();

    attachmentPerGroup.MasterFieldID = masterFieldID;
    attachmentPerGroup.MasterFieldOptionID = MasterFieldOptionID;
    attachmentPerGroup.AttachmentAnsUID = AttachmentAnsUID;

    this.attachmentPerGroup.push(attachmentPerGroup);
  }


  deleteFromGroupAttachmentDetails(masterFieldID: number)
  {
    const itemIndex = this.getGroupAttachmentDetailIndex(masterFieldID);

    if (itemIndex > -1)
    {
      this.attachmentPerGroup.splice(itemIndex, 1);
    }
  }

  editGroupAttachmentDetails(masterFieldID: number, MasterFieldOptionID: number, AttachmentAnsUID: any)
  {
    const itemIndex = this.getGroupAttachmentDetailIndex(masterFieldID);
    if (itemIndex > -1)
    {
      this.attachmentPerGroup[itemIndex].AttachmentAnsUID = AttachmentAnsUID;
      this.attachmentPerGroup[itemIndex].MasterFieldOptionID = MasterFieldOptionID;
    }
  }

  getGroupAttachmentDetailIndex(masterFieldID: number): number
  {
    return this.attachmentPerGroup.findIndex(x => x.MasterFieldID == masterFieldID);
  }

  rawAttachments(attachments: AttachmentDetails[])
  {
    if (attachments.length > 0)
    {
      let arr = new Array();
      attachments.forEach(element =>
      {
        arr.push(element.AttachmentAnsUID);
      });
      return arr.join(',');
    }
    else
    {
      return null;
    }
  }

  _onContactChanged($event: WrapperContactChangedArgs)
  {

    if (this.edit)
    {
      this.moveReservationSelectionHeaderUID = $event.Group.SelectionHeaderUID;
    }
    if (this.contactLookupDetails.find(x => Number(x.PositionNumber) == Number($event.PositionNumber)))
    {
      const item = this.contactLookupDetails.find(x => Number(x.PositionNumber) == Number($event.PositionNumber));
      let itemPosition = this.contactLookupDetails.indexOf(item);
      this.contactLookupDetails[itemPosition] = $event;
      if (this.attachmentDetails.length > 0)
      {
        const itemIndex = this.getAttachmentDetailForContactIndex(item.PositionNumber);

        if (itemIndex > -1 && this.attachmentDetails[itemIndex].AttachmentAnsUID != undefined)
        {
          this.attachmentDetails.splice(itemIndex, 1);
        }
      }
    }
    else
    {
      this.contactLookupDetails.push($event);
    }


    if (this.contactLookupDetails.length > 0
      && !this.edit
      && (this.moveTeeTimeID == undefined || this.moveTeeTimeID == null)
      && this.model.TeeTimeDetails.NumberofPlayers == $event.PositionNumber)
    {
      this.updatePrice(false, false);
    }
    else
    {
      if (!this.isLoad)
      {
        setTimeout(() =>
        {
          if (this.model.TeeTimeDetails.NumberofPlayers == $event.PositionNumber)
          {
            this.updatePrice(false, this.edit);
          }

        }, 200);
      }
      else
      {
        if (this.isLoad && this.model.TeeTimeDetails.NumberofPlayers == $event.PositionNumber)
        {
          setTimeout(() =>
          {
            if (this.model.TeeTimeDetails.NumberofPlayers == $event.PositionNumber)
            {
              this.updatePrice(false, this.edit);
            }
          }, 200);
        }
      }

    }


  }

  _oncontactLookupLoaded($event: boolean)
  {
    if ($event)
    {
      //this.isContactLookUpLoaded = true;
      this.turnOffProgressBar();
    }
  }

  contactLookupLoaded(): boolean
  {
    return this.isLoading;
  }


  _onContactAdded($event: WrapperContactChangedArgs)
  {
    let req = new ValidateMemberDetailsDataServiceTeeTimeDetailsRequest();
    req.ContactIds = $event.NewContact.ContactID;
    req.CustomerID = $event.NewContact.ContactID;
    req.FivePlayer = this.defaultGroup.NumberOfPersons == 5 ? true : false;
    req.NineHole = this.model.TeeTimeDetails.Holes == 18 ? false : true;
    req.TeeTime = this.model.TeeTimeDetails.TTDate + " " + this.model.TeeTimeDetails.Time;
    req.CourseID = this.model.TeeTimeDetails.CourseID;
    req.TeeTimeID = this.teeTimeID;

    this.TeeTimeDetailsService.ValidateMemberDetailsDataService(req).subscribe(response =>
    {
      this.validatedMember = undefined;
      if (response.ErrorMessage != "" && response.ErrorMessage != null)
      {
        this.validatedMember = response.ErrorMessage;
      }
      else
      {
        this.validatedMember = "Valid";
      }
    }, error =>
    {
      this.validatedMember = error.error.Message;
    });

    this.validatedMember = "";
  }


  _onSubmitCallback($event)
  {
    if (!this.edit)
    {
      const request = $event.Request;
      const response = $event.Response;
      this.selectionHeaderUID = $event.Response.SelectionGroupUID;
      let req = new PostTeeTimeDetailsRequest();
      let contactLookupSelection: PostContactSelectionWrapperRequest = null;
      contactLookupSelection = this.form.controls.ContactLookupWrapper.value as PostContactSelectionWrapperRequest;
      this.teeTimeCart = new PostTeeTimeDetailsResponse();
      req.ContactSelections = contactLookupSelection;
      req.IsReleaseTeeTime = false;
      req.TeeTimeID = this.teeTimeID;
      req.TermsAndConditions = this.termsAndConditions;
      req.PrePayTeeTime = (this.model.TeeTimeDetails.PaymentType === 1 || this.model.TeeTimeDetails.PaymentType === 2) ? true : false;
      req.MoveTeeTimeID = this.moveTeeTimeID;
      req.MoveReservation = (this.moveTeeTimeID) ? true : false;
      //1 = prepay , 2 =  prepay or due at course
      req.IsAddToShoppingCart = req.PrePayTeeTime ? true : false;
      req.IsCommitTeeTime = (this.model.TeeTimeDetails.PaymentType == 0 || this.payOption == 1) ? true : false;
      req.TeeTimePriceDetails = this.getPriceDetails();
      req.TeeTimePriceDetails.PaymentType = this.model.TeeTimeDetails.PaymentType;
      req.TeeTimePriceDetails.AttachmentTotal = this.model.TeeTimeDetails.AttachmentFee;
      req.TeeTimePriceDetails.AttachmentTaxTotal = this.model.TeeTimeDetails.AttachmentTax;
      req.TeeTimePriceDetails.PayAtCoursePriceDetails = this.model.PayAtCoursePrices.PayAtCoursePriceDetails;
      req.TeeTimePriceDetails.PrePayPriceDetails = this.model.PrePayPrices.PrePayPriceDetails;
      req.SelectionHeaderUID = this.selectionHeaderUID;

      if (req.IsCommitTeeTime)
      {
        req.PrePayTeeTime = false;
      }


      if (this.validateMembers != false)
      {
        this.submit(req);
      }
    }
    else
    {
      const request = $event.Request;
      const response = $event.Response;
      let updatememberrequest = new UpdateMemberDataServiceTeeTimeDetailsRequest();

      request.ContactSelections.forEach(x =>
      {
        let numberofPlayers = x.ContactSelectedResults.filter(function (c)
        {
          return !c.NotSet;
        }).length;

        this.updatelist = [];
        let currentNumofPlayer = this.model.TeeTimeDetails.NumberofPlayers;
        let addedPlayers = 0;

        if (numberofPlayers > currentNumofPlayer)
        {
          addedPlayers = numberofPlayers - currentNumofPlayer;
        }

        let courseID = x.RefID;
        x.ContactSelectedResults.filter(c => !c.NotSet).forEach((y, index) =>
        {
          let updatemember = new UpdateMembers();
          updatemember.TeetimeID = this.teeTimeID;
          updatemember.Emailid = y.Email;
          updatemember.Contactid = y.ContactID;
          updatemember.PlayerNumber = index + 1;
          updatemember.IsGuest = y.SelectionType === 2 ? "1" : "0";
          updatemember.FirstName = y.FirstName;
          updatemember.LastName = y.LastName;
          updatemember.Courseid = courseID;
          updatemember.Sendemailtoall = "0";
          updatemember.Allowedit = y.CanEdit ? "1" : "0";
          updatemember.Currentnumofplayers = currentNumofPlayer;
          updatemember.Addedplayers = addedPlayers;
          updatemember.selectionGroupUID = response.SelectionGroupUID;
          updatemember.Phone = y.Phone;
          this.updatelist.push(updatemember);
        });
      });

      var attachmentDetails = this.getPriceDetails();
      let attachment = attachmentDetails.Attachments;
      let priceDetails: PriceDetails = new PriceDetails();
      let attachmentPerGroup = attachmentDetails.AttachmentsPerGroup;
      updatememberrequest.Attachments = attachment;
      updatememberrequest.AttachmentsPerGroup = attachmentPerGroup;
      updatememberrequest.UpdateMemberList = this.updatelist;
      updatememberrequest.DeletedPlayers = this.deletedPlayers;
      priceDetails.PaymentType = this.model.TeeTimeDetails.PaymentType;
      priceDetails.PayAtCoursePriceDetails = this.model.PayAtCoursePrices.PayAtCoursePriceDetails;
      priceDetails.PrePayPriceDetails = this.model.PrePayPrices.PrePayPriceDetails;
      updatememberrequest.PriceDetails = priceDetails;

      this.TeeTimeDetailsService.UpdateMemberDataService(updatememberrequest)
        .subscribe(response =>
        {
          setTimeout(() =>
          {
            //this.updatePrice(false, true);

            const request = new GetTeeTimeDetailsRequest();
            request.TeeTimeID = this.teeTimeID;
            request.IsEdit = true;
            this.updateMembers = true;
            this.load(request);
          }, 200);
          if (this.isContactRemoved)
          {
            this._snackBar.showInfo(this.teiTextTranslateService.instant('TEE_TIME.TEE_TIME_DETAIL.PLAYER_REMOVED'), '', null, 'center', 'bottom');
          }
          else
          {
            this._snackBar.showSuccess(this.teiTextTranslateService.instant('TEE_TIME.TEE_TIME_DETAIL.SUCCESSFULLY_UPDATED'));
          }
          this.isContactRemoved = false;
        }, error =>
        {
          this.turnOffProgressBar();
          this._snackBar.showError(error.error?.Title);
        });
    }
  }

  _onContactRemoved($event: { args: WrapperContactRemovedArgs, resolve: () => void, reject: () => void; })
  {
    if (this.model.TeeTimeDetails.NumberofPlayers - 1 < this.model.MinimumNumberOfPlayers)
    {
      this._snackBar.showError(this.translate.instant('TEE_TIME.TEE_TIME_DETAIL.MINIMUM_PLAYER_ERROR', { minPlayer: this.model.MinimumNumberOfPlayers }), '', null, 'left', 'bottom');
      $event.reject();
      return;
    }
    this.deletedPlayers = [];
    this.deletedPlayers.push($event.args.PositionNumber);

    if (this.attachmentDetails.length > 0)
    {
      const itemIndex = this.getAttachmentDetailForContactIndex($event.args.PositionNumber);

      if (itemIndex > -1)
      {
        this.attachmentDetails.splice(itemIndex, 1);
      }
    }

    if (this.contactLookupDetails.length > 0)
    {
      this.updatePrice(true, true);
      this.isContactRemoved = true;
      this.save(this.form);
    }

    $event.resolve();
  }

  updatePrice(isRemoved: boolean, isEdit: boolean)
  {
    this.contactLookupDetails.length = isRemoved === true ? (this.contactLookupDetails.length - 1) : this.contactLookupDetails.length;
    let request_price = this.getPriceDetails();
    request_price.NumberofPlayers = this.contactLookupDetails.length;
    request_price.IsEdit = isEdit;
    request_price.IsLoad = this.isLoad;
    request_price.PaymentType = this.model.TeeTimeDetails.PaymentType; // 0 - pay at course / 1 - pay at course and pre pay / 2 - pre pay
    let teeTimePrices = new GetPriceTeeTimeDetailsResponse();

    this.TeeTimeDetailsService.GetPrice(request_price).subscribe(response =>
    {
      teeTimePrices = response;
      let payAtCoursePrice = new PayAtCoursePrice();
      let prePayPrice = new PrePayPrice();
      let payAtCoursePriceDetail = new PriceDetail();
      let prePayPriceDetail = new PriceDetail();
      this.model.PayAtCoursePrices = new PayAtCoursePrice();
      this.model.PayAtCoursePrices.PayAtCoursePriceDetails = new PriceDetail();
      this.model.PrePayPrices = new PrePayPrice();
      this.model.PrePayPrices.PrePayPriceDetails = new PriceDetail();

      if (this.model.TeeTimeDetails.PaymentType == 0)
      {
        // pay at course
        payAtCoursePrice = teeTimePrices.PayAtCoursePrices;
        payAtCoursePriceDetail = teeTimePrices.PayAtCoursePrices.PayAtCoursePriceDetails;
        this.model.PayAtCoursePrices.PayAtCoursePriceDetails = payAtCoursePriceDetail;

      }
      else if (this.model.TeeTimeDetails.PaymentType == 2)
      {
        // pay now or due at course
        payAtCoursePrice = teeTimePrices.PayAtCoursePrices;
        payAtCoursePriceDetail = teeTimePrices.PayAtCoursePrices.PayAtCoursePriceDetails;
        prePayPrice = teeTimePrices.PrePayPrices;
        prePayPriceDetail = teeTimePrices.PrePayPrices.PrePayPriceDetails;
        this.model.PayAtCoursePrices.PayAtCoursePriceDetails = payAtCoursePriceDetail;
        this.model.PrePayPrices.PrePayPriceDetails = prePayPriceDetail;
      }
      else
      {
        //pay now
        prePayPrice = teeTimePrices.PrePayPrices;
        prePayPriceDetail = teeTimePrices.PrePayPrices.PrePayPriceDetails;
        this.model.PrePayPrices.PrePayPriceDetails = prePayPriceDetail;
      }

      this.isLoad = false;
      this.turnOffProgressBar();
    },
      error =>
      {
        this.saveChangesDisabled = true;
        this.isLoad = false;
        this.turnOffProgressBar();
        this._snackBar.showError(error.error);
      });
  }

  submit(req: PostTeeTimeDetailsRequest)
  {
    this.TeeTimeDetailsService.Post(req).subscribe(response =>
    {
      this.turnOffProgressBar();
      if (req.IsCommitTeeTime && !response.CCRequired)
      {
        // let paymenturl = location.protocol + '//' + location.hostname + response.PaymentMethodUrl + 'checkout';
        // window.location.href = paymenturl;
        this.router.navigate(['../../..', 'confirmation', req.TeeTimeID, req.MoveReservation], { queryParams: { moid: req.MoveTeeTimeID }, relativeTo: this.activeRoute });
      }
      else
      {
        this.teeTimeCart = response;
        if (this.teeTimeCart.TeeTimeId != null)
        {
          localStorage.setItem("cartTeeTimeId", this.teeTimeCart.TeeTimeId);
        }
        if (this.teeTimeCart?.Count > 0 && this.teeTimeCart?.Count != null)
        {
          this.CartDialog();
        }
        else if (this.teeTimeCart.ErrorMessage != '')
        {
          this._snackBar.showError(this.teeTimeCart.ErrorMessage);
          return;
        }
        else
        {

          this.shoppingCartService.onAddToShoppingCart();
          this.router.navigate([this.teeTimeCart.PaymentMethodUrl]);
        }
      }
    }, error =>
    {
      this.turnOffProgressBar();
      this.error = true;
      // this._snackBar.showError(error.error);
    });
  }

  getPriceDetails()
  {
    let request_price = new GetPriceTeeTimeDetailsRequest();
    request_price.Attachments = new Attachment();
    request_price.AttachmentsPerGroup = this.attachmentPerGroup;
    request_price.CartType = this.model.TeeTimeDetails.CartType;
    request_price.IsNineHole = (this.model.TeeTimeDetails.Holes == 9) ? '1' : '0';
    request_price.IsPrePay = (this.payOption === 0) ? "1" : "0";
    request_price.TeeTimeId = this.teeTimeID;
    request_price.StartTee = this.model.TeeTimeDetails.StartTee;
    request_price.CourseID = this.model.TeeTimeDetails.CourseID;
    request_price.Time = this.model.TeeTimeDetails.Time;
    request_price.TTDate = this.model.TeeTimeDetails.TTDate;
    request_price.NumberofPlayers = this.model.TeeTimeDetails.NumberofPlayers;
    request_price.OrgBookedDateTime = this.model.TeeTimeDetails.OrgBookedDateTime;
    request_price.CourseName = this.model.TeeTimeDetails.CourseName;
    request_price.TeeTimeDisplay = this.model.TeeTimeDetails.TeeTimeDisplay;
    request_price.IsEdit = this.edit;
    request_price.MoveReservation = (this.moveTeeTimeID) ? true : false;
    request_price.MoveReservationTeeTimeID = this.moveTeeTimeID;

    this.contactLookupDetails.forEach(x =>
    {
      if (Number(x.PositionNumber) == 1)
      {
        request_price.PlayerOneCID = x.NewContact.ContactID;
        request_price.PlayerOneFN = x.NewContact.FirstName;
        request_price.PlayerOneLN = x.NewContact.LastName;
        request_price.PlayerOneEmail = x.NewContact.Email;
        request_price.PlayerOnePhone = x.NewContact.Phone;
        request_price.Attachments.PlayerOneAttachment = (this.attachmentDetails.length > 0) ? this.rawAttachments(this.attachmentDetails.filter(y => y.PositionNumber == Number(x.PositionNumber))) : null;
      }
      else if (Number(x.PositionNumber) == 2)
      {
        request_price.PlayerTwoCID = x.NewContact.ContactID;
        request_price.PlayerTwoFN = x.NewContact.FirstName;
        request_price.PlayerTwoLN = x.NewContact.LastName;
        request_price.PlayerTwoEmail = x.NewContact.Email;
        request_price.PlayerTwoPhone = x.NewContact.Phone;
        request_price.Attachments.PlayerTwoAttachment = (this.attachmentDetails.length > 0) ? this.rawAttachments(this.attachmentDetails.filter(y => y.PositionNumber == Number(x.PositionNumber))) : null;
      }
      else if (Number(x.PositionNumber) == 3)
      {
        request_price.PlayerThreeCID = x.NewContact.ContactID;
        request_price.PlayerThreeFN = x.NewContact.FirstName;
        request_price.PlayerThreeLN = x.NewContact.LastName;
        request_price.PlayerThreeEmail = x.NewContact.Email;
        request_price.PlayerThreePhone = x.NewContact.Phone;
        request_price.Attachments.PlayerThreeAttachment = (this.attachmentDetails.length > 0) ? this.rawAttachments(this.attachmentDetails.filter(y => y.PositionNumber == Number(x.PositionNumber))) : null;
      }
      else if (Number(x.PositionNumber) == 4)
      {
        request_price.PlayerFourCID = x.NewContact.ContactID;
        request_price.PlayerFourFN = x.NewContact.FirstName;
        request_price.PlayerFourLN = x.NewContact.LastName;
        request_price.PlayerFourEmail = x.NewContact.Email;
        request_price.PlayerFourPhone = x.NewContact.Phone;
        request_price.Attachments.PlayerFourAttachment = (this.attachmentDetails.length > 0) ? this.rawAttachments(this.attachmentDetails.filter(y => y.PositionNumber == Number(x.PositionNumber))) : null;
      }
      else if (Number(x.PositionNumber) == 5)
      {
        request_price.PlayerFiveCID = x.NewContact.ContactID;
        request_price.PlayerFiveFN = x.NewContact.FirstName;
        request_price.PlayerFiveLN = x.NewContact.LastName;
        request_price.PlayerFiveEmail = x.NewContact.Email;
        request_price.PlayerFivePhone = x.NewContact.Phone;
        request_price.Attachments.PlayerFiveAttachment = (this.attachmentDetails.length > 0) ? this.rawAttachments(this.attachmentDetails.filter(y => y.PositionNumber == Number(x.PositionNumber))) : null;
      }
    });

    return request_price;

  }

  cancelReservationDialog()
  {
    const dialogRef = this.dialog.open(CancelReservationDialogComponent, {
      panelClass: "modal-material-template",
      width: "325px",
    });

    dialogRef.afterClosed().subscribe((result) =>
    {
      if (result)
      {
        this.cancelReservation();
      }
    });
  }

  timeTODate(timeString: any): Date
  {
    const [hours, minutes, seconds] = timeString.split(':');
    const date = new Date();
    date.setHours(parseInt(hours, 10));
    date.setMinutes(parseInt(minutes, 10));
    date.setSeconds(parseInt(seconds, 10));
    return date;

  }
  cancelReservation()
  {
    const request = new DeleteTeeTimeTeeTimeDetailsRequest();
    request.DeleteTeeTimeId = this.teeTimeID;
    this.TeeTimeDetailsService.DeleteTeeTime(request).subscribe(response =>
    {
      let ItineraryUrl = localStorage.getItem("ItineraryURL");
      //window.location.href = ItineraryUrl;
      const path = new URL(ItineraryUrl)?.pathname;
      this.router.navigate(
        [path],
        { replaceUrl: true }
      );
    }, error =>
    {
      this._snackBar.showError(error.error);
    });
  }

  //Open dialog for terms and conditions
  openTermAndConditionsDialog()
  {
    const dialogData: DialogData = {
      title: this.teiTextTranslateService.instant('TEE_TIME.TEE_TIME_DETAIL.TERMS_AND_CONDITION'),
      message: this.model.TermsAndConditionsText,
      textOkayButton: this.teiTextTranslateService.instant('TEE_TIME.TEE_TIME_DETAIL.I_ACCPECT'),
      textCancelButton: this.teiTextTranslateService.instant('TEE_TIME.TEE_TIME_DETAIL.I_DECLINE'),
      fullScreenInMobileView: true
    };

    const scrollStrategy = this.overlay.scrollStrategies.reposition();
    const dialogRef = this.confirmdialogService.openDialog(dialogData, { disableClose: true, autoFocus: false, scrollStrategies: scrollStrategy, panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'] });

    dialogRef.afterClosed().subscribe(result =>
    {
      if (result === true)
      {
        this.form.controls['TermsAndConditions'].setValue(true);
      } else
      {
        this.form.controls['TermsAndConditions'].setValue(false);
      }
    });
  }

  private getTeeTimesUrl(): string
  {
    const currentUrl = this.router.url;
    const urlParts = currentUrl.split('/').filter(Boolean);

    const isParameterized = this.activatedRoute.snapshot.url.length >= 3;
    const levelsUp = isParameterized ? 3 : 1;

    // Remove last `levelsUp` parts
    const targetParts = urlParts.slice(0, urlParts.length - levelsUp);
    const targetUrl = '/' + targetParts.join('/');

    return targetUrl;
  }

  ngOnDestroy()
  {
    clearInterval(this.heartbeatInterval);
    localStorage.removeItem('teeTimeRequestdetail');
    this.sub?.unsubscribe();
  }

  payTabChanged($event)
  {
    let val = Number($event.value);
    this.payOption = (val === 0) ? 0 : 1;
  }

  get currentLanguage()
  {
    const profileSettings = JSON.parse(localStorage.getItem('profile-settings'));
    return profileSettings?.language ?? "en";
  }
}
