import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { CheckAttachmentsBeforeMoveDataServiceRequest, CheckAttachmentsBeforeMoveDataServiceResponse, GetManageDialogRequest, GetManageDialogResponse, IManageDialogService, PostManageDialogRequest, PostManageDialogResponse } from '../@intermediate/manage-dialog';

@Injectable({
  providedIn: 'root'
})

export class ManageDialogService implements IManageDialogService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Booking/Teetimes/ManageDialog';
  }

  public Get(request: GetManageDialogRequest): Observable<GetManageDialogResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetManageDialogResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostManageDialogRequest): Observable<PostManageDialogResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostManageDialogResponse>(apiUrl, request);
  }

  public GetAttachmentsChecked(request: CheckAttachmentsBeforeMoveDataServiceRequest): Observable<CheckAttachmentsBeforeMoveDataServiceResponse>
  {
    const apiurl = this.serviceUrl + '/GetAttachmentsChecked';
    return this.http.get<CheckAttachmentsBeforeMoveDataServiceResponse>(apiurl, { params: request as any });
  }
}
