import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { Component, Inject, OnInit, SkipSelf } from '@angular/core';
import { UntypedFormArray } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { ActivatedRoute } from '@angular/router';
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
import { LabelType, Options } from '@angular-slider/ngx-slider';
import { UtilizationRange } from '../../@intermediate/dynamic-pricing-details';
import { GetDynamicPricingUtilizationRangesRequest, GetDynamicPricingUtilizationRangesResponse, IDynamicPricingUtilizationRangesService, PostDynamicPricingUtilizationRangesRequest, PostDynamicPricingUtilizationRangesRequestForm, rangeValidator, valueChanged } from '../../@intermediate/dynamic-pricing-utilization-ranges';

@Component({
  selector: 'tei-tee-times-dialog-dynamic-pricing-utilization-ranges',
  templateUrl: './dynamic-pricing-utilization-ranges.component.html',
  styleUrls: ['./dynamic-pricing-utilization-ranges.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(DynamicPricingUtilizationRangesComponent)
  ]
})
export class DynamicPricingUtilizationRangesComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{
  model: GetDynamicPricingUtilizationRangesResponse;
  form: PostDynamicPricingUtilizationRangesRequestForm;
  courseTemplateUID: string;
  addRangeDisabled: boolean = false;
  maxRange: number = 10;

  options: Options = {
    floor: 0,
    ceil: 100,
    translate: (value: number, label: LabelType): string =>
    {
      switch (label)
      {
        case LabelType.Low:
          return value + '%';
        case LabelType.High:
          return value + '%';
        default:
          return '';
      }
    }
  };


  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('IDynamicPricingUtilizationRangesService') private DynamicPricingUtilizationRangesService: IDynamicPricingUtilizationRangesService,
    private dialog: MatDialogRef<DynamicPricingUtilizationRangesComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private confirmdialogService: ConfirmDialogService,
    private snackbarService: SnackBarService,
    private activatedRoute: ActivatedRoute)
  {
    super(_errorStateMatcher);
    this.courseTemplateUID = this.data.templateUID;
  }

  ngOnInit()
  {
    this.form = new PostDynamicPricingUtilizationRangesRequestForm(undefined);
    const request = new GetDynamicPricingUtilizationRangesRequest();

    this.form.valueChanges.subscribe(value =>
    {
      var rindex = 1;
      value.UtilizationRanges.forEach((x, i) =>
      {
        if (x.Deleted == false)
        {
          x.SortOrder = rindex++;
        }

      });
      valueChanged(false);
    });

    request.CourseTemplateUID = this.courseTemplateUID;
    this.load(request);
  }

  private load(request: GetDynamicPricingUtilizationRangesRequest)
  {
    if (!this.model)
    {
      this.model = new GetDynamicPricingUtilizationRangesResponse();
    }

    this.DynamicPricingUtilizationRangesService.Get(request).subscribe(response =>
    {
      this.model = response;
      if (this.model.UtilizationRanges.filter(f => f.UtilizationRangeUID != null).length >= 10)
      {
        this.addRangeDisabled = true;
      }
      this.form.setValue(this.model);
      valueChanged(true);
    });
  }

  save(form: PostDynamicPricingUtilizationRangesRequestForm)
  {

    const req = new PostDynamicPricingUtilizationRangesRequest();
    req.CourseTemplateUID = this.courseTemplateUID;
    req.UtilizationRanges = this.getUpdatedValues(form);
    if (req.UtilizationRanges.length > 0)
    {
      this.DynamicPricingUtilizationRangesService.Post(req).subscribe(response =>
      {
        this.dialog.close('saved');
        this.snackbarService.showSuccess('Template saved.');
      }, error =>
      {
        let errorMessage = error.error.Message;
        let a = errorMessage.indexOf("Start");
        let b = errorMessage.indexOf("\r");
        this.snackbarService.showError(errorMessage.substring(a, b));
      });
    }
    else
    {
      this.dialog.close('canceled');
    }
  }

  getUpdatedValues(form: any)
  {
    const req = new PostDynamicPricingUtilizationRangesRequest();
    let utilizationRanges: UtilizationRange[] = [];
    this.form.UtilizationRanges.controls.forEach(x =>
    {
      if (x.dirty || (x.value["UtilizationRangeUID"] != null && x.value["Deleted"] == true))
      {
        x.value["IsControlUpdated"] = true;
        utilizationRanges.push(x.value);
      }

      // for drag and drop only
      if (x.value["UtilizationRangeUID"] != null && x.value["IsControlUpdated"] == false)
      {
        this.model.UtilizationRanges.forEach(element =>
        {
          if (x.value["SortOrder"] != element.SortOrder && x.value["UtilizationRangeUID"] === element.UtilizationRangeUID)
          {
            x.value["IsControlUpdated"] = true;
            utilizationRanges.push(x.value);
          }
        });
      }


    });
    req.UtilizationRanges = utilizationRanges.filter(x => (x.Deleted == false && x.UtilizationRangeUID == null) || (x.UtilizationRangeUID != null));
    req.UtilizationRanges.forEach(f =>
    {
      if (f.UtilizationRangeUID != null)
      {
        this.model.UtilizationRanges.forEach(element =>
        {
          if ((f.UtilizationRangeUID === element.UtilizationRangeUID && f.IsControlUpdated == false)
            &&
            (f.UtilName != element.UtilName
              || f.UtilRange[0] != element.UtilRange[0]
              || f.UtilRange[1] != element.UtilRange[1]
              || f.ColorCode != element.ColorCode
            )
          )
          {
            f.IsControlUpdated = true;
          }

        });
      }
    });
    req.UtilizationRanges = req.UtilizationRanges.filter(f => f.IsControlUpdated == true);
    return req.UtilizationRanges;

  }

  addRangeGroup()
  {
    const utilizationRanges = this.form.get('UtilizationRanges') as UntypedFormArray;
    const len = utilizationRanges.controls.filter(f => f.value.Deleted == false).length;
    if (len == this.maxRange - 1)
    {
      this.addRangeDisabled = true;
    }
    const utilizationRange = new UtilizationRange();
    utilizationRange.UtilName = "";
    utilizationRange.UtilStartRange = 0;
    utilizationRange.UtilEndRange = 0;
    utilizationRange.ColorCode = "";
    utilizationRange.IsControlUpdated = true;
    utilizationRange.UtilRange = [utilizationRange.UtilStartRange, utilizationRange.UtilEndRange];
    const maxIndex = utilizationRanges.value.filter(o => o.Deleted == false).length;
    utilizationRange.SortOrder = maxIndex + 1;
    utilizationRanges.setValue([utilizationRange]);
  }

  removeRange(index: number)
  {
    const rangeArray = this.form.get('UtilizationRanges') as UntypedFormArray;
    const deleteMsg = 'Are you sure you want to delete this Range?';
    const dialogData: DialogData = {
      title: 'Delete',
      message: deleteMsg,
      textOkayButton: 'Delete',
      textCancelButton: 'Cancel'
    };
    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        rangeArray.controls.forEach((x, ti) =>
        {
          if (ti == index)
          {
            x.value.Deleted = true;
            x.value.IsControlUpdated = true;
            this.form.UtilizationRanges.controls[ti]["controls"].Deleted.value = true;
            rangeArray.controls[ti]["controls"].UtilRange.status = "VALID";
            rangeArray.controls[ti]["controls"].UtilName.status = "VALID";
            rangeArray.controls[ti]["controls"].ColorCode.status = "VALID";
            rangeArray.controls[ti].setErrors(null);


            // re- validate the rate range
            if (this.form.UtilizationRanges.status === "INVALID")
            {
              this.form.UtilizationRanges.controls.forEach(x =>
              {
                if (x.status === "INVALID")
                {
                  rangeValidator(x["controls"].UtilRange);
                }
              });
            }

          }
        });

        rangeArray.controls.forEach((y, i) =>
        {
          if (i >= index && (y.value.Deleted == false))
          {
            y.value.SortOrder = (y.value.SortOrder) - 1;
          }
        });

        const len = rangeArray.controls.filter(f => f.value.Deleted == false).length;
        if (len == this.maxRange)
        {
          this.addRangeDisabled = true;
        }
        else
        {
          this.addRangeDisabled = false;
        }
      }
    });
  }

  drop(event: CdkDragDrop<string[]>)
  {
    const utilizationRanges = this.form.get('UtilizationRanges') as UntypedFormArray;

    moveItemInArray(utilizationRanges.controls, event.previousIndex, event.currentIndex);
    var index = 1;
    utilizationRanges.controls.forEach((x, i) =>
    {
      if (x.value.Deleted == false)
      {
        //x.value.SortOrder = index++;
        utilizationRanges.controls[i].patchValue(["SortOrder", index++]);
      }
    });
  }

  onCancel()
  {
    this.dialog.close('canceled');
  }
}
